import $ from 'jquery';
import InViewPort from '../base/inViewPort/inViewPort';
import Swiper from 'swiper';

export default class CareerSlider {
  constructor(config) {
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);

    this.$swiperContainer = this.$el.find(
      this.config.id || '.career-swiper-container'
    );

    this.initSlider(this.config);
  }

  initSlider(config) {
    const self = this;
    const swiperInstance = new Swiper(
      this.config.id || '.career-swiper-container',
      {
        // Optional parameters
        initialSlide: 0,
        direction: 'horizontal',
        loop: false,
        slidesPerView: 'auto',
        slidesPerGroup: 3,
        slidesOffsetBefore: config.slidesOffsetBefore || 120,
        slidesOffsetAfter: 65,
        spaceBetween: config.spaceBetween || 32,
        centeredSlides: false,
        variableWidth: false,
        watchOverflow: true,
        observer: true,
        observeParents: true,

        // Navigation arrows
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        onInit: self.onInit.bind(this),
        breakpoints: {
          // when window width is <= 768
          768: {
            spaceBetween: 10,
            slidesOffsetBefore: 27,
            slidesOffsetAfter: 40,
            slidesPerGroup: 1
          }
        }
      }
    );
  }

  onInit(e) {
    if (!$('body').hasClass('is-experience-editing')) {
      const colInView = new InViewPort({
        el: '.swiper-wrapper',
        inViewinitedClass: 'in-viewport-inited',
        inViewClass: 'was-in-viewport',
        inViewCurrentClass: 'in-viewport',
        eventName: 'columnInView',
        offsetBottom: -200,
        offsetTop: -200
      });
    }

    if (e.slides.length < 4) {
      $(e.nextButton).css('display', 'none');
      $(e.prevButton).css('display', 'none');
    }
  }
}
