/**
 * Class: StickyButton
 */
import $ from "jquery";
import * as utils from "../base/utils/utils";
import { emitter } from '../base/emitter/emitter';
export default class StickyButton {
  
  constructor(config) {
  
    this.config = $.extend(
      {
        $el: ".sticky-button"
      },
      config
    );
    this.$el = $(this.config.$el);
    this.$headerEl = $(".headerSlider-top");
    this.$button = this.$el.find(".sticky-button__btn")
    this.$cookieBot = $("#coiOverlay");
    this.init();
  }

  init() {


    this.bindEvents();
  }

  addToHeader() {
    if (this.$headerEl) {
      this.$headerEl.append(this.$el);
    }
  }
  hasCookieBot() {
    return this.$cookieBot.length;
  }
  setUpButton() {
    
    if (this.hasCookieBot()) {
      this.$button.addClass("sticky-button__btn--cookie-bot")
    }
    emitter.on('resize', obj => {
      if (obj.width > 769) {
        this.$el.css('bottom', -this.$el.height());
      } else {
        this.$el.css('bottom', '');
      }
    });
  }

  bindEvents() {
    if (
      !utils.$body.hasClass("is-experience-editing")      
    ) {
      this.setUpButton();
      this.addToHeader();
    }    
  }
}
