import TweenMax from 'gsap/src/uncompressed/TweenMax';
import $ from 'jquery';
import moment from 'moment';
import { emitter } from '../base/emitter/emitter';
import { DateFormatingOptions, paragraphOverflow } from "../base/utils/utils";

export default class ArticlesPresentation {
  constructor(config) { 
    this.config = $.extend({
      el: '.news-module'
    }, config);
    this.$el = $(this.config.el);
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.$dates = this.$el.find('.news-list-item-date');
    this.$dates.each(function (idx, elem) {
        var $elem = $(elem);
      $elem.text(
        moment($elem.text().trim()).format(DateFormatingOptions.dateFormat)
      );
    });

    this.$newsImageList = this.$el.find('.news-image-list');
    this.$newsImageListItem = this.$el.find('.news-image-list-item');
    this.$newsContainer = this.$el.find('.news-container');
    this.$newsListItem = this.$el.find('.news-list-item');
    this.$activeMarker = this.$el.find('.active-marker');
    this.$newsListItemText = this.$el.find(".news-list-item-text");
    this.activeIndex = 0;

    // first element add highlighted
    this.$newsListItem.first().addClass("highlighted");
    if(this.$newsImageList.find('[name="' + 0 + '"]').length > 0)
    {
        this.$newsImageList.find('[name="' + 0 + '"]').addClass('highlighted');
    }
    

    // interval setup
    this.newsInterval = null; // this.setNewsInterval();
    this.setNewsImageListHeight();
    this.setNewsVideoHeight();
    this.bindEvents();
  }

  // bind events
  bindEvents() {
    var self = this;

    $(function() {
        self.calcMarkerPosition();
    });

    emitter.on('resize', function(obj) {
        self.setNewsImageListHeight();
        self.setNewsVideoHeight();
    });

    this.$newsListItem.on("mouseenter", (e) => {
        if (window.innerWidth <= 990) {
          return;
        }
        self.mouseEnterHandler(e);
    });
    this.$newsListItem.on("mouseleave", (e) => {
        if (window.innerWidth <= 990) {
          return;
        }
        self.mouseLeaveHandler(e);
    });
    paragraphOverflow(this.$newsListItemText);
  }
  
  // set news images container height, which is neseccary on ipad
  setNewsImageListHeight() {
    this.$newsImageList.css({
          height: '100%', 
          'min-height': '510px'
    });
  }

  // set video height,
  setNewsVideoHeight() {
    this.$newsImageListItem
      .filter('video, .youtube-video')
      .each((index, value) => {
        // let $vid = $(value);
      let width = this.$newsImageList.innerWidth();
        let height = (width * 9) / 16;

      if(height < this.$newsImageList.innerHeight()){
        height = this.$newsImageList.innerHeight();
          width = (height * 16) / 9;
      }
      $(value).css({
        height: height + 'px',
        width: width + 'px'
      });
    });
  }

  // mouse enter handler, enter animation and clear interval
  mouseEnterHandler(e) {
    this.activeIndex = $(e.currentTarget).index();
    clearInterval(this.newsInterval);
    this.setActive(this.activeIndex);
  }

  // mouseleave handler, leave animation and set new interval
  mouseLeaveHandler(e) {
    this.newsInterval = this.setNewsInterval();
  }

  // set and remove highlighted class on elements
  setActive(index) {      
    if (!this.$newsListItem.eq(index).hasClass('highlighted')) {
      this.$newsImageList.children().removeClass('highlighted');
      this.$newsListItem.removeClass('highlighted');

      this.$newsImageList.find('[name="' + index + '"]').addClass('highlighted');
      this.$newsListItem.eq(index).addClass('highlighted');

      this.calcMarkerPosition();
    }
  }

  // active marker position calculation
  calcMarkerPosition() {
      var activeElementTop = this.$newsContainer.find('.highlighted').position().top,
          activeElementheight = this.$newsContainer.find('.highlighted').innerHeight();

    TweenMax.to(this.$activeMarker, 0.2, {
      top: activeElementTop + activeElementheight / 2 - 20
    });
  }

  // set interval
  setNewsInterval() {
    return setInterval(() => {
      this.changeActiveNews();
    }, 500);
  }

  // change active news - highlighted
  changeActiveNews() {
    this.activeIndex = 0;
    this.setActive(this.activeIndex);
  }
}
