/**
 * Class: AnchorLinks
 * Description: Her goes description
 */

import $ from "jquery";

export default class AnchorLinks {
  constructor() {
    this.headerHeight = $(".headerSliderCorporate").length > 0? $(".headerSliderCorporate").height() : 0;
    this.animationSpeed = 300;
    this.anchorLink = "a[href*=\\#]";
    this.$bodyHtml = $("html,body");
    this.init();
  }
  init() {
    let self = this;

    $(window)
      .on("hashchange", function(e) {
        let UrlIdentifier = window.location.hash;

        if (UrlIdentifier) {
          let anchorElement = self.getAnchorElement(
            self.getHash(UrlIdentifier)
          );

          if (anchorElement.length > 0) {
            self.animateToPosition(anchorElement);
          }
        }
      })
      .trigger("hashchange");

    $(this.anchorLink).on("click", function(e) {
      e.preventDefault();

      let UrlIdentifier = self.getHash($(this).attr("href"));

      let anchorElement = self.getAnchorElement(UrlIdentifier);

      if (anchorElement.length > 0) {
        self.animateToPosition(anchorElement);
      }
      if (history.pushState) {
        history.pushState(null, null, `#${UrlIdentifier}`);
      } else {
        location.hash = `#${UrlIdentifier}`;
      }
      location.hash = `#${UrlIdentifier}`;
    });
  }
  getHash(url) {
    return url.substr(url.indexOf("#")).substr(1);
  }

  getAnchorElement(UrlIdentifier) {
    return $(`[name="${UrlIdentifier}"],[id="${UrlIdentifier}"]`);
  }
  animateToPosition($el) {
    this.$bodyHtml.animate(
      {
        scrollTop: $el.offset().top - this.headerHeight
      },
      this.animationSpeed
    );
  }
}
