const $ = window.$;

if (window.app !== undefined) {
    window.app.directive("flow", ["FlowService", function (FlowService) {
        return {
            restrict: 'A',
            scope: true,
            controller: ["$scope", "$attrs", "$location", "$timeout", "FlowService", function ($scope, $attrs, $location, $timeout, FlowService) {

                var steps = [];
                var state = {
                    index: 0,
                    get currentstep() {
                        return steps[this.index];
                    },
                    get nextstep() {
                        if (this.index >= steps.length - 1) {
                            return null;
                        }

                        return steps[this.index + 1];
                    },
                    get previousstep() {
                        if (this.index <= 0) {
                            return null;
                        }

                        return steps[this.index - 1];
                    }
                };

                var flow = this;
                flow.steps = steps;
                flow.state = state;
                flow.id = $attrs.flow;

                if ($attrs.skipValidation) {
                    flow.skipValidation = JSON.parse($attrs.skipValidation);
                }

                flow.RegisterStep = function (step) {
                    step.Hide();

                    steps.push(step);

                    if (flow.steps.length > 1 && $location.path() && $location.path() == step.path) {
                        NavigateTo(steps.length - 1);
                    } else if (flow.steps.length === 1) {
                        NavigateTo(0);
                    }
                };

                flow.PreviousStep = function () {
                    if (state.index >= 1) {
                        NavigateTo(state.index - 1)
                    }
                };

                flow.NextStep = function () {
                    if (state.index < flow.steps.length - 1) {

                        if (!state.currentstep.IsValid()) {
                            return;
                        }

                        NavigateTo(state.index + 1);
                    }
                };

                flow.NavigateToStep = function (index) {
                    if (index >= 0 && index < state.index) {
                        NavigateTo(index);
                    }
                };

                flow.IsValid = function () {
                    return flow.steps.every(function (element) {
                        return element.IsValid();
                    });
                };

                function NavigateTo(index) {
                    if (index < 0 || index > steps.length - 1) {
                        return;
                    }

                    // Validate all previous steps
                    var invalid = steps.slice(0, index).findIndex(function (element) {
                        return !element.IsValid();
                    });

                    // If previous step is invalid, navigate to that instead
                    if (invalid >= 0) {
                        index = invalid;
                    }

                    state.currentstep.Hide();
                    state.index = index;
                    state.currentstep.Show();

                    $timeout(function () {
                        $location.path(state.currentstep.path);
                    }, 0);

                    FlowService.save(flow.id, $scope.model);
                }

                $scope.$watch(function () {
                    return $location.path();
                }, function (newValue, oldValue) {
                    if (newValue !== oldValue) {
                        var index = steps.indexOfBy("path", newValue);

                        // Only navigate if hashbang url corresponds to a path and index is different than current index
                        if (index > -1 && state.index !== index) { 
                            NavigateTo(index);
                        }
                    }
                    });

                if ($attrs.flowModel) {
                    FlowService.init(JSON.parse($attrs.flowModel));
                }

                $scope.model = FlowService.model;
            }]
        }
    }]);

    window.app.directive("flowPrevious", ['$window', function ($window) {
        return {
            restrict: 'EA',
            require: ['^flow'],
            scope: true,
            template: '<button type="button" ng-show="button.show" ng-disabled="!button.enabled" ng-transclude></button>',
            replace: true,
            transclude: true,
            link: function (scope, element, attrs, ctrls) {
                var flow = ctrls[0];

                scope.button = {
                    get show() {
                        return flow.state.index > 0;
                    },
                    get enabled() {
                        return this.show;
                    }
                };

                $(element).on('click', function () {
                    $window.scrollTo(0, 0);
                    flow.PreviousStep();
                });
            }
        }
    }]);

    window.app.directive("flowNext", ['$window', function ($window) {
        return {
            restrict: 'EA',
            require: ['^flow'],
            scope: true,
            template: '<button type="{{button.type}}" ng-show="button.show" ng-disabled="!button.enabled" ng-transclude></button>',
            replace: true,
            transclude: true,
            link: function (scope, element, attrs, ctrls) {
                var flow = ctrls[0];

                scope.button = {
                    get type() {
                        var type = "button";

                        if (flow.state.nextstep == null) {
                            type = "submit";
                        }

                        return type;
                    },
                    get show() {
                        return !(this.type == "submit"  && flow.skipValidation);
                    },
                    get enabled() {
                        return flow.state.currentstep.IsValid();
                    }
                };

                $(element).on('click', function () {
                    $window.scrollTo(0, 0);
                    flow.NextStep();
                });
            }
        }
    }]);

    window.app.directive("ngModel",
        function () {
            return {
                restrict: 'A',
                priority: 0,
                require: 'ngModel',
                link: function postLink(scope, elem, attrs, ngModel) {
                    if (attrs.type === 'checkbox' || attrs.type === 'radio') {
                        ngModel.$setViewValue($(elem).is(':checked'));
                    } else if (attrs.value) {
                        ngModel.$setViewValue(attrs.value);
                    }
                }
            }
        });
}
