/* eslint-disable no-useless-escape */
/**
 * Class: Responsive
 * Description: Her goes description
 */
import { emitter } from '../emitter/emitter';
import * as utils from '../utils/utils';

export class Responsive {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    this.config = config;
    this.size = '';
    this.lastSize = '';
    this.init();
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;
    utils.$win
      .on({
        resize: utils.debounce(function() {
          const size = self.getSize();
          if (size !== self.lastSize) {
            emitter.emit('responsive/sizechange', size);
            self.lastSize = size;
          }
        }, 100)
      })
      .resize();
  }
  getSize() {
    return window
      .getComputedStyle(document.querySelector('body'), ':before')
      .getPropertyValue('content')
      .replace(/\"/g, '');
  }
}

/**
 * Exports Responsive as singleton. Consume with:
 * import { responsive } from '../responsive/responsive';
 * (remember the brackets around { responsive }).
 */
export let responsive = new Responsive();
