import $ from "jquery";

export default class SelectDropdown {
  constructor(config) {
    this.config = Object.assign({}, config);
    this.element = this.config.el;
    this.selectElement = this.element.querySelector("select");
    this.toggle = this.element.querySelector(".dropdown-toggle");
    this.dropdownMenu = this.element.querySelector(".dropdown-menu");
    this.dropdownMenuWrapper = this.dropdownMenu.querySelector(
      ".dropdown-items-wrapepr"
    );
    this.optionsMenu = this.element.querySelector(".dropdown-options");
    this.displayText = this.element.querySelector(".dropdown-display-text");
    this.displayTextAddition = this.element.querySelector(
      ".dropdown-display-addition"
    );
    this.displayIcon = this.element.querySelector(".dropdown-display-icon");

    this.placeholder =
      this.config.placeholder || this.selectElement.getAttribute("placeholder");
    this.focusedItem = null;
    this.jumpKeywords = "";
    this.jumpToTimer = null;
    this.disabled = false;
    this.keepOpen = false;
    this.isMultiSelect = false;
    this.showIcon = this.element.dataset["showicon"] === "true";
    this.selectedValues = [];
    this.detachMenu = this.element.dataset["detachmenu"] !== "false";
    this.direction = "";

    this.init();
  }

  init() {
    this.bindEvents();
    this.setupElements();
    this.renderDisplay();
  }

  setupElements() {
    Object.defineProperty(this.element, "value", {
      get: () => {
        return this.selectedValues;
      }
    });

    Object.defineProperty(this.element, "disabled", {
      get: () => {
        return this.disabled;
      },
      set: disabled => {
        this.disabled = disabled;

        if (disabled) {
          this.toggle.setAttribute("disabled", "disabled");
        } else {
          this.toggle.removeAttribute("disabled", "disabled");
        }
      }
    });

    Object.defineProperty(this.element, "direction", {
      set: direction => {
        this.direction = direction;
      }
    });

    if (this.detachMenu) {
      this.detach();
    }
  }

  detach() {
    this.element.removeChild(this.dropdownMenu);
    document.body.appendChild(this.dropdownMenu);

    this.dropdownMenu.style.width = this.element.offsetWidth + 'px';
  }

  handleSelectItem(e) {
    if (e instanceof MouseEvent) {
      this.selectItem(e.target);
    } else if (this.focusedItem) {
      this.selectItem(this.focusedItem.element);
    } else {
      return;
    }

    e.preventDefault();
    e.stopPropagation();
  }

  toggleSelectedItem(item) {
    const value = item.dataset["value"];

    const menuItems = this.dropdownMenu.querySelectorAll(`.dropdown-item`);

    let selectedIndex = -1;

    for (let i = 0; i < this.selectElement.options.length; i++) {
      if (this.selectElement.options[i].value === value) {
        this.selectElement.options[i].selected = true;
        menuItems[i].classList.add("selected");
        item.setAttribute("aria-selected", "true");
        selectedIndex = i;
      } else {
        this.selectElement.options[i].selected = false;
        menuItems[i].classList.remove("selected");
      }
    }

    return selectedIndex;
  }

  selectItem(e) {
    const target = e;

    if (target.hasAttribute("disabled")) {
      return;
    }

    const selectedIndex = this.toggleSelectedItem(target);

    this.focusedItem = { element: target, index: selectedIndex };

    this.buildSelectedOptions();
    this.renderDisplay();

    $(this.element).trigger("change", { selectedValues: this.selectedValues });

    if (!this.keepOpen) {
      this.closeMenu();
    }
  }

  buildSelectedOptions() {
    this.selectedValues = [this.selectElement.value];
  }

  renderDisplay() {
    const selectedOptions = [];

    for (let i = 0; i < this.selectElement.children.length; i++) {
      const option = this.selectElement.children[i];

      if (!option.selected) {
        continue;
      }

      selectedOptions.push(option);
    }

    this.displayTextAddition.textContent = "";

    if (selectedOptions.length === 0) {
      this.displayText.textContent = this.placeholder;
      this.element.setAttribute("title", this.placeholder);

      return;
    }

    this.displayText.textContent = selectedOptions[0].text;
    this.element.setAttribute("title", selectedOptions[0].text);

    if (selectedOptions.length > 1) {
      this.displayTextAddition.textContent = `+ ${selectedOptions.length - 1}`;
    }

    if (this.showIcon) {
      this.displayIcon.src = selectedOptions[0].dataset["image"];
    }
  }

  updateDropdownPosition() {
    const offset = this.element.getBoundingClientRect();

    this.dropdownMenu.style.width = `${offset.width}px`;
    this.dropdownMenu.style.left = `${offset.left}px`;
  }

  keydown(e) {
    switch (e.which) {
      case 38:
        //up arrow
        this.focusPreviousItem();
        e.preventDefault();
        e.stopPropagation();
        break;

      case 40:
        //down arrow
        this.focusNextItem();
        e.preventDefault();
        e.stopPropagation();
        break;

      case 13: //enter
        this.handleSelectItem(e);
        break;

      case 27:
      case 9:
        //escape and tab
        this.closeMenu();
        break;

      default:
        this.jumpToOptions(e.key);
        break;
    }
  }

  jumpToOptions(keys) {
    this.jumpKeywords += keys;

    let activeIndex = 0;

    if (this.focusedItem) {
      activeIndex = this.focusedItem.index + 1;
    }

    let foundItem = this.findMatchingOption(
      this.optionsMenu.children,
      activeIndex
    );

    if (!foundItem) {
      // wrap around to top
      foundItem = this.findMatchingOption(this.optionsMenu.children, 0);
    }

    if (foundItem) {
      const foundIndex = Array.prototype.indexOf.call(
        this.optionsMenu.children,
        foundItem
      );
      this.dropdownMenuWrapper.scrollTop = foundItem.offsetTop;
      this.focusItem(foundItem, foundIndex);
    } else {
      this.jumpKeywords = "";
    }

    if (this.jumpToTimer) {
      clearTimeout(this.jumpToTimer);
    }

    this.jumpToTimer = setTimeout(() => {
      this.jumpKeywords = "";
    }, 300);
  }

  findMatchingOption(list, startIndex) {
    for (let i = startIndex; i < list.length; i++) {
      const item = list[i];
      if (
        item.textContent
          .toLowerCase()
          .indexOf(this.jumpKeywords.toLowerCase()) === 0
      ) {
        return item;
      }
    }

    return null;
  }

  focusNextItem() {
    if (!this.focusedItem) {
      const currentItem = this.dropdownMenu.querySelector("li");

      if (currentItem) {
        this.focusItem(currentItem, 0);
      }

      return false;
    }

    const item = this.focusedItem.element;
    const nextItem = item.nextElementSibling;

    if (nextItem) {
      const nextIndex = Array.prototype.indexOf.call(
        item.parentElement.children,
        nextItem
      );
      return this.focusItem(nextItem, nextIndex);
    }

    return false;
  }

  focusPreviousItem() {
    if (!this.focusedItem) {
      return false;
    }

    const item = this.focusedItem.element;
    const previousItem = item.previousElementSibling;

    if (previousItem) {
      const previousIndex = Array.prototype.indexOf.call(
        item.parentElement.children,
        previousItem
      );
      return this.focusItem(previousItem, previousIndex);
    }

    return false;
  }

  focusItem(item, index) {
    if (this.focusedItem) {
      this.focusedItem.element.classList.remove("focused");
    }

    item.classList.add("focused");

    this.focusedItem = { element: item, index: index };

    if (
      this.dropdownMenuWrapper.scrollHeight >
      this.dropdownMenuWrapper.clientHeight
    ) {
      const activeElement = this.focusedItem.element;
      const itemBottom = activeElement.offsetTop + activeElement.offsetHeight;
      const scrollBottom =
        this.dropdownMenuWrapper.clientHeight +
        this.dropdownMenuWrapper.scrollTop;

      if (itemBottom > scrollBottom) {
        // scroll down to focused item
        this.dropdownMenuWrapper.scrollTop =
          itemBottom - this.dropdownMenuWrapper.offsetHeight;
      } else if (activeElement.offsetTop < this.dropdownMenuWrapper.scrollTop) {
        // scroll up to focused item
        this.dropdownMenuWrapper.scrollTop = activeElement.offsetTop;
      }
    }

    return this.focusedItem;
  }

  initOptions() {
    const fragment = document.createDocumentFragment();

    this.selectedValues.length = 0;

    const options = this.selectElement.children;

    for (let i = 0; i < options.length; i++) {
      const li = document.createElement("li");
      li.className = "dropdown-item";
      li.textContent = options[i].textContent;

      Object.assign(li.dataset, options[i].dataset);

      li.dataset["value"] = options[i].value;

      if (options[i].selected) {
        li.classList.add("selected");
        li.setAttribute("aria-selected", "true");
        this.selectedValues.push(options[i].value);
      }

      const image = li.dataset["image"];

      if (image) {
        const imageElement = this.generateImage(image);
        li.insertBefore(imageElement, li.childNodes[0]);
      }

      fragment.appendChild(li);
    }

    this.optionsMenu.innerHTML = "";
    this.optionsMenu.appendChild(fragment);

    this.element.disabled = options.length === 0 || this.selectElement.disabled;

    this.renderDisplay();
  }

  generateImage(imageUrl) {
    const image = document.createElement("img");
    image.className = "dropdown-item-icon";
    image.src = imageUrl;

    return image;
  }

  initOptionsData(data) {
    const optionsFragment = document.createDocumentFragment();

    data.options.forEach(o => {
      const option = new Option(o.text, o.value);

      if (o.data) {
        Object.assign(option.dataset, o.data);
      }

      optionsFragment.appendChild(option);
    });

    this.selectElement.options.length = 0;
    this.selectElement.appendChild(optionsFragment);

    this.initOptions();
  }

  closeMenu() {
    this.dropdownMenu.style.display = "none";
    this.element.classList.remove("open");
    this.toggle.removeAttribute("aria-expanded");
    this.focusedItem = null;

    const focusedItems = this.optionsMenu.querySelectorAll(
      ".dropdown-item.focused"
    );

    for (let i = 0; i < focusedItems.length; i++) {
      focusedItems[i].classList.remove("focused");
    }
  }

  showMenu(e) {
    const offset = e.target.getBoundingClientRect();
    const $dropdownMenu = $(this.dropdownMenu);
    let top = 0;
    let dropdownDirection = "bottom";

    if (!this.direction) {
      const spaceAbove = offset.top - $dropdownMenu.outerHeight();
      const spaceBelow = window.innerHeight - (offset.top + offset.height) - $dropdownMenu.outerHeight();
  
      if (spaceBelow < 0 && spaceAbove > spaceBelow) {
        dropdownDirection = "top";
      }
    } else {
      dropdownDirection = this.direction;
    }

    // check bottom screen
    if (this.detachMenu) {
      if (dropdownDirection === "top") {
        top = offset.top + window.pageYOffset - $dropdownMenu.outerHeight();
      } else  {
        top = $(document).scrollTop() + offset.bottom;
      }
    } else {
      if (dropdownDirection === "top") {
        top = -$dropdownMenu.outerHeight();
      } else {
        top = offset.height;
      }
    }

    if (dropdownDirection === "top") {
      $dropdownMenu.addClass("dropdown-menu-open--top");
      this.element.classList.add("dropdown-menu-open--top");
    } else {
      $dropdownMenu.removeClass("dropdown-menu-open--top");
      this.element.classList.remove("dropdown-menu-open--top");
    }

    $dropdownMenu.css({
      display: "block",
      top: top
    });

    // realign left when menu causes scrollbar to appear when visible
    let left = e.target.getBoundingClientRect().left;

    if (!this.detachMenu) {
      left = 0;
    }

    $dropdownMenu.css({
      width: e.target.getBoundingClientRect().width,
      left: left
    });
  }

  handleMobileSelection() {
    this.renderDisplay();
  }

  selectItemByData(e, data) {
    if (!data || !data.options) {
      return;
    }

    data.options.forEach(d => {
      const option = this.selectElement.querySelector(`[data-id="${d}"]`);

      if (!option) {
        return;
      }

      option.selected = true;

      const menuItem = this.dropdownMenu.querySelector(`[data-id="${d}"]`);

      if (menuItem) {
        menuItem.setAttribute("aria-selected", "true");
        menuItem.classList.add("selected");

        if (!this.isMultiSelect) {
          this.selectedValues.length = 0;
        }

        if (!this.selectedValues.indexOf(option.value) === -1) {
          this.selectedValues.push(option.value);
        }
      }
    });

    this.renderDisplay();
  }

  bindEvents() {
    // display opened dropdown menu on the correct position
    $(this.element).on("shown.bs.dropdown", this.showMenu.bind(this));

    $(this.element).on("hide.bs.dropdown", this.closeMenu.bind(this));

    $(this.element).on("select-data", this.selectItemByData.bind(this));

    $(this.element).on("init-options-data", (e, data) =>
      this.initOptionsData(data)
    );

    $(this.element).on(
      "update-dropdown-position",
      this.updateDropdownPosition.bind(this)
    );

    window.addEventListener("resize", this.closeMenu.bind(this));

    // handle option selection
    this.dropdownMenu.addEventListener("click", e => {
      if (e.target.nodeName.toLowerCase() === "li") {
        this.handleSelectItem(e);

        e.stopPropagation();
        e.preventDefault();
      }
    });

    this.dropdownMenu.addEventListener("keydown", this.keydown.bind(this));

    this.toggle.addEventListener("keydown", this.keydown.bind(this));

    // handle option selection on mobile
    this.selectElement.addEventListener(
      "change",
      this.handleMobileSelection.bind(this)
    );

    $(() => {
      this.initOptions();
      $(this.element).trigger("load");
    });
  }
}
