/**
 * Class: Stock
 * Description: Component used in Horizontal Stock Teaser
 */

import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions, getGlobalSettings } from '../base/utils/utils';

export default class Stock {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.stock',
        dongLast: '.stock-value',
        dongDelta: '.stock-delta',
        dongVolume: '.stock-volume-value',
        dongLastDate: '.stock-latest-value',
        omxLast: '.stock-index-value',
        omxDelta: '.stock-index-delta',

        apiAddress: '/api/stockfeed/dong',
        apiPollingPeriod: 5000,
        negativeClass: 'is-negative',
        dateFormat: DateFormatingOptions.dateFormat,
        numberLocale: getGlobalSettings('numberLocale')
      },
      config
    );
    this.$el = $(this.config.el);
    this.$dongLast = this.$el.find(this.config.dongLast);
    this.$dongDelta = this.$el.find(this.config.dongDelta);
    this.$dongVolume = this.$el.find(this.config.dongVolume);
    this.$dongLastDate = this.$el.find(this.config.dongLastDate);
    this.$omxLast = this.$el.find(this.config.omxLast);
    this.$omxDelta = this.$el.find(this.config.omxDelta);
    this.lastData = null;

    if (this.$el.length > 0) this.init();
  }

  init() {
    this.sendRequest();
  }

  sendRequest() {
    $.get({
      url: this.config.apiAddress,
      success: this.refilStockDataOnElement.bind(this),
      error: this.requestFail.bind(this)
    });
  }

  changeDataRegexReplacer(match, p1,p2, p3, p4, p5) {
    return p1 + this.convertNumberFormat(p2, 2) + p3 + p4 + this.convertNumberFormat(p5, 2);
  }

  refilStockDataOnElement(data) {
    var reponseCheckValue = JSON.stringify(data.response);
    const changeDataRegex = /^(\+|\-)([\d\.]+)([\s\/[a-zA-Z]+)(\+|\-)([\d\.]+)/;

    if (this.lastData != reponseCheckValue) {
      this.lastData = reponseCheckValue;
      this.$dongLast.text(this.convertNumberFormat(data.response.DongLast, 2));
      this.$dongDelta.text(data.response.DongChange.replace(changeDataRegex, this.changeDataRegexReplacer.bind(this)));
      this.proccessNegativeClass(this.$dongDelta, data.response.DongChange);
      this.$dongVolume.text(this.convertNumberFormat(data.response.DongVolume));
      var formattedDate = moment(data.response.Date).format(this.config.dateFormat);
      this.$dongLastDate.html(formattedDate);
      this.$omxLast.text(this.convertNumberFormat(data.response.OMXC20Last, 2));
      this.$omxDelta.text(data.response.OMXC20Change.replace(changeDataRegex, this.changeDataRegexReplacer.bind(this)));
      this.proccessNegativeClass(this.$omxDelta, data.response.OMXC20Change);
    }

    setTimeout(this.sendRequest.bind(this), 5000);
  }

  convertNumberFormat(str, decimalPlace = 0) {
    const number = +str.replace(",", "");
    return number.toLocaleString(this.config.numberLocale, {minimumFractionDigits: decimalPlace});  
  }

  proccessNegativeClass($elem, response) {
    if (response.indexOf('-') > -1) {
      $elem.addClass(this.config.negativeClass);
    } else {
      $elem.removeClass(this.config.negativeClass);
    }
  }

  requestFail() {
    setTimeout(this.sendRequest.bind(this), 5000);
  }
}
