if (window.angular !== undefined) {
    /**
 * Class: Product price location directive.
 */
    window.app.directive('productpricelocation', ['productService', function (productService) {

        var productpricelocation = {};
        productpricelocation.restrict = 'AE';
        productpricelocation.scope = true;

        productpricelocation.controller = ['$scope', function ($scope) {
            
        }];

        productpricelocation.link = function (scope, element, attrs) {
            productService.LoadConsumptionFromParam();
            scope.productForm = productService.model;
        };

        return productpricelocation;
    }]);
}