/**
 * Class: BreadCrumb
 */
import $ from 'jquery';
import { $win } from '../base/utils/utils';

export default class BreadCrumb {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.breadcrumbs-container-ov'
      },
      config
    );

    this.$el = $(this.config.el);

    if (this.$el.length > 0) {
      this.init();
    }
  }

  // calc total breadcrumbs elements with
  calcBreadcrumbsWitdh() {
    // init with (we don't need error :))
    var ElemWidthTotal = 0;
    $('.breadcrumbs--ov > li').each(function() {
      ElemWidthTotal = ElemWidthTotal + $(this).outerWidth();
    });
    return ElemWidthTotal;
  }

  breadcrumsWidth() {
    // initial state
    // need using ONLY STATIC (before first breadcrumbsBuild() run) total breadcrumbs element width
    var breadcrumbsItemsLength = $('.breadcrumbs--ov > li').length;

    //save all breadcrumbs in clean state
    var breadcrumbsTempItems = $('.breadcrumbs--ov').html();
    var containerWith = $('.breadcrumbs--ov').width();
    $('.breadcrumbs--ov').html('');
    $('.breadcrumbs--ov').html(breadcrumbsTempItems);

    // check if inner elements exist and prepare sub items
    if ($('.breadcrumbs-hidden').length < 1) {
      // wrap items in continer
      $('.breadcrumbs--ov')
        .children(':not(:nth-child(1))')
        .wrapAll('<div class="breadcrumbs-hidden"></div>');

      // create <li> with ...
      $('.breadcrumbs--ov').append(
        '<li class="breadcrumb-item--ov no-link"><div class="breadcrumbs-toggle">...</div></li>'
      );
      $('.breadcrumbs-hidden').appendTo('.breadcrumb-item--ov.no-link');
    }

    // add showing  elements to breadcrumbs
    for (var i = 0; i < breadcrumbsItemsLength - 1; i++) {
      $('.breadcrumbs-hidden li:last-child').insertAfter(
        '.breadcrumbs--ov > li:nth-child(2)'
      );

      //compare container width with total elements width
      if (!(this.calcBreadcrumbsWitdh() < containerWith)) {
        $('.breadcrumbs--ov > li:nth-child(3)').appendTo(
          $('.breadcrumbs-hidden')
        );
        break;
      }

      // if we don't have any items then we need to clean html
      if ($('.breadcrumbs-hidden li').length < 1) {
        $('.no-link').remove();
        break;
      }
    }
    if (breadcrumbsItemsLength == 1) {
      $('.no-link').remove();
    }
  }

  init() {
    this.$el.parent().css('transform', 'none');
    this.bindEvents();
  }

  bindEvents() {
    var self = this;

    self.breadcrumsWidth();
    $(document).ready(function() {
      //click on '...' with bubble
      var boo = false;
      $(document).click(function(e) {
        var container = $('.breadcrumbs-toggle');
        if (container.is(e.target) && boo == false) {
          $('.breadcrumbs-hidden').show();
          boo = true;
        } else {
          $('.breadcrumbs-hidden').hide();
          boo = false;
        }
      });

      $win.resize(function() {
        self.breadcrumsWidth();
      });
    });
  }
}
