/**
 * Class: AnnouncementArticle
 * Description: AnnouncementArticle script
 */
//import {m, utils} from '../../js/main';
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions } from '../base/utils/utils';

export default class AnnouncementArticle {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */

  constructor(config) {
    this.config = $.extend({
        el: '.announcement-article',
        date: '.article-date'
      },config);
    this.$el = $(this.config.el);
    this.$date = this.$el.find(this.config.date);

    if(this.$el.length){
      this.init();
    }
  }

  init(options) {
    this.$date.text(moment(this.$date.text().trim()).format(DateFormatingOptions.dateFormat));
  }
  bindEvents() {
  }
}
