/**
 * Class: ListFilters
 */
import $ from 'jquery';
import { $win, $body } from '../base/utils/utils';

export default class ListFilters {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.listFilters'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$mobileApply = this.$el.find('.listFilters-apply-filters');
    this.$mobileTrigger = this.$el.find('.listFilters-mobileTrigger');
    this.$mobileFilterWrapper = this.$el.find('.list-filters-wrapper');
    this.$mobileFilterHeaderText = this.$el.find('.list-filters-text');
    this.mobileFiltersOpenClass = 'listFilters-mobile-open';
    this.mobileFiltersOpen = false;
    this.init();

    if ($win.width() <= 768) {
      this.$mobileFilterWrapper.addClass('btn');
    }
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var self = this;
    this.$mobileFilterWrapper.on({
      click: function(/* e */) {
        if (!self.mobileFiltersOpen) {
          if (!$('.list-filters-text').is(':hidden')) {
            self.openMobileFilters();
          }
        }
      }
    });

    this.$mobileApply.on({
      click: function(e) {
        self.closeMobileFilters();
        e.stopImmediatePropagation();
      }
    });

    $('.listFilters-mobileTrigger-open').on({
      click: function(e) {
        self.closeMobileFilters();
        e.stopImmediatePropagation();
      }
    });

    $win.resize(function() {
      if ($win.width() <= 768) {
        if (!self.mobileFiltersOpen) {
          self.$mobileFilterWrapper.removeClass('btn').addClass('btn');
        } else {
          self.$mobileFilterWrapper.removeClass('btn');
        }
      } else {
        self.$mobileFilterWrapper.removeClass('btn');
      }
    });
  }

  resetParentTransform() {
    this.$el.parent().css('transform', '');
    this.$el.parent().css('transition-duration', '');
  }

  removeParentTransform() {
    this.$el.parent().css('transition-duration', '0ms');
    this.$el.parent().css('transform', 'none');
  }

  openMobileFilters() {
    var self = this;
    this.removeParentTransform();
    setTimeout(function() {
      self.$mobileFilterWrapper.removeClass('btn');
      self.$mobileFilterHeaderText.hide();
      self.$el.addClass(self.mobileFiltersOpenClass);
      self.mobileFiltersOpen = true;
      $body.css('overflow', 'hidden');
    }, 200);
  }

  closeMobileFilters() {
    this.resetParentTransform();
    this.$el.removeClass(this.mobileFiltersOpenClass);
    this.$mobileFilterHeaderText.show();
    this.$mobileFilterWrapper.addClass('btn');
    this.mobileFiltersOpen = false;
    $body.css('overflow', 'auto');
  }
}
