export function imageFallBack(options) {
  var options = $.extend(
    {
      image: ".image__element",
      hide:true,
      cssClasses:"teaser-img background-image-center-center",
      width:"",
      height:"",
      svgDimensions:{}  
    },
    options
  )
  var imgUrl = options.image.attr("src");
  
  var imgContainer = $(`<div/>`);

  imgContainer.attr({ 
      "data-svg-width":options.svgDimensions.width,
      "data-svg-height":options.svgDimensions.height
  });

  imgContainer.css({
    "background-image":`url(${imgUrl})`,
    "background-size":`${options.width}px ${options.height}px `
  });
    
  imgContainer.addClass(options.cssClasses).insertBefore(options.image);
  if (options.hide) {
    options.image.addClass("d-none");
  }
}
export function getImageSize($el) {
  var imgWidth = $el.data('svg-width');
  var imgHeight = $el.data('svg-height');

  var containerWidth = $el.parent().width();
  var containerHeight = $el.parent().height();

  var imgRatio = imgHeight / imgWidth;
  var containerRatio = containerHeight / containerWidth;

  var finalWidth;
  var finalHeight;

  if (containerRatio > imgRatio) {
    finalHeight = containerHeight;
    finalWidth = containerHeight / imgRatio;
  } else {
    finalWidth = containerWidth;
    finalHeight = containerWidth * imgRatio;
  }
  
  return {width:finalWidth, height:finalHeight}
}

export function htmlEscape(str) {
  return str
    .replace(/&/g, '&amp;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#39;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;');
}

export function getDevice() {
  const userAgent =
    navigator.userAgent ||
    navigator.vendor ||
    window.opera;

  if (
    userAgent.match(/iPad/i) ||
    userAgent.match(/iPhone/i) ||
    userAgent.match(/iPod/i)
  ) {
    return "ios";
  } else if (userAgent.match(/Android/i)) {
    return "android";
  } else if (userAgent.toLowerCase().indexOf("edge") > -1) {
    return "MS Edge (EdgeHtml)";
  } else if (userAgent.toLowerCase().indexOf("edg") > -1) {
    return "MS Edge Chromium";
  } else if (
    userAgent.toLowerCase().indexOf("opr") > -1 &&
    !!window.opr
  ) {
    return "opera";
  } else if (
    userAgent.toLowerCase().indexOf("chrome") > -1 &&
    !!window.chrome
  ) {
    return "chrome";
  } else if (userAgent.toLowerCase().indexOf("trident") > -1) {
    return "Internet Explorer";
  } else if (userAgent.toLowerCase().indexOf("firefox") > -1) {
    return "firefox";
  } else if (userAgent.toLowerCase().indexOf("safari") > -1) {
    return "safari";
  } else {
    return "unknown";
  }
}

// SCROLLTO
// easing functions http://goo.gl/5HLl8
Math.easeInOutQuad = function(t, b, c, d) {
  t /= d / 2;
  if (t < 1) {
    return (c / 2) * t * t + b;
  }
  t--;
  return (-c / 2) * (t * (t - 2) - 1) + b;
};

Math.easeInCubic = function(t, b, c, d) {
  var tc = (t /= d) * t * t;
  return b + c * tc;
};

Math.inOutQuintic = function(t, b, c, d) {
  var ts = (t /= d) * t,
    tc = ts * t;
  return b + c * (6 * tc * ts + -15 * ts * ts + 10 * tc);
};

// requestAnimationFrame for Smart Animating http://goo.gl/sx5sts
var requestAnimFrame = (function() {
  return (
    window.requestAnimationFrame ||
    window.webkitRequestAnimationFrame ||
    window.mozRequestAnimationFrame ||
    function(callback) {
      window.setTimeout(callback, 1000 / 60);
    }
  );
})();

export function scrollTo(to, duration, callback) {
  // because it's so fucking difficult to detect the scrolling element, just move them all
  function move(amount) {
    document.documentElement.scrollTop = amount;
    document.body.parentNode.scrollTop = amount;
    document.body.scrollTop = amount;
  }

  function position() {
    return (
      document.documentElement.scrollTop ||
      document.body.parentNode.scrollTop ||
      document.body.scrollTop
    );
  }
  var start = position(),
    change = to - start,
    currentTime = 0,
    increment = 20;
  duration = typeof duration === 'undefined' ? 300 : duration;
  if (Modernizr.touch) {
    duration = duration / 4;
  }
  var animateScroll = function() {
    // increment the time
    currentTime += increment;
    // find the value with the quadratic in-out easing function
    var val = Math.easeInOutQuad(currentTime, start, change, duration);
    // move the document.body
    move(val);
    // do the animation unless its over
    if (currentTime < duration) {
      requestAnimFrame(animateScroll);
    } else {
      if (callback && typeof callback === 'function') {
        // the animation is done so lets callback
        callback();
      }
    }
  };
  animateScroll();
}

