export function equalheight(container) {
  var currentTallest = 0;
  var currentRowStart = 0;
  var rowDivs = new Array();
  var $el;
  var topPosition = 0;
  var currentDiv = 0;

  $(container).each(function() {
    $el = $(this);
    $($el).height('auto');
    topPosition = $el.position().top;

    if (currentRowStart != topPosition) {
      for (currentDiv = 0; currentDiv < rowDivs.length; currentDiv++) {
        rowDivs[currentDiv].height(currentTallest);
      }
      rowDivs.length = 0; // empty the array
      currentRowStart = topPosition;
      currentTallest = $el.height();
      rowDivs.push($el);
    } else {
      rowDivs.push($el);
      currentTallest =
        currentTallest < $el.height() ? $el.height() : currentTallest;
    }
    for (currentDiv = 0; currentDiv < rowDivs.length; currentDiv++) {
      rowDivs[currentDiv].height(currentTallest);
    }
  });
}

// scroll disable and enable
var keys = {
  37: 1,
  38: 1,
  39: 1,
  40: 1
};

function preventDefault(e) {
  e = e || window.event;
  if (e.preventDefault) e.preventDefault();
  e.returnValue = false;
}

function preventDefaultForScrollKeys(e) {
  if (keys[e.keyCode]) {
    preventDefault(e);
    return false;
  }
}

export function disableScroll() {
  if (window.addEventListener)
    // older FF
    window.addEventListener('DOMMouseScroll', preventDefault, false);
  window.onwheel = preventDefault; // modern standard
  window.onmousewheel = document.onmousewheel = preventDefault; // older browsers, IE
  window.ontouchmove = preventDefault; // mobile
  document.onkeydown = preventDefaultForScrollKeys;
}

export function enableScroll() {
  if (window.removeEventListener)
    window.removeEventListener('DOMMouseScroll', preventDefault, false);
  window.onmousewheel = document.onmousewheel = null;
  window.onwheel = null;
  window.ontouchmove = null;
  document.onkeydown = null;
}

export function scrollTop() {
  return document.body.scrollTop || document.documentElement.scrollTop;
}

export function getHash() {
  if (window.location.hash) {
    var hash = window.location.hash.substring(1); //Puts hash in variable, and removes the # character
    return hash;
    // hash found
  } else {
    return null;
  }
}
export function setHash(hash) {
  if (history.pushState) {
    history.pushState(null, null, '#' + hash);
  } else {
    location.hash = '#' + hash;
  }
}

export function disableBodyScroll() {
  var scrollTop = lastScrollTop;
  $win.on('scroll.window', function() {
    $win.scrollTop(scrollTop);
  });
}
export function enableBodyScroll() {
  $win.off('scroll.window');
}

export function shuffleArray(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex;
  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;
    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }
  return array;
}

export function TemplateEngine(html, options) {
  var re = /<%([^%>]+)?%>/g,
    reExp = /(^( )?(if|for|else|switch|case|break|{|}))(.*)?/g,
    code = 'var r=[];\n',
    cursor = 0,
    match;
  var add = function(line, js) {
    js
      ? (code += line.match(reExp) ? line + '\n' : 'r.push(' + line + ');\n')
      : (code +=
          line != '' ? 'r.push("' + line.replace(/"/g, '\\"') + '");\n' : '');
    return add;
  };
  while ((match = re.exec(html))) {
    add(html.slice(cursor, match.index))(match[1], true);
    cursor = match.index + match[0].length;
  }
  add(html.substr(cursor, html.length - cursor));
  code += 'return r.join("");';
  return new Function(code.replace(/[\r\t\n]/g, '')).apply(options);
}

export function actualSize($el) {
  var previousCss = $el.attr('style');

  $el.css({
    position: 'absolute', // Optional if #myDiv is already absolute
    visibility: 'hidden',
    display: 'block',
    maxHeight: 'none',
    height: 'auto'
  });
  var returnObj = {
    height: $el.height(),
    width: $el.width()
  };
  $el.attr('style', previousCss ? previousCss : '');

  return returnObj;
}

export function addOneTimeEvent(conditionFunc, doFunc, interval, context) {
  var intervalFunc = window.setInterval(function() {
    if (conditionFunc(context)) {
      window.clearInterval(intervalFunc);
      doFunc(context);
    }
  }, interval);
}

export function fixExperienceEditorNavigation(navElementToFix) {
  var loadingIndicator = $('#ribbonPreLoadingIndicator');
  // var that = this;
  if (loadingIndicator.length !== 0) {
    addOneTimeEvent(
      function(/* that */) {
        //are we ready to fix stuff?
        if (loadingIndicator[0].style.display === 'none') {
          var height = $('#scCrossPiece').css('height');
          if (
            getExperienceEditor().getContext().isRibbonRendered &&
            height !== '50px'
          ) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      },
      function(/* that */) {
        //we are ready to fix, now fix
        if (
          window.Sitecore &&
          Sitecore.PageModes &&
          !Sitecore.PageModes.PageEditor
        ) {
          // Preview mode
          $('.headerClassic-spacer').css('padding-top', 0);
          navElementToFix.css('position', 'relative');
          $('.headerSlider-navigation').css('position', 'absolute');
          $('.headerSlider-navigation').css('top', 0);
          $('.headerSlider-navigation').css('width', '100%');
        }

        // var scCrossPieceHeight = $('#scCrossPiece').css('height');
        // navElementToFix.css("top", scCrossPieceHeight);
        // console.log("setting height: " + scCrossPieceHeight);
      },
      100,
      this
    );
  } else if (window.Sitecore && Sitecore.WebEdit) {
    // when content editor is open
    $('.headerClassic-spacer').css('padding-top', 0);
  }
}

var lastScrollTop = 0;
// notify modules that scroll or resize has happened (this way we only have one scroll/resize event listener)
$win
  .on({
    'DOMMouseScroll mousewheel scroll ': debounce(function() {
      var st = $win.scrollTop();
      var scrollDirection = '';

      // find out if we scroll/touchmove up or down
      if (st > lastScrollTop) {
        scrollDirection = 'down';
      } else if (st < lastScrollTop) {
        scrollDirection = 'up';
      } else {
        scrollDirection = '';
      }
      var delta = lastScrollTop - st;
      lastScrollTop = st;
      if (scrollDirection == 'down') {
        scrollDirection = 'down';
      } else if (scrollDirection == 'up') {
        // Do nothing
      } else {
        scrollDirection = false;
      }

      emitter.emit('scroll', {
        top: $win.scrollTop(),
        direction: delta
      });
    }, 5),
    resize: debounce(function() {
      this.viewportHeight = $html.outerHeight();
      emitter.emit('resize', {
        height: $html.outerHeight(),
        width: $html.width()
      });
    }, 150)
  })
  .trigger('scroll')
  .trigger('resize');

$win.keydown(function(e) {
  // e.preventDefault();
  var key = null;
  //enter
  if (e.which === 13) {
    key = 'enter';
  }
  //escape
  if (e.which === 27) {
    // emitter.emit('keyEvent', 'escape');
    key = 'escape';
  }
  //space
  if (e.which === 32) {
    // emitter.emit('keyEvent', 'space');
    key = 'space';
  }
  // uparrow
  if (e.which === 38) {
    // emitter.emit('keyEvent', 'up');
    key = 'up';
  }
  //downarrow
  if (e.which === 40) {
    // emitter.emit('keyEvent', 'down');
    key = 'down';
  }
  // arrowRight
  if (e.which === 39) {
    // emitter.emit('keyEvent', 'right');
    key = 'right';
  }
  // arrowLeft
  if (e.which === 37) {
    // emitter.emit('keyEvent', 'left');
    key = 'left';
  }
  //backspace
  if (e.which === 8) {
    // emitter.emit('keyEvent', 'backspace');
    key = 'backspace';
  }
  if (key) {
    emitter.emit('keyEvent', key);
  }
});

export function getGlobalSettings() {
  const defaultSettings = {
    numberLocale: 'en',
    dateFormat: 'DD.MM.YYYY HH:mm'
  };

  const body = document.querySelector('body');
  const settings = {
    ...defaultSettings,
    ...body.dataset
  };

  if (typeof arguments[0] === 'string' && settings.hasOwnProperty(arguments[0])) {
    return settings[arguments[0]];
  }

  return settings;
}

export function setupListenerOnViewportHeight() {
  if (window.hasSetupViewportHeight) return;

  function setDocHeight() {
    document.documentElement.style.setProperty('--vh', `${window.innerHeight/100}px`);
  };

  window.addEventListener('DOMContentLoaded', setDocHeight);
  window.addEventListener('Load', setDocHeight);
  window.addEventListener('resize', setDocHeight);
  window.addEventListener('orientationchange', setDocHeight);

  window.hasSetupViewportHeight = true;
}

export function createElementFromHTML(htmlString) {
  const div = document.createElement('div');
  div.innerHTML = htmlString.trim();
  return div.firstChild; 
}

export function noop() {}

export function isFunction(func) {
  return typeof func === 'function';
}

export function paragraphOverflow($el) {
  $el.each((i, el) => {
    var text;
    while (el.clientHeight < el.scrollHeight) {
      text = el.innerHTML.trim();
      if (text.split(" ").length <= 1) {
        break;
      }
      el.innerHTML = text.replace(/\W*\s(\S)*$/, "...");
    }
  });
}
