/**
 * Class: Accordion
 */
import $ from 'jquery';
import { $win } from '../base/utils/utils';

export default class Accordions {
  constructor(config) { // put in defaults here
    this.config = $.extend({
        el: '.accordion'
      }, config);
    this.$el = $(this.config.el);

    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    if (!this.$el.length)
      return false;
    Accordions.bindClickToAccordions(this.$el);
  }

  static bindClickToAccordions(elems, callback){
    elems.on('click', '.accordion-item-trigger', function (event) {
      event.preventDefault();
      var $item = $(this).parent('.accordion-items-item'),
        $content = $item.find('.accordion-item-expanded'),
        $accordion = $item.parent('.accordion-items');
      if ($item.hasClass('is-open')) {
        $content.slideUp({
          complete: function(){
            $item.removeClass('is-open');
            if (typeof callback === 'function') {
              callback();
            }
          }
        });
      } else {
        $accordion.find('.accordion-items-item').each(function(){
          var $self = $(this);
          if(!$accordion.hasClass('multi-open')){
            $self.find('.accordion-item-expanded').slideUp({
              complete: function(){
                $self.removeClass('is-open');
                if(typeof callback === 'function'){
                  callback();
                }
              }
            });
          }
        });

        $content.slideDown({
          complete: function(){
            $item.addClass('is-open');
            if (typeof callback === 'function') {
              callback();
            }
          }
        });
      }
      $win.trigger('resize');
    });
  }
}
