/**
 * Class: MegaNavigation
 */
import $ from 'jquery';
import Modernizr from 'modernizr';
import { emitter } from '../base/emitter/emitter';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';

export default class MegaNavigation {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.megaNavigation'
      },
      config
    );
    this.$el = $(this.config.el);

    //MobileMenu vars
    this.$mobileMenuTrigger = this.$el.find('.megaNavigation-trigger');

    // New Menu Trigger
    //this.$mobileMenuTrigger = this.$el.find('.megaNavigation-menu-trigger');
    this.$mobileMenu = this.$el.find('.megaNavigation-menu');
    this.mobileMenuVisibleClass = 'megaNavigation-menu-visible';
    this.mobileMenuVisible = false;

    // menuItems
    this.$menuItems = this.$el.find('.megaNavigation-menu-item');
    this.$menuItemLinks = this.$el.find('a.megaNavigation-menu-item-link');
    this.menuItemTriggerClass = 'megaNavigation-submenu-trigger';

    //submenu vars
    this.mobileMenuSubmenuClass = '.megaNavigation-submenu';
    this.$submenus = this.$el.find(this.mobileMenuSubmenuClass);
    this.mobileMenuSubmenuTriggerClass = '.megaNavigation-submenu-trigger';
    this.mobileMenuSubmenuVisibleClass = 'megaNavigation-submenu-visible';

    this.isSliderNavigation = $('.headerSlider').length > 0;

    // Make navigation items without subitems work in touch mode
    if (Modernizr.touch && !this.isSliderNavigation) {
      var submenuTriggers = this.$el.find('.megaNavigation-menu-item-link');
      this.$mobileSubmenuTrigger = submenuTriggers;
      submenuTriggers.addClass('megaNavigation-submenu-trigger');
    }

    // Make extra-touch-links visible if it's slider navigation, IE says no-touch to Modernizr, and the navigator has maxTouchPoints anyway.
    // This fixes the issue with touch-links not being visible
    if (
      this.isSliderNavigation &&
      !Modernizr.touch &&
      navigator.maxTouchPoints > 0
    ) {
      this.$el
        .find('.megaNavigation-submenu-item-section')
        .addClass('force-visibility-on-touch-device');
    }

    this.init();
    this.size = null;
  }
  init() {
    this.bindEvents();
    this.setFontSizeClass();
    emitter.on('responsive/sizechange', size => {
      this.size = size;
    });
  }
  bindEvents() {
    var self = this;    
    // Experimental Nav Menu
    $(
      '.nav-trigger .megaNavigation-menu-trigger, .nav-trigger .desktop-menu-trigger'
    ).on('click', function(e) {
      e.preventDefault();
      $('.nav-trigger-inner').toggleClass('active');
    });
    // Experimental Nav Menu
    $(
      '.nav-trigger .megaNavigation-menu-trigger, .nav-trigger .desktop-menu-trigger'
    ).keyup(function(e) {
      if (e.keyCode == 32) {
        e.preventDefault();
        $('.nav-trigger-inner').toggleClass('active');
      }
    });

    $(document).on('click', e => {
      if (
        $('.megaNavigation-menu').has(e.target).length === 0 &&
        $('.megaNavigation').has(e.target).length == 0
      ) {
        if ($('.header-tools').has(e.target).length !== 1) {
          // let $active = $('.megaNavigation').find('.active-sub');

          this.$menuItemLinks.parent().removeClass('active-sub');
          this.$menuItemLinks.removeClass('megaNavigation-submenu-visible');
          //$active.addClass('active-sub');
        }
      }
    });

    // toggle megamenu dropdown
    this.$menuItemLinks.on({
      click: e => {
        e.preventDefault();
        var $el = $(e.currentTarget);

        // if menu don't have submenu items
        if (
          $el
            .siblings('.megaNavigation-submenu')
            .find('.megaNavigation-submenu-item-submenu-item').length !== 0
        ) {
          if ($el.parent().hasClass('active-sub')) {
            $el.parent().removeClass('active-sub');
            $el.removeClass('megaNavigation-submenu-visible');
          } else {
            this.$menuItemLinks.parent().removeClass('active-sub');
            this.$menuItemLinks.removeClass('megaNavigation-submenu-visible');

            // dont add this class to large menu.
            if (this.size !== 'large') {
              $el.addClass('megaNavigation-submenu-visible');
            }

            $el.parent().addClass('active-sub');
          }
        } else {
          if ($el.attr('href') !== undefined) {
            window.location.href = $el.attr('href');
          }
        }
      }
    });

    // Use escape to close menu if open
    this.$mobileMenu.on({
      keyup: function(e) {
        if (e.keyCode == 27) {
          e.preventDefault();
          self.toggleMobileMenu();
        }
      }
    });

    this.$mobileMenuTrigger.on({
      click: function(e) {
        e.preventDefault();
        self.toggleMobileMenu();
      },
      keyup: function(e) {
        if (e.keyCode == 32) {
          e.preventDefault();
          //self.toggleMobileMenu();
          self.$mobileMenuTrigger.trigger('click');
        }
      }
    });
  }
  setFontSizeClass() {
    if (!this.isSliderNavigation) {
      // get length of characters in 1-level-menuitems and set class based on the number.
      this.menuItemsChars = this.$menuItems
        .not('.hidden-md-up')
        .find(' > a')
        .text();
      if (this.menuItemsChars.length > 50) {
        utils.$body.addClass('megaNavigation-small-font');
      }
    }
  }
  toggleMobileMenu() {
    if (this.mobileMenuVisible) {
      this.hideMobileMenu();
    } else {
      this.showMobileMenu();
      this.showSubMenuSelctedLink();
    }
  }
  showSubMenuSelctedLink() {
    try {
      if (responsive.getSize() === 'small') {
        var navMenu = this.$el.find('.megaNavigation-menu');
        var animateSubMenuItem;
        this.$menuItems.each(function(menuItemindx, menuItem) {
          var isItemActive = false;
          if (!isItemActive) {
            $(menuItem)
              .find('.megaNavigation-submenu-item-link')
              .each(function(submenuItemLinkIndx, submenuItemLink) {
                if (
                  $(submenuItemLink).hasClass(
                    'megaNavigation-submenu-item-link-active'
                  )
                ) {
                  $(menuItem)
                    .find('.megaNavigation-menu-item-link')
                    .addClass('megaNavigation-submenu-visible');
                  if (!$(menuItem).hasClass('active-sub')) {
                    $(menuItem).addClass('active-sub');
                  }
                  isItemActive = true;
                  animateSubMenuItem = submenuItemLink;
                  setTimeout(function() {
                    navMenu.animate({
                      scrollTop:
                        $(animateSubMenuItem).offset().top -
                        navMenu.offset().top +
                        navMenu.scrollTop() -
                        60,
                      duration: 200
                    });
                  }, 400);
                }
              });
            $(menuItem)
              .find('.megaNavigation-submenu-item-submenu-item-link')
              .each(function(
                submenuItemSubMenuItemLinkIndx,
                submenuItemSubMneuItemLink
              ) {
                if (
                  $(submenuItemSubMneuItemLink).hasClass(
                    'megaNavigation-item-active'
                  )
                ) {
                  if (!$(menuItem).hasClass('active-sub')) {
                    $(menuItem).addClass('active-sub');
                  }
                  isItemActive = true;
                  animateSubMenuItem = submenuItemSubMneuItemLink;
                  setTimeout(function() {
                    navMenu.animate({
                      scrollTop:
                        $(animateSubMenuItem).offset().top -
                        navMenu.offset().top +
                        navMenu.scrollTop() -
                        60,
                      duration: 200
                    });
                  }, 400);
                }
              });
          }
          if (!isItemActive) {
            if (
              $(menuItem)
                .find('.megaNavigation-menu-item-link')
                .hasClass('megaNavigation-submenu-visible')
            ) {
              $(menuItem)
                .find('.megaNavigation-menu-item-link')
                .removeClass('megaNavigation-submenu-visible');
            }
          }
        });
      }
    } catch (err) {
      // do nothing
    }
  }
  hideMobileMenu() {
    // utils.enableBodyScroll();
    this.mobileMenuVisible = false;
    this.$menuItemLinks.parent().removeClass('active-sub');
    this.$el.removeClass(this.mobileMenuVisibleClass);
    utils.$body.removeClass('mobilemenu-open');
  }
  showMobileMenu() {
    // utils.disableBodyScroll();
    this.$el.addClass(this.mobileMenuVisibleClass);
    this.mobileMenuVisible = true;
    utils.$body.addClass('mobilemenu-open');
  }
}

