        $.post(
          '/api/feature/customer/UpdateNemoLogin',
          {
            primaryPerson: {
              email: $('#mNemoFirstTimeLoginEmail')
                .val()
                .trim()
            },
            password: $('#mNemoFirstTimeLoginPassword')
              .val()
              .trim()
          },
          function(/* result */) {
            $('.close').click();
          }
        );
