  function toggleOverflow(addOverflow) {
    const overflowClass = "overflow-hidden";  
    if (addOverflow) {
      $('body').addClass(overflowClass);
    } else {
      $('body').removeClass(overflowClass);
    }
  }
  
  function changetxtType(x, type) {
    if (x.prop('type') == type) return x; //That was easy.
    try {
      return x.prop('type', type);
    } catch (e) {
      var html = $('<div>')
        .append(x.clone())
        .html();
      // eslint-disable-next-line no-useless-escape
      var regex = /type=(\")?([^\"\s]+)(\")?/; //matches type=text or type="text"
      //If no match, we add the type attribute to the end; otherwise, we replace
      var tmp = $(
        html.match(regex) == null
          ? html.replace('>', ' type="' + type + '">')
          : html.replace(regex, 'type="' + type + '"')
      );
      //Copy data from old element
      tmp.data('type', x.data('type'));
      var events = x.data('events');
      var cb = (function(events) {
        return function() {
          //Bind all prior events
          for (let i in events) {
            var y = events[i];
            for (let j in y) tmp.bind(i, y[j].handler);
          }
        };
      })(events);
      x.replaceWith(tmp);
      setTimeout(cb, 10); //Wait a bit to call function
      return tmp;
    }
  }

  const isCommmunicationHeader = $('.communication-toolbox.headerSliderCorporate');

  if (isCommmunicationHeader.length === 1) {
    $('html').addClass('communication-toolbox');
    $('main').css('padding-bottom', $('main > div.communication-toolbox').height());
  }
