/**
 * Class: EmergencyInformation
 * Description: Her goes description
 */
import $ from 'jquery';
import * as utils from '../base/utils/utils';

export default class EmergencyInformation {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    this.moduleClass = '.emergency-modal';
    this.config = $.extend(
      {
        el: this.moduleClass,
        version: '',
        apiAddress: '/api/emergencyinformation/CheckAsViewed?ver='
      },
      config
    );
    this.$el = $(this.config.el);

    if (this.$el.length > 0) this.init();
  }

  init() {
    utils.$body.addClass('modal-open');
    this.bindEvents();
  }

  bindEvents() {
    var self = this;

    this.$el.on('click', function(e) {
      if ($(e.target).hasClass('emergency-closer')) {
        utils.$body.removeClass('modal-open');
        $(self.moduleClass).remove();
      }
    });
  }
}
