if (window.app !== undefined) {
    window.app.factory('productService', ['$window', function ($window) {

        var productService = {};
        var cunsumptionLoaded = false;

        productService.model = {
            "products": [],
            "priceLocation": "east",
            "kwh": "",
            "gas": "",
            "prod": "",
            "fixed": ""
        }

        productService.saveData = function () {
            $window.localStorage && $window.localStorage.setItem('productdata', JSON.stringify(productService.model));
        }

        productService.loadData = function () {
            var productData = $window.localStorage && $window.localStorage.getItem('productdata');
            var tmpdata = JSON.parse(productData);
            if (tmpdata){
                productService.model = tmpdata;
            }
        }

        /**
        * Get GET parameters from URL - gas, kwh
        */
        productService.parseQueryString = function () {
            var str = window.location.search;
            var objURL = {};

            str.replace(
                new RegExp("([^?=&]+)(=([^&]*))?", "g"),
                function ($0, $1, $2, $3) {
                    objURL[$1] = $3;
                }
            );
            return objURL;
        };

        productService.LoadConsumptionFromParam = function () {
            if (cunsumptionLoaded) return;
            cunsumptionLoaded = true;
            // load from session data
            productService.loadData();

            // load from url.
            var getparams = productService.parseQueryString();
            if (getparams['kwh']) {
                productService.model.kwh = Math.round(parseInt(getparams['kwh']) / 12);
            }
            if (getparams['gas']) {
                productService.model.gas = Math.round(parseInt(getparams['gas']) / 12);
            }

            if (getparams['prod']) {
                productService.model.prod = getparams['prod'];
            }

            if (getparams['fixed']) {
                productService.model.fixed = getparams['fixed'];
            }

            if (getparams['reg']) {
                productService.model.priceLocation = getparams['reg'];
            }

            productService.saveData();
        }

        // product consumption helping methods
        productService.getUnitParam = function (uniturlkey) {
            productService.LoadConsumptionFromParam();
            if (uniturlkey in productService.model && productService.model[uniturlkey] != "") {
                var unitsize = productService.model[uniturlkey];
                return unitsize;
            }
            return false;
        }
        
        productService.getFallBackPriceWithoutConsumption = function (pricearray) {
            var defaultTotalPrice = null;
            var defaultTotalPriceShouldbeUsed = false;
            angular.forEach(pricearray, function (price) {
                if (price.DefaultPrice == true) {
                    defaultTotalPrice = price;
                }
                if (price.Unit && price.Unit.UnitUrlParam != '') {
                    if (productService.getUnitParam(price.Unit.UnitUrlParam) === false) {
                        defaultTotalPriceShouldbeUsed = true;
                    }
                }
            });
            return defaultTotalPriceShouldbeUsed == true ? defaultTotalPrice : null;
        }

        return productService;
    }]);
}