/**
 * Class: SearchResults
 * Description: SearchResults script
 */
import { $win } from '../base/utils/utils';
const $ = window.$;

export default class SearchResults {
  constructor(config) {
    $('.empty_result').hide();
    $('.has_result').hide();
    this.sets = {
      pagesize: 20,
      offset: 0,
      phrase: '',
      ankiroProfile: '',
      apiAddress: '',
      domOrder: '',
      createdRow: function(row) {
        $(row).addClass('list-items-item');
      },
      columnDefs: [
        {
          targets: [0],
          className: 'list-items-item-column  list-items-item-column-haslink',
          render: function(data, type, row) {
            var breadcumblist = row['BreadCrumbList'];
            var breadcumbStr = '';
            if (breadcumblist !== null) {
              breadcumblist.forEach(item => {
                breadcumbStr +=
                  "<li class='breadcrumb-item--sov'><a href='" +
                  item.URL +
                  "' class='breadcrumb-link-sov'>" +
                  item.DisplayName +
                  '</a></li>';
              });
            } else {
              breadcumbStr =
                "<li class='breadcrumb-item--sov'><a href='/' class='breadcrumb-link-sov'></a></li>";
            }
            return (
              "<div><ul class='breadcrumbs--sov'>" +
              breadcumbStr +
              '</ul>' +
              "<span class='list-items-item-value list-items-item-value-type-text word_break'><a class='search-results-link' href='" +
              row['PageUrl'] +
              "'>" +
              (row['PageTitle'] !== ''
                ? "<b class='word_break searchpage_title'>" +
                  row['PageTitle'] +
                  '</b><br />'
                : '') +
              "<p class='list-item-description'>" +
              row['PageContentHtml'] +
              '</p></a></span></div>'
            );
          }
        }
      ],
      language: {
        paginate: {
          next:
            '<span aria-hidden="true"></span><span class="sr-only">Next</span>',
          previous:
            '<span aria-hidden="true"></span><span class="sr-only">Previous</span>'
        },
        emptyTable: 'No data available in table'
      }
    };

    this.dataTableElem = null;

    this.config = $.extend(
      {
        el: '.search-result-module',
        table: '.search-result-list',
        resultsPerPage: '.search-results-per-page',
        categories: '.category-filter',
        searchPhrase: '#search',
        searchPhraseBtn: '.search-result-submit-btn',
        hdnSearchResultText: '#hdnSearchResultText',
        searchResultTextHeading4: '#searchResultText h4'
      },
      config
    );

    this.$el = $(this.config.el);
    this.$table = this.$el.find(this.config.table);
    this.$resultsPerPage = this.$el.find(this.config.resultsPerPage);
    this.$categories = this.$el.find(this.config.categories);
    this.$searchPhrase = this.$el.find(this.config.searchPhrase);
    this.$searchPhraseBtn = this.$el.find(this.config.searchPhraseBtn);
    this.$hdnSearchResultText = $(this.config.hdnSearchResultText);
    this.$searchResultTextHeading4 = $(this.config.searchResultTextHeading4);
    if (this.$el.length) {
      this.init();
    }
  }

  init(/* options */) {
    let SearchResultsOptions = window.SearchResultsOptions || {};
    $.extend(true, this.sets, SearchResultsOptions);
    this.bindEvents();
  }
  initDataTable() {
    if ($win.width() > 420 && $win.width() < 768) {
      $.fn.dataTable.ext.pager.numbers_length = 7;
    } else if ($win.width() > 330 && $win.width() < 420) {
      $.fn.dataTable.ext.pager.numbers_length = 6;
    } else if ($win.width() <= 330) {
      $.fn.dataTable.ext.pager.numbers_length = 5;
    }

    var self = this;
    this.dataTableElem = this.$table.DataTable({
      ordering: false,
      info: false,
      stateSave: true,
      responsive: true,
      bLengthChange: false,
      processing: true,
      serverSide: true,
      searching: false,
      lengthChange: true,
      ajax: {
        url: this.sets.apiAddress,
        data: function(d) {
          d.offset = self.sets.offset;
          d.phrase = self.$searchPhrase.val();
          d.ankiroProfile = self.sets.ankiroProfile;
          d.categories = encodeURIComponent(self.getCategories()); //encodeURIComponent('uri=' + self.$categories.filter(':checked').val());
        },
        dataSrc: this.sets.dataSrcFunction
      },
      pageLength: this.sets.pagesize,
      dom: this.sets.domOrder,
      createdRow: this.sets.createdRow,
      columnDefs: this.sets.columnDefs,
      language: this.sets.language,
      drawCallback: function(settings) {
        var categoriesCount = settings.json.CategoriesCount;
        categoriesCount.forEach(function(value, index) {
          $('#countCategory' + index).text(value);
          $('#countCategorym' + index).text(value);
        });
        let recordsTotal = 0;
        var pagination = $(this)
          .closest('.dataTables_wrapper')
          .find('.dataTables_paginate');
        pagination.toggle(this.api().page.info().pages > 1);
        var hv = self.$hdnSearchResultText.val();

        if (this.api().page.info().pages === 0) {
          recordsTotal = 0;
        } else {
          recordsTotal = this.api().page.info().recordsTotal;
        }
        var searchResultText = hv.replace('{total}', recordsTotal);
        let currentPage = this.api().page.info().page + 1;
        let toItems = this.api().page.info().length * currentPage;
        if (this.api().page.info().length * currentPage > recordsTotal) {
          toItems = recordsTotal;
        }

        if (this.api().page.info().pages === 0) {
          $('.empty_result').show();
          $('.has_result').hide();
        } else {
          $('.empty_result').hide();
          $('.has_result').show();
          $('#search_results_inbetween').text(
            this.api().page.info().length * this.api().page.info().page +
              1 +
              ' - ' +
              toItems
          );
          $('#total_results').text(recordsTotal);
        }

        self.$searchResultTextHeading4.text(searchResultText);
        $('html, body').animate(
          { scrollTop: $('#search').offset().top - 100 },
          'slow'
        );
      }
    });
  }
  bindEvents() {
    var self = this;
    $(document).ready(function() {
      ////if(self.$resultsPerPage.val().length !== 0)
      //{
      // self.sets.pagesize=parseInt(self.$resultsPerPage.val());
      // }
      self.sets.pagesize = parseInt(10);
      self.initDataTable();
      var re = new RegExp('RedrawTable=([^;]+)');
      var cv = re.exec(document.cookie);
      if (cv != null) {
        if (unescape(cv[1]) == 'true') {
          document.cookie =
            'RedrawTable=; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
          self.dataTableElem.draw();
        }
      }
    });

    this.$categories.on('change', function() {
      let selected_id = $(this).attr('id');
      if (selected_id.indexOf('_m') >= 0) {
        selected_id = selected_id.substring(0, selected_id.length - 2);
        $('#' + selected_id).prop('checked', $(this).prop('checked'));
      } else {
        $('#' + selected_id + '_m').prop('checked', $(this).prop('checked'));
      }

      $('html, body').animate(
        { scrollTop: $('#search').offset().top - 100 },
        'slow'
      );
      self.dataTableElem.draw();
      self.updateQueryInURL();
    });
    this.$resultsPerPage.on('change', function() {
      self.dataTableElem.page.len($(this).val());
      self.dataTableElem.draw();
      self.updateQueryInURL();
    });
    this.$searchPhraseBtn.on('click', function() {
      self.dataTableElem.draw();
      self.updateQueryInURL();
    });
    this.$searchPhrase.on('keyup', function(e) {
      e = e || window.event;

      if (e.keyCode == 13) {
        self.dataTableElem.draw();
        self.updateQueryInURL();
      } else {
        return false;
      }
    });
  }

  getCategories() {
    var cats = '';
    this.$categories.filter(':checked').each(function() {
      if (cats == '') cats += 'uri=' + $(this).val();
      else cats += '&uri=' + $(this).val();
    });
    return cats;
  }

  updateQueryInURL() {
    var self = this;
    var pageUrl = window.location.href.split('?')[0];
    var searchText = self.$el.find(self.$searchPhrase).val();
    var params = { search: searchText };
    var new_url = pageUrl + '?' + $.param(params);
    window.history.pushState({ path: new_url }, '', new_url);
  }
}
