if (window.angular !== undefined) {
    window.app.factory('priceService', ['productService', function (productService) {

        var priceService = {};

        priceService.model = [];

        priceService.addProductprice = function(productId, priceArray) {
            priceService.model[productId] = priceArray;
        }

        priceService.getProductprice = function (productId) {
            return priceService.model[productId];
        }

        priceService.fixedDecimals = function (number) {
            return parseFloat(number).toFixed(2);
        }

            // returns re
        priceService.getUnitPrice = function (price) {
            if (!price) return '-';
            if (price.Price != '') return priceService.fixedDecimals(price.Price);
            if (price.PriceEast != '' && price.PriceWest != '') {
                if (productService.model.priceLocation === 'east') return priceService.fixedDecimals(price.PriceEast);
                if (productService.model.priceLocation === 'west') return priceService.fixedDecimals(price.PriceWest);
            }
            return "-";
        }

        // returns kr.
        priceService.getPrice = function (price, isCalculate = true) {
            if (!price) return '-';
            if (priceService.getUnitPrice(price) != '-' && price.Unit.UnitUrlParam == '') return price.Unit.Inhundredth ? price.Price / 100 : price.Price;
            if (priceService.getUnitPrice(price) != '-' && price.Unit && price.Unit.UnitUrlParam != '') {
                var unitConsumption = productService.getUnitParam(price.Unit.UnitUrlParam);
                if (unitConsumption && isCalculate) {
                    var pricetmp = productService.getUnitParam(price.Unit.UnitUrlParam) * priceService.getUnitPrice(price);
                    return price.Unit.Inhundredth ? Math.round(pricetmp / 100) : Math.round(pricetmp);
                }
                else {
                    return price.Unit.Inhundredth ? priceService.getUnitPrice(price) / 100 : priceService.getUnitPrice(price);
                }
            }
        }

        priceService.showPrice = function (price, isCalculate) {
            var additionalPrice = parseFloat(0);
            if (price.AddedPrice) additionalPrice = parseFloat(price.AddedPrice);

            if (isCalculate && (!price.Unit || !price.Unit.UnitUrlParam || productService.getUnitParam(price.Unit.UnitUrlParam) !== false)) {
                return (parseFloat(priceService.getPrice(price, isCalculate)) + additionalPrice);
            }
            else {
                var value = 0;
                if (price.Unit && price.Unit.Inhundredth) {
                    // if price is not calculated show the price in oere
                    value = parseFloat(priceService.getPrice(price, isCalculate)) * 100;
                } else {
                    value = parseFloat(priceService.getPrice(price, isCalculate));
                }
                if (!isCalculate)
                    return value;
                return value.toFixed(2);
            }
        }

        return priceService;
    }]);
}