/**
 * Class: MultiNarrative
 * Description: Here goes description
 */
import $ from 'jquery';
import { responsive } from '../base/responsive/responsive';
import Swiper from 'swiper';

export default class MultiNarrative {
  constructor(config) {
    //defaults
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    this.$heightel = this.$el.find('.same-height');
    this.$options = this.$el.find('.narrative-option');
    this.$narrativeBodies = this.$el.find('.narrative-body');
    this.$narrativeCtas = this.$el.find('.narrative-cta');
    this.$narrativeImgs = this.$el.find('.narrative-img');
    this.$mediaTrack = this.$el.find('.narrative-media-track');
    this.$contentTrack = this.$el.find('.narrative-content-track');
    this.$swiperContainer = this.$el.find('.swiper-container');

    this.$narrativeContent = this.$el.find('[data-narrative-content]');

    this.init();
  }
  init() {
    this.bindEvents();
    this.setupSwiper();
    this.setTracks();
  }
  setTracks() {
    this.$mediaTrack.css('width', this.$options.length * 100 + '%');
    this.$contentTrack.css('width', this.$options.length * 100 + '%');
  }
  switchContent(swiper) {
    var position = 100 / swiper.slides.length;
    this.$mediaTrack.css(
      'transform',
      'translate3d(-' + swiper.activeIndex * position + '%,0,0)'
    );
    this.$contentTrack.css(
      'transform',
      'translate3d(-' + swiper.activeIndex * position + '%,0,0)'
    );
  }
  setupSwiper() {
    var self = this;
    this.swiper = new Swiper(this.$swiperContainer, {
      direction: 'horizontal',
      initialSlide: 1,
      slidesPerView: 3,
      breakpoints: {
        993: {
          slidesPerView: 1
        }
      },
      nextButton: '.swiper-button-next',
      prevButton: '.swiper-button-prev',
      onInit: function(swiper) {
        self.switchContent(swiper);
      },
      onSlideChangeStart: function(swiper) {
        if (swiper.currentBreakpoint !== 'max') {
          self.switchContent(swiper);
        }
      },
      onClick: function(swiper, e) {
        if (responsive.getSize() !== 'small') {
          //for some reason, swiper need both onClick and onTap to work properly (one is not enough)
          if (
            !$(e.srcElement).hasClass('swiper-button-next') &&
            !$(e.srcElement).hasClass('swiper-button-prev') &&
            !$(e.srcElement)
              .parent()
              .hasClass('active')
          ) {
            swiper.activeIndex = swiper.clickedIndex;
            swiper.update();
            self.switchContent(swiper);
          }
        }
      },
      onTap: function(swiper, e) {
        if (responsive.getSize() !== 'small') {
          //for some reason, swiper need both onClick and onTap to work properly (one is not enough)
          if (
            !$(e.srcElement).hasClass('swiper-button-next') &&
            !$(e.srcElement).hasClass('swiper-button-prev') &&
            !$(e.srcElement)
              .parent()
              .hasClass('active')
          ) {
            swiper.activeIndex = swiper.clickedIndex;
            swiper.update();
            self.switchContent(swiper);
          }
        }
      }
    });

    // Disable possibility to drag the entire swiper-container
    this.swiper.disableTouchControl();
  }

  bindEvents() {
    var self = this;
    if (
      /MSIE (\d+\.\d+);/.test(navigator.userAgent) ||
      navigator.userAgent.indexOf('Trident/') > -1
    ) {
      // Fixes link jump on IE
      $(this.$narrativeBodies).on('mouseenter', function() {
        self.$narrativeBodies.each(function() {
          var parentHeight = $(this)
            .parent()
            .height();
          $(this).css('height', parentHeight);
        });
      });
      $(this.$narrativeBodies).on('mouseleave', function() {
        self.$narrativeBodies.each(function() {
          $(this).css('height', '100%');
        });
      });
    }
  }
}
