import $ from 'jquery';
import InViewPort from '../base/inViewPort/inViewPort';
import { responsive } from '../base/responsive/responsive';
import { emitter } from '../base/emitter/emitter';
import Swiper from 'swiper';

export default class ImageSlider {
  constructor(config) {
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    this.youtubeIds = [];
    this.isYouTubeReady = false;
    this.isYouTubeInitialized = false;
    this.isMobile = responsive.getSize() === 'small';

    this.$swiperContainer = this.$el.find(
      this.config.id || '.swiper-container'
    );

    window.addEventListener('load', event => {
      $('.imageSlider .swiper-button-prev').animate({ opacity: 100 }, 2000);
      $('.imageSlider .swiper-button-next').animate({ opacity: 100 }, 2000);
    });

    emitter.on('video/youtube-ready', () => {
      if (!this.isYouTubeInitialized) {
        this.$el.find('.swiper-youtube-control').on('click', this.toggleYoutube.bind(this));
        this.isYouTubeInitialized = true;
      }

      this.isYouTubeReady = true;
    })

    this.initSlider();
  }

  initSlider() {
    // If slider is on communication toolbox in one column container then add offset to previous button
    var communicationToolboxLayout = $(
      '.communication-toolbox.headerSliderCorporate'
    );
    this.$swiperContainer.each(function() {
      var adjustLeftArrow =
        communicationToolboxLayout.length > 0 &&
        $(this).closest('.col-12').length > 0 &&
        $(this).closest('.col-12')[0].className == 'col-12';
      if (adjustLeftArrow) {
        $(this)
          .find('.swiper-button-prev')
          .addClass('offset-lg-2');
      }
    });

    var self = this;

    var swiperInstance = new Swiper(this.$swiperContainer[0], {
      // Optional parameters
      initialSlide: 1,
      direction: 'horizontal',
      loop: this.$swiperContainer[0].dataset.loopCarousel === 'true',
      slidesPerView: 'auto',
      slidesOffsetBefore: 32,
      spaceBetween: 32,
      centeredSlides: true,
      variableWidth: true,
      observer: true,
      observeParents: true,

      // Navigation arrows
      nextButton: '.swiper-button-next',
      prevButton: '.swiper-button-prev',
      onSlideChangeStart: self.updateText.bind(self),
      onInit: self.onInit.bind(self),
      breakpoints: {
        // when window width is <= 768
        768: {
          initialSlide: 0,
          slidesOffsetBefore: 0,
          spaceBetween: 10
        }
      },
    });
  }

  onInit(e) {
    this.updateText(e);

    if (!$('body').hasClass('is-experience-editing')) {
      let colInView = new InViewPort({
        el: e.wrapper[0],
        inViewinitedClass: 'in-viewport-inited',
        inViewClass: 'was-in-viewport',
        inViewCurrentClass: 'in-viewport',
        eventName: 'columnInView',
        offsetBottom: -200,
        offsetTop: -200
      });
    }

    // remove duplicate youtube slide when swiper is loopable
    this.$el.find('.swiper-slide-duplicate > iframe').remove();

    const control = '&controls=' + (this.isMobile ? '0' : '1');

    this.$el.find('.teaser-video-iframe').each((index, iframe) => {
      iframe.id += '-' + (index + 1).toString();
      this.youtubeIds.push(iframe.id);

      setTimeout(() => iframe.setAttribute('src', iframe.src + control), 300);
    });
  }
  
  toggleYoutube(e) {
    const youtubeVideo = YT.get(e.target.nextElementSibling.id);

    if (youtubeVideo) {
      if (youtubeVideo.getPlayerState() !== 1) {
        youtubeVideo.playVideo();
      } else {
        youtubeVideo.pauseVideo();
      }
    }
  }

  updateText(e) {
    var id = '.swiper-description';
    if (this.config && this.config.id) {
      id = this.config.id + '-description';
    }

    var activeIndex = e.realIndex;

    $(e.slides).each((index, slide) => {
      var video = $(slide).find('div.video-js');

      if (video.length > 0 && video[0].player) {
        video[0].player.pause();
      }

      var youtube = $(slide).find('.teaser-video-iframe');

      if (youtube.length > 0 && this.isYouTubeReady) {
        const player = YT.get(youtube[0].id);
        
        if (player && typeof player.pauseVideo === 'function') {
          player.pauseVideo();
        }
      }
    });

    var $sliderDescriptions = $(e.container)
      .closest('.imageSlider')
      .find(id + ' .description');
    $sliderDescriptions.each(function(index, description) {
      $(description).css('display', index === activeIndex ? 'block' : 'none');
    });
  }
}
