import $ from 'jquery';

export default class Richtext {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.richtext'
      },
      config
    );
    this.$el = $(this.config.el);
    this.init();
  }

  init() {
    this.bindEvents();
    this.removeUnderlineFromAs();
  }

  bindEvents() {}

  removeUnderlineFromAs() {
    this.$el.find('a').each(function() {
      $(this)
        .has('span')
        .css('text-decoration', 'none');
    });
  }
}
