if (window.angular !== undefined) {
    Array.prototype.indexOfBy = function(name, value) {
        for (var i = 0; i < this.length; i++) {
            if (angular.equals(this[i][name], value)) {
                return i;
            }
        }

        return -1;
    }

    Array.prototype.getElementBy = function(name, value) {
        var index = this.indexOfBy(name, value);

        if (index > -1) {
            return this[index];
        }

        return null;
    }
}