import $ from 'jquery';
import InViewPort from '../base/inViewPort/inViewPort';
import { debounce, scrollTo } from '../base/utils/utils';

const location = window.location;
const history = window.history;

export default class FilterFunction {
  static SetupStaticMethod() {
    if (!window.FilterFunctionData) {
      window.FilterFunctionData = {
        counter: 1
      }
    }
  }

  static GenerateObjectIdentifier($el) {
    const counter = window.FilterFunctionData.counter;
    window.FilterFunctionData.counter++;
    if ($el[0].id) {
      $el.data('FilterFunctionIndex', $el[0].id);
      return $el[0].id;
    }
    $el.data('FilterFunctionIndex', counter);
    return counter;
  }

  static SerializeHash(obj) {
    let item = [];

    for (let key in obj) {
      if (typeof obj[key] === 'undefined') {
        obj[key] = '';
      }
      if(obj[key] === '') {
        item.push(`ff${key}`);
      } else {
        item.push(`ff${key}=${obj[key]}`);
      }
    }

    const hash = `#${item.join('&')}`;
    if (hash === '#') {
      history.replaceState(undefined, undefined, ' ');
    } else {
      history.replaceState(undefined, undefined, hash);
    }
  }

  static ParseHash() {
    const hash = location.hash;
    if (hash && hash !== '' && hash !== '#') {
      if (hash.substr(0, 3) === '#ff') {
        const data = hash.substr(1).split('&');
        const obj = {};
        data.forEach(item => {
          const split = item.split('=');
          let index = split[0].substr(2);
          if (!isNaN(index)) {
            index = parseInt(index);
          }
          if (index) {
            if (typeof split[1] === 'undefined') {
              obj[index] = '';
            } else {
              obj[index] = split[1];
            }
          }
        });
        return obj;
      }
    }
    return {};
  }

  static SetHash(id, tag) {
    const obj = FilterFunction.ParseHash();
    obj[id] = tag;
    FilterFunction.SerializeHash(obj);
  }

  static GetHash(id) { 
    const obj = FilterFunction.ParseHash();
    return obj[id] || "";
  }

  static scrollToFirstHash($el) {
    if (typeof window.FilterFunctionHasChangedHash === 'undefined') {
      window.FilterFunctionHasChangedHash = false;
    }

    if (window.FilterFunctionHasChangedHash) {
      return;
    }

    const obj = FilterFunction.ParseHash();
    const elIndex = $el.data('FilterFunctionIndex');

    for (let key in obj) {
      if (elIndex == key) {
        const headerOffset = 60;
        const position = $el.offset().top - headerOffset;
        if ('scrollRestoration' in history) {
          history.scrollRestoration = 'manual';
        }
        $(() => {
          scrollTo(position, 200);
        });
        window.FilterFunctionHasChangedHash = true;
        break;
      }
    }
  }

  constructor($el, options) {
    FilterFunction.SetupStaticMethod();

    this.$el = $el;
    this.$window = $(window);
    this.options = $.extend(
      {
        tagsClass: 'ff-tags',
        tagClass: 'ff-tag',
        tagInactiveClass: 'btn--inactive',
        tagListSelector: '.ff-tag-list',
        dropDownBtnClass: '.ff-dropdown-button',
        itemsSelector: '.ff-items-data',
        itemSelector: '.ff-item',
        itemHideClass: 'ff-item-hide',
        itemShowClass: 'ff-item-show',
        itemRightColumnClass: 'ff-item--right',
        itemIframe: '.ff-item__iframe',
        itemVideo: '.ff-item__video',
        itemLeftColumnClass: 'ff-item--left',
        loadMoreSelector: '.ff__btn__loadmore',
        mobileSelector: '.ff-items-mobile',
        desktopSelector: '.ff-items-desktop',
        desktopLeftSelector: '.ff-items--left',
        desktopRightSelector: '.ff-items--right',
        maxItemOnLoad: 5,
        maxItemLoadMore: 5
      },
      options
    );

    this.options.tagsSelector = this.options.tagsSelector || `.${this.options.tagsClass.split(' ').join('.')}`;
    this.options.tagSelector = this.options.tagSelector || `.${this.options.tagClass.split(' ').join('.')}`;

    this.$tags = this.$el.find(this.options.tagsSelector);
    this.$parent = this.$el.find(this.options.itemsSelector);
    this.$items = this.$parent.find(this.options.itemSelector);
    this.$currentItems = this.$items;
    this.$dropDownBtn = this.$el.find(this.options.dropDownBtnClass);
    this.$loadMoreBtn = this.$el.find(this.options.loadMoreSelector);
    this.$tagBtns = this.$tags.find(this.options.tagSelector);
    this.$tagList = this.$el.find(this.options.tagListSelector);
    this.$mobile = this.$el.find(this.options.mobileSelector);
    this.$desktop = this.$el.find(this.options.desktopSelector);
    this.$desktopLeft = this.$desktop.find(this.options.desktopLeftSelector);
    this.$desktopRight = this.$desktop.find(this.options.desktopRightSelector);
    this.tags = [];
    this.desktopLeftItems = [];
    this.desktopRightItems = [];
    this.savedEl = {};
    this.isMobileView = true;
    this.isDesktopView = false;
    this.isExperienceEditing = $('body').hasClass('is-experience-editing');
    this.objectIndex = FilterFunction.GenerateObjectIdentifier(this.$el);
    FilterFunction.scrollToFirstHash(this.$el);

    if (this.$el) {
      this.init();
    }
  }

  init() {
    this.setupView();
    this.savedEl = this.mapTaggedElement(this.$items);
    this.tags = Object.keys(this.savedEl);
    const tag = FilterFunction.GetHash(this.objectIndex);
    this.$currentItems = typeof this.savedEl[tag] !== 'undefined' ? this.savedEl[tag] : this.$items;
    this.setupItems(this.$currentItems);
    this.setupResizeListener();
    this.setupLoadmoreBtn();
    this.setupTagBtnListener();

    this.dropDown();
    if (this.isIE11()) {
      $("html").addClass("isIe11");
    }

    if (tag !== '') {
      this.$tagBtns.addClass(this.options.tagInactiveClass);
      this.$tagBtns.filter(`[data-tag="${tag}"]`).removeClass(this.options.tagInactiveClass);
    }
  }

  isIE11() {
    return !!window.MSInputMethodContext && !!document.documentMode;
  }

  setupView() {
    this.isMobileView = this.$desktop.css('display') === 'none';
    this.isDesktopView = !this.isMobileView;
  }

  dropDown() {
    const $tagList = this.$tagList;
    this.$dropDownBtn.on('click', (e) => {
      var $el = $(e.currentTarget);
      $el.toggleClass('ff-dropdown-button--active');
      $tagList.slideToggle();
    });

    $tagList.on("click", '.ff-tag-list__button', (e) => {
      var $el = $(e.target);
      if ($el.hasClass('ff-tag-list__button--selected')) {
        return;
      }

      $tagList.find('.ff-tag-list__button').removeClass("ff-tag-list__button--selected");
      $el.addClass("ff-tag-list__button--selected");
      var tag = $el.data('tag');
      this.$tags.find(`[data-tag="${tag}"]`).trigger('click');
      $tagList.slideToggle();
      this.$dropDownBtn.toggleClass('ff-dropdown-button--active');
    });
  }

  mapTaggedElement($items) {
    var savedEl = [];
    $items.each((i, el) => {
      const $el = $(el);
      const tags = ($el.data('tags') || '').split(',').filter(e => e !== '');
      tags.forEach(item => {
        if (!savedEl[item]) {
          savedEl[item] = $el;
        } else {
          savedEl[item] = savedEl[item].add($el);
        }
      });
    });
    return savedEl;
  }

  updateSide($items) {
    const itemInCol = this.options.maxItemLoadMore;
    const result = {
      left: $([]),
      right: $([])
    };
    $items.each((i, el) => {
      const $el = $(el);
      if (i % itemInCol < 2) {
        result.left = result.left.add($el.clone());
      } else {
        result.right = result.right.add($el.clone());
      }
    });

    return result;
  }

  getYoutubeVideoUrl($iframe) {
    let url = $iframe.data("lazysrc") || $iframe.data("src");
    let params = url.indexOf("?") > 0 ? "&" : "?";

    if ($iframe.data("muted") === "True") {
      params = params + "mute=1&";
    }

    if ($iframe.data("autoplay") === "True") {
      params = params + "autoplay=1&";
    }

    if ($iframe.data("loop") === "True") {
      params = params + "loop=1&";
    }

    if ($iframe.data("suggested") === "True") {
      params = params + "rel=1&";
    }

    url = url + params;

    return url;
  }

  setupItems($items) {
    $items.parent().find(this.options.itemSelector).addClass(this.options.itemHideClass);

    $items
      .slice(0, this.options.maxItemOnLoad).removeClass(this.options.itemHideClass);

    const $iframes = $items.parent().find(this.options.itemIframe);

    $iframes.each((i, iframe) => {
      const $iframe = $(iframe);
      $iframe.attr("src", this.getYoutubeVideoUrl($iframe));
    });

    const $videos = $items.parent().find(this.options.itemVideo);
    $videos.each((i, video) => {
      const $video = $(video);
      const $videoSource = $video.find("source");

      if ($videoSource.data("lazysrc")) {
        $videoSource.attr("src", $videoSource.data("lazysrc"));
      }
    });

    // Reset desktop view
    this.$desktopLeft.html('');
    this.$desktopRight.html('');

    this.renderItems($items);
  }

  paragraphOverflow($el) {
    $el.find('.ff-item__paragraph').each((i, el) => {
      var text;
      while (el.clientHeight < el.scrollHeight) {
        text = el.innerHTML.trim();
        if (text.split(' ').length <= 1) {
          break;
        }
        el.innerHTML = text.replace(/\W*\s(\S)*$/, '...');
      }
    });
  }

  renderItems($items) {
    let $colAniEl;

    if (this.isDesktopView) {
      const sides = this.updateSide($items);
      this.desktopLeftItems = sides.left;
      this.desktopRightItems = sides.right;
      this.$desktopLeft.html(this.desktopLeftItems);
      this.$desktopRight.html(this.desktopRightItems);

      this.paragraphOverflow(this.$desktopLeft);
      this.paragraphOverflow(this.$desktopRight);
      $colAniEl = this.$desktop.find(`${this.options.itemSelector}:not(.${this.options.itemHideClass})`);
    } else {
      $colAniEl = this.$mobile.find(`${this.options.itemSelector}:not(.${this.options.itemHideClass})`);
    }
    this.setItemAnimation($colAniEl);
  }

  setItemAnimation($el) {
    //// Initing the column-animation ////
    if (!this.isExperienceEditing) {
      return new InViewPort({
        el: $el,
        inViewinitedClass: 'col-in-viewport-inited',
        inViewClass: 'col-was-in-viewport',
        inViewCurrentClass: 'col-in-viewport',
        eventName: 'itemInView',
        offsetBottom: -50,
        offsetTop: 800
      });
    }
    return null;
  }

  setupResizeListener() {
    this.$window.on('resize', debounce((e) => {
      this.setupView();
      this.renderItems(this.$currentItems);

    }, 200));
  }

  setupLoadmoreBtn() {
    this.renderLoadmoreBtn(this.$loadMoreBtn, this.getHiddenItems().length > 0);
    this.setupLoadMoreBtnListener();
  }

  renderLoadmoreBtn($loadMoreBtn, show = true) {
    if (!show) {
      $loadMoreBtn.addClass('ff__btn__loadmore--hide');
    } else {
      $loadMoreBtn.removeClass('ff__btn__loadmore--hide');
      $loadMoreBtn.closest('.col').removeClass('col-in-viewport-inited col-in-viewport col-was-in-viewport');
      this.setItemAnimation($loadMoreBtn.closest('.col'));
    }
  }

  setupTagBtnListener() {
    this.$tags.on('click', this.options.tagSelector, e => {
      e.preventDefault();
      const $btn = $(e.target);
      if (!$btn.hasClass(this.options.tagInactiveClass)) {
        return;
      }
      const tag = $btn.data('tag');

      this.$currentItems = typeof this.savedEl[tag] !== 'undefined' ? this.savedEl[tag] : this.$items;
      this.setupItems(this.$currentItems);
      this.renderLoadmoreBtn(this.$loadMoreBtn, this.getHiddenItems().length > 0);
      this.$tagBtns.addClass(this.options.tagInactiveClass);
      $btn.removeClass(this.options.tagInactiveClass);
      FilterFunction.SetHash(this.objectIndex, tag);
    });
  }

  getHiddenItems() {
    return this.$currentItems.filter((i, el) => $(el).hasClass(this.options.itemHideClass));
  }

  showMore(count = this.options.maxItemLoadMore) {
    const $visibleItems = this.getHiddenItems();

    $visibleItems
      .slice(0, count)
      .removeClass(this.options.itemHideClass);
    this.renderItems(this.$currentItems);
  }

  setupLoadMoreBtnListener() {
    this.$loadMoreBtn.on('click', e => {
      e.preventDefault();
      this.showMore(this.options.maxItemLoadMore);
      this.renderLoadmoreBtn(this.$loadMoreBtn, this.getHiddenItems().length > 0);
    });
  }
}
