import { TYPES } from './dynamic-loading-options';
import None from './handler/none';
import Single from './handler/single';
import Multiple from './handler/multiple';
import Custom from './handler/custom';

const handlers = {};

handlers[TYPES.NONE] = None;
handlers[TYPES.SINGLE] = Single;
handlers[TYPES.MULTIPLE] = Multiple;
handlers[TYPES.CUSTOM] = Custom;

export default (type) => handlers[type];
