import $ from 'jquery';

export default class CustomerDetails {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.customerDetails'
      },
      config
    );
    this.$el = $(this.config.el);
    this.init();
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    // bind your events here.
  }
}
