import DynamicLoadingComponents from './dynamic-loading-components';
import DynamicLoadingHandlers from './dynamic-loading-handlers';
import DynamicLoadingOptions from './dynamic-loading-options';

const { TYPES } = DynamicLoadingOptions;

const componentHasName = ({ name }) => !!name;

const componentImporterIsFunction = ({ importer }) => typeof importer === 'function';

const componentGenerateNodes = (component) => {
  const selector = component.selector || `[${DynamicLoadingOptions.attr}="${name}"]`;
  const nodes = document.querySelectorAll(selector);
  const type = component.type || TYPES.NONE;
  const callback = component.callback || DynamicLoadingOptions.noop;
  return {
    name: component.name,
    importer: component.importer,
    selector,
    type,
    nodes,
    callback
  }
}

const componentNodesIsNotEmpty = ({ nodes }) => {
  return nodes.length > 0;
};

const createComponentPromises = ({ name, nodes, callback, importer, type }) => {
  return new Promise((resolve, reject) => {
    importer().then((modules) => {
      const handler = DynamicLoadingHandlers(type);
      resolve(handler(name, modules, nodes, callback));
    }).catch((e) => {
      console.error(`Could not load ${name} module`);
      reject(e);
    });
  });
};

const dynamicLoading = (components) => {
  return components
    .filter(componentHasName)
    .filter(componentImporterIsFunction)
    .map(componentGenerateNodes)
    .filter(componentNodesIsNotEmpty)
    .map(createComponentPromises)
};

let instance = null;

export default class DynamicLoading {
  constructor() {
    if (instance) {
      return instance;
    }
    
    this.dynamicLoadingPromises = dynamicLoading(DynamicLoadingComponents);
    this.dynamicLoadingAll = Promise.all(this.dynamicLoadingPromises.filter((item) => !!item));
    instance = this;
  }
}
