/**
 * Class: Sitemap
 * Description: Her goes description
 */
import $ from 'jquery';

export default class Sitemap {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.sitemap'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$sitemapTop = this.$el.find('.sitemap-top');
    this.$sitemapTopRow = this.$el.find(
      '.sitemap-top > .row, .sitemap-top.row'
    );
    this.$expands = this.$el.find('.sitemap-expand');

    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;
    this.$expands.find('.expand-trigger').on({
      click: function(/* e */) {
        var $parent = $(this).parents('.expand');
        if (!$parent.data('module').isVisible) {
          self.closeAllexpands();
        }
        self.checkExpands();
      }
    });
  }
  closeAllexpands() {
    $.each(this.$expands, function(i, el) {
      $(el)
        .data('module')
        .close();
    });
  }
  checkExpands() {
    var allClosed = false;
    $.each(this.$expands, function(i, el) {
      if ($(el).data('module').isVisible) {
        allClosed = true;
      }
    });
    if (allClosed) {
      this.$sitemapTop.removeClass('has-open-expand');
      this.$sitemapTopRow.addClass('theme-blue');
      this.$sitemapTopRow.removeClass('theme-white-blue');
    } else {
      this.$sitemapTop.addClass('has-open-expand');
      this.$sitemapTopRow.removeClass('theme-blue');
      this.$sitemapTopRow.addClass('theme-white-blue');
    }
  }
}
