/**
 * Class: Link List
 */
import $ from 'jquery';

export default class List {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.list'
      },
      config
    );

    this.$el = $(this.config.el);
    if (this.$el.length) {
      this.init();
    }
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    this.$el.on('click', 'a.list-header-column-view-toggle-list', function(
      event
    ) {
      event.preventDefault();
      var list = $(this).parents('.list-items');
      if (list.hasClass('list-type-cards')) {
        var originalListType = list.data('originalListType');
        if (!originalListType) {
          originalListType = 'list';
        }
        list.data('originalListType', originalListType);
        list.removeClass('list-type-cards');
        list.addClass('list-type-' + originalListType);
      }
    });

    this.$el.on('click', 'a.list-header-column-view-toggle-cards', function(
      event
    ) {
      event.preventDefault();
      var list = $(this).parents('.list-items');
      if (!list.hasClass('list-type-cards')) {
        var originalListType = 'list';
        if (list.hasClass('list-type-table')) {
          originalListType = 'table';
        }
        list.data('originalListType', originalListType);
        list.removeClass('list-type-list list-type-table');
        list.addClass('list-type-cards');
      }
    });

    const collapseable = $(this.$el).hasClass('list--collapseable');

    if(collapseable && $( window ).width() < 960) {
      $(this.$el).first('.list-title').click(
          function() { 
            console.log('clicked', this);
            $(this).closest('.list').toggleClass('list--toggle');
          }
      );
    }

    if($( window ).width() < 960 && ($(this.$el).parent().hasClass('article-placeholder-right') || $(this.$el).parent().hasClass('article-placeholder-left'))) {
      $(this.$el).parent().addClass('align-top');
    }
  }
}
