import $ from 'jquery';

export default class OrderFlow {
  constructor(config) {
    this.orderFlow = null;
    this.steps = null;
    this.link = null;
    this.config = $.extend(
      {
        el: '.orderflow'
      },
      config
    );
    this.$el = $(this.config.el);

    if (this.$el.length) {
      this.init();
    }
  }
  init(/* options */) {
    this.orderflow = $('div.orderflow');
    this.link = $('a.megaNavigation-item-active')
      .siblings('ul.megaNavigation-submenu')
      .find('a.megaNavigation-submenu-item-link-active');
    this.bindEvents();
  }
  bindEvents() {
    // var self = this;

    var to = this.link.attr('href');

    this.link.on('click', function() {
      this.orderflow = $('div.orderflow');
      this.steps = $('div.orderflow-centered > div.stepindicator');
      if (this.orderflow.length > 0 && this.steps.length == 0) {
        window.location = to;
      }
    });
  }
}
