// Globals
const $ = window.$ = require('jquery');

var oldParseJSON = $.parseJSON;
$.parseJSON = function (json) {
  if (!json && json !== null) {
    console.warn("jQuery.parseJSON requires a valid JSON string");
    return null;
  }
  return oldParseJSON.apply(this, arguments);
};

// This function is already deprecated in jquery v3
$.fn.extend({
  size: () => {
    return this.length;
  }
});

