if (window.app !== undefined) {
    window.app.factory('FlowService', ['$http', function ($http) {

        var service = {};
        service.model = {};

        service.init = function (model) {
            for (var prop in model) {
                if (model.hasOwnProperty(prop) && model[prop]) {
                    if (!service.model[prop]) {
                        service.model[prop] = model[prop];
                    }
                }
            }
        };

        service.save = function (id) {
            $http.post('/api/flow/save/' + id, service.model);
        }

        return service;
    }]);
}
