/**
 * Class: AnnouncementList
 * Description: AnnouncementList script
 */
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions, $win } from '../base/utils/utils';

export default class Announcement {
  constructor(config) {
    this.sets = {
      pagesize: 20,
      offset: 0,
      item: null,
      apiAddress: '',
      domOrder: '',
      createdRow: function(row, data, index) {
        $(row).addClass('list-items-item');
      },
      columnDefs: [
        {
          targets: [0],
          className: "list-items-item-column  list-items-item-column-haslink",
          searchable: true,
          render: function (data, type, row) {
            const dateFormatted = moment(data).format(DateFormatingOptions.dateFormat);
            return `<a class="list-items-item-column-link" href="${row[3]}"></a><span class="list-items-item-value list-items-item-value-type-text">${dateFormatted}</span>`;
          }
        },
        {
          targets: [1],
          className: "list-items-item-column list-items-item-column-haslink-mobile",
          render: function (data, type, row) {
            const dateFormatted = moment(row[0]).format(DateFormatingOptions.dateFormat);
            return `<a class="list-items-item-column-link" href="${row[3]}"></a><span class="list-items-item-value list-items-item-value-type-text hide-date-md">${dateFormatted}</span><span class="list-items-item-value list-items-item-value-type-text"><b>${row[1]}</b><br>${row[2]}</span>`;
          }
        }
      ],
      language: {
        paginate: {
          next: '<span aria-hidden="true"></span><span class="sr-only">Next</span>',
          previous: '<span aria-hidden="true"></span><span class="sr-only">Previous</span>',
        },
        emptyTable: "No data available in table"
      }
    };

    this.dataTableElem = null;

    this.config = $.extend({
        el: '.announcement-module',
        table: '.announcements-main-list',
        filters: '.announcement-filters',
        dropdowns:'.announcement-filters-dropdown',
        monthFilterContainer: '.announcement-filters-month',
        month: '.month',
        year: '.year'
      },config);
    this.$el = $(this.config.el);
    this.$table = $(this.config.table);
    this.$filters = $(this.config.filters);
    this.$month = this.$filters.find(this.config.month);
    this.$year = this.$filters.find(this.config.year);
    this.$monthFilterContainer = this.$filters.find(this.config.monthFilterContainer);

    if(this.$el.length){
      this.init();
    }
  }

  init(options) {
    const AnnouncementListOptions = window.AnnouncementListOptions || {};
    $.extend(true, this.sets, AnnouncementListOptions);
    this.initDataTable();
    this.bindEvents();
  }

  initDataTable() {
    if ($win.width() > 420 && $win.width() < 768) {
      $.fn.dataTable.ext.pager.numbers_length = 6;
    } else if ($win.width() > 330 && $win.width() < 420) {
      $.fn.dataTable.ext.pager.numbers_length = 5;
    } else if ($win.width() <= 330) {
      $.fn.dataTable.ext.pager.numbers_length = 4;
    }

    this.dataTableElem = this.$table.DataTable({
      ordering: false,
      info: false,
      responsive: true,
      stateSave: true,
      bLengthChange: false,
      processing: true,
      serverSide: true,
      searching: false,
      ajax: {
        url: this.sets.apiAddress,
        data: (d) => {
          d.offset = this.sets.offset;
          d.item = this.sets.item;
          d.month = this.$month.val();
          d.year = this.$year.val();
          d.cacontextlanguage = this.sets.calanguage;
        },
        dataSrc: this.sets.dataSrcFunction
      },
      pageLength: this.sets.pagesize,
      dom: this.sets.domOrder,
      createdRow: this.sets.createdRow,
      columnDefs: this.sets.columnDefs,
      language: this.sets.language
    });
  }

  bindEvents() {
    this.$month.on('change', () => {
      this.dataTableElem.draw();
    });

    this.$year.on('change', (e) => {
      if ($(e.target).val() != 0) {
        this.$monthFilterContainer.show();
      } else {
        this.$monthFilterContainer.hide();
      }

      this.dataTableElem.draw();
    });
  }
}
