/**
 * Class: moving-form
 * Description: Her goes description
 */
import $ from 'jquery';

export default class MovingForm {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    this.config = $.extend(
      {
        el: '.moving-form'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$allTogglableFieldsets = this.$el.find('fieldset[data-show-if]'); //all fieldsets
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    let thiz = this;
    this.$allTogglableFieldsets.find('select').on('change', function() {
      //hide any levels below the currently changed level

      // let currentLevel = parseInt(this.closest("fieldset").getAttribute('data-level'));
      // currentLevel = currentLevel+1; //all levels below the changed one should be hidden again
      // if(currentLevel < 3){
      // while(currentLevel < 3){
      // let elemntsOnLevel = thiz.$el.find('[data-level="'+currentLevel+'"]');
      // elemntsOnLevel.addClass('hidden-xs-up');
      // elemntsOnLevel.find('select').val(""); //reset values for selects being re-hidden
      // currentLevel = currentLevel+1;
      // }
      // }

      // Hide all selects further down the DOM tree
      let current = $(this).closest('fieldset');
      current = current.next();
      while (current.length) {
        current.addClass('hidden-xs-up');
        current.find('select').val('');
        current = current.next();
      }

      //toggle content
      thiz.$el
        .find('div[data-show-if]')
        .not('[data-show-if~="' + this.value + '"]')
        .addClass('hidden-xs-up'); //hide content that doesn't have the current show-if-attributes
      thiz.$el
        .find('[data-show-if~="' + this.value + '"]')
        .removeClass('hidden-xs-up');
    });
  }
}
