// Depended on bootstrap modal
import $ from 'jquery';
import { createElementFromHTML, getElement, isFunction, noop } from '../base/utils/utils';
import './videobox.scss';

const defaultOptions = {
  modal: {
    show: false,
    focus: false
  },
  videoMuted: false,
  videoControls: true,
  videoEndClose: false,
  onClose: noop,
  onOpen: noop,
  onClick: noop
};

const className = {
  video: 'videoBox__modal-video',
  disabled: 'videoBox--disabled'
}

const videoBoxTemplate = (props = defaultOptions) => `<div class="modal fade videoBox__modal" tabindex="-1" role="dialog" style="display: none;" aria-hidden="true">
  <div class="modal-dialog videoBox__modal-dialog" role="document">
    <video
      class="videoBox__modal-video"
      aria-label=""
      disablePictureInPicture
      ${props.videoPoster ? 'poster="' + props.videoPoster + '"' : ''}
      ${String(props.videoControls).toLowerCase() === "true" ? "controls" : ""}
      ${String(props.videoMuted).toLowerCase() === "true" ? "muted" : ""}
      playsinline
      src="${props.videoSource}"
      type="video/mp4">
    </video>
    <button type="button" class="videoBox__modal-close" data-dismiss="modal" aria-label="Close">
      <span aria-hidden="true">&times;</span>
    </button>
  </div>
</div>`;

function initVideoBox() {
  const $template = createElementFromHTML(videoBoxTemplate(this.options));

  document.body.appendChild($template);
  this.$videoModal = $($template);
  this.$videoModal.modal(this.options.modal);
  this.$video = this.$videoModal[0].querySelector(`.${className.video}`);
}

function elementClickHandler(e) {
  e.preventDefault();
  this.$videoModal.modal('show');

  if (isFunction(this.options.onClick)) {
    this.options.onClick(this, e);
  }
}

function videoEndedHandler(e) {
  console.log(this.options.videoEndClose);
  if (String(this.options.videoEndClose).toLowerCase() === 'true') {
    closeModal.bind(this)();
  }
}

function openModal() {
  this.$videoModal.modal('show');

}

function closeModal() {
  this.$videoModal.modal('hide');
}

function playVideo() {
  this.$video.play();
}

function pauseVideo() {
  this.$video.pause();
}

function stopVideo() {
  pauseVideo.bind(this)();
  if (this.$video.currentTime) {
    this.$video.currentTime = 0;
  }
}

function setupEventListener() {
  if (this.$el.addEventListener) {
    this.$el.addEventListener('click', elementClickHandler.bind(this), false);
  }

  if (this.$videoModal) {
    this.$videoModal.on('shown.bs.modal', (e) => {
      this.$video.play();

      if (isFunction(this.options.onOpen)) {
        this.options.onOpen(this, e);
      }
    });

    this.$videoModal.on('hide.bs.modal', (e) => {
      this.$video.pause();

      if (isFunction(this.options.onClose)) {
        this.options.onClose(this, e);
      }
    });

    this.$videoModal.on('hidden.bs.modal', (e) => {
      this.$video.pause();
    });

    if (this.$video.addEventListener) {
      this.$video.addEventListener('ended', videoEndedHandler.bind(this));
    }
  }
}

export default class VideoBox {
  constructor(_options) {
    this.options = {
      ...defaultOptions,
      ..._options
    };

    this.$el = getElement(this.options.el);

    if (!this.$el) {
      console.warn('There is no valid node');
      return;
    }

    if (!this.options.videoSource) {
      console.warn('There is no video');
      this.$el.classList.add(className.disabled);
      this.$el.disabled = true;
      return;
    }

    initVideoBox.bind(this)();
    setupEventListener.bind(this)();
  }

  open() {
    openModal.bind(this)();
  }

  close() {
    closeModal.bind(this)();
  }

  play() {
    playVideo.bind(this)();
  }

  pause() {
    pauseVideo.bind(this)();
  }

  stop() {
    stopVideo.bind(this)();
  }
}
