/**
 * Class: Social
 * Description: Her goes description
 */
import $ from 'jquery';

export default class Social {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) { // put in defaults here
    this.config = $.extend({
      el: '.social-media-feed'
    },config);
    this.$el = $(this.config.el);
    this.init();
  }
  init() {
    this.bindEvents();

    this.initTwitter();
    this.initColoredContentBanner();
  }
  bindEvents() {
    // bind your events here.
  }

  initTwitter() {
    setTimeout(() => {
      const $twitter = this.$el.find('.twitter-feed');
      if ($twitter.find('iframe').length === 0) {
        $twitter.find('.twitter-timeline').addClass('social-media-feed--hide');
        $twitter.find('.social-media-feed__fallback').removeClass('social-media-feed--hide');
        $twitter.parent('.twitter').css('overflow', 'hidden');
      }
    }, 500);
  }

  initColoredContentBanner() {
    const $coloredContentBanner = this.$el
      .filter('.fifty-percent-feed')
      .parent('.coloredContentBox-cta');

    if ($coloredContentBanner.length > 0) {
      $coloredContentBanner.addClass('coloredContentBox-cta--social-media-fifty');
    }
  }
}
