/* eslint-disable */
import $ from "jquery";

window.encapsulateString = function(text) {
  return '"' + text.replace(/"/g, "'") + '"';
};

window.decapsulateString = function(text) {
  if (
    typeof text === 'string' &&
    text.substr(0, 1) === '"' &&
    text.substr(text.length - 1, 1) === '"'
  )
    return text.substr(1, text.length - 2);
  else return text;
};

window.getRequestParameter = function(key) {
  var parameters = window.location.search.replace('?', '');

  var keyValues = parameters.split('&');

  for (var i = 0; i < keyValues.length; ++i) {
    var keyValue = keyValues[i].split('=');

    if (keyValue[0] === key) return decodeURIComponent(keyValue[1]);
  }

  return '';
};

//Inspired from http://stackoverflow.com/questions/281264/remove-empty-elements-from-an-array-in-javascript
window.cleanArray = function(actual, deleteValue) {
  var newArray = new Array();
  for (var i = 0; i < actual.length; ++i) {
    if (actual[i] !== deleteValue) {
      newArray.push(actual[i]);
    }
  }

  return newArray;
};

window.appendToUrls = function(parameterName, parameterValue) {
  var links = $('a');

  for (var i = 0, length = links.length; i < length; ++i) {
    var currentHref = $(links[i]).attr('href');

    if (angular.isUndefined(currentHref) || currentHref.indexOf('/') !== 0)
      continue;

    //If the link contains other parameters, add it, otherwise begin parameters
    if (currentHref.indexOf('?') > -1)
      $(links[i]).attr(
        'href',
        currentHref + '&' + parameterName + '=' + parameterValue
      );
    else
      $(links[i]).attr(
        'href',
        currentHref + '?' + parameterName + '=' + parameterValue
      );
  }
};

window.shortYear = function(value) {
  if (typeof value !== 'string') return value.toString().slice(-2);
  else return value.slice(-2);
};

//From http://stackoverflow.com/questions/5639346/what-is-the-shortest-function-for-reading-a-cookie-by-name-in-javascript
window.getCookieValue = function getCookieValue(a) {
  var b = document.cookie.match('(^|;)\\s*' + a + '\\s*=\\s*([^;]+)');
  return b ? b.pop() : '';
};

//From http://stackoverflow.com/questions/3224834/get-difference-between-2-dates-in-javascript
window.getDifferenceInDays = function(fromDate, toDate) {
  var timeDiff = Math.abs(toDate.getTime() - fromDate.getTime());
  var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24));

  return diffDays;
};

window.getDifferenceInHours = function(fromDate, toDate) {
  var timeDiff = Math.abs(toDate.getTime() - fromDate.getTime());
  var diffHours = Math.ceil(timeDiff / (1000 * 3600));

  return diffHours;
};

window.disableSpinner = function(button) {
  button.next('.formLoaderContainer').remove();
  button.removeAttr('disabled');
  button.css('padding-left', '');
  button.css('-webkit-transition', '');
  button.css('transition', '');
};

$(document).ready(function() {
  //From http://stackoverflow.com/questions/9046184/reload-the-site-when-reached-via-browsers-back-button
  //Used to prevent people from going back in the browser history for accessing pages which require authentication
  var $input = $('#refresh-page');

  $input.val() === 'yes' ? location.reload(true) : $input.val('yes');

  //Add referer to the contact page
  var $contactAnchor = $('a.refer-link');

  $contactAnchor.attr(
    'href',
    $contactAnchor.attr('href') + '?referer=' + window.location
  );
});

//Make date sorting possible in datatables
//Taken from https://github.com/l-lin/angular-datatables/issues/313
$.extend($.fn.dataTableExt.oSort, {
  'date-pre': function(date) {
    return moment(date, 'DD-MM-YYYY');
  },
  'date-asc': function(a, b) {
    return a.isBefore(b) ? -1 : a.isAfter(b) ? 1 : 0;
  },
  'date-desc': function(a, b) {
    return a.isBefore(b) ? 1 : a.isAfter(b) ? -1 : 0;
  },
  'danish-number-pre': function(number) {
    return parseFloat(number.replace('.', '').replace(',', '.'));
  },
  'danish-number-asc': function(a, b) {
    return a < b ? -1 : a > b ? 1 : 0;
  },
  'danish-number-desc': function(a, b) {
    return a < b ? 1 : a > b ? -1 : 0;
  }
});

window.WebForm_FireDefaultButton = function(event, target) {
  if (event.keyCode == 13) {
    var __nonMSDOMBrowser =
      window.navigator.appName.toLowerCase().indexOf('explorer') == -1;
    var src = event.srcElement || event.target;
    if (!src || src.tagName.toLowerCase() != 'textarea') {
      var defaultButton;
      if (__nonMSDOMBrowser) {
        defaultButton = document.getElementById(target);
      } else {
        defaultButton = document.all[target];
      }
      if (defaultButton && typeof defaultButton.click != 'undefined') {
        defaultButton.click();
        event.cancelBubble = true;
        if (event.stopPropagation) event.stopPropagation();
        return false;
      }
    }
  }
  return true;
};
