/**
 * Class: Button
 * Description: Her goes description
 */
import $ from "jquery";
import * as utils from "../base/utils/utils";
import Cookies from "js-cookie";
export default class StickyBar {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults

    this.config = $.extend(
      {
        $el: ".sticky-bar",
        disableCookie: false,
        cookieExpiryDays: 1
      },
      config
    );
    this.$el = $(this.config.$el);
    this.$headerEl = $(".headerSlider-top");
    this.$closeBtn = this.$el.find(".sticky-bar__close-btn");
    this.showClassName = "sticky-bar--show";
    this.cookieName = "stickyBar" + "__" + this.config.cookieId;
    this.cookieValue = "hiddenBar";
    this.disableCookie = this.config.disableCookie;
    this.cookieExpiryDays = this.config.cookieExpiryDays;

    this.init();
  }

  init() {
    this.bindEvents();
  }

  addToHeader() {
    if (this.$headerEl) {
      this.$headerEl.append(this.$el);
      if (!Cookies.get(this.cookieName) || this.disableCookie) {
        this.$el.addClass(this.showClassName);
      }
    }
  }

  close() {
    this.$closeBtn.on("click", () => {
      if (!this.disableCookie) {
        Cookies.set(this.cookieName, this.cookieValue, {
          expires: this.cookieExpiryDays
        });
      }
      this.$el.fadeOut(() => {
        this.$el.removeClass(this.showClassName);
      });
    });
  }

  bindEvents() {
    if (
      !utils.$body.hasClass("is-experience-editing") &&
      !utils.$body.hasClass("is-experience-preview")
    ) {
      this.addToHeader();
    }
    this.close();
  }
}
