import FontFaceOnload from 'fontfaceonload';
import $ from 'jquery';

export default class Icons {
  constructor() {
    this.init();
  }
  init() {
    this.bindEvents();
  }

  bindEvents() {
    // var self = this;
    FontFaceOnload('dong-maine', {
      success: function() {
        $('.show-icon-onfontload').removeClass('show-icon-onfontload');
      },
      error: function() {
        $('.show-icon-onfontload').removeClass('show-icon-onfontload');
      },
      timeout: 1000
    });
  }
}
