/**
 * Class: dawaAutocomplete
 * Description: Autocomplete for addresses using DAWA.
 * This function is based on the project: https://github.com/DanmarksAdresser/autocomplete
 */
import $ from 'jquery';
import 'jquery-ui/ui/widgets/autocomplete';
const angular = window.angular;

export default class dawaAutocomplete {
  constructor(/* config */) {
    // Fix for wrong width of autocomplete field in some cases.
    // see this for detailt: https://stackoverflow.com/a/11845718
    $.ui.autocomplete.prototype._resizeMenu = function() {
      var ul = this.menu.element;
      ul.outerWidth(this.element.outerWidth());
    };

    $.widget('dawa.dawaautocomplete', {
      options: {
        jsonp: !('withCredentials' in new XMLHttpRequest()),
        baseUrl: 'https://dawa.aws.dk',
        minLength: 2,
        delay: 300,
        adgangsadresserOnly: false,
        autoFocus: true,
        timeout: 10000,
        error: null,
        params: {},
        fuzzy: true,
        stormodtagerpostnumre: true
      },

      _create: function() {
        var element = this.element;
        var options = this.options;
        var targetType = options.adgangsadresserOnly
          ? 'adgangsadresse'
          : 'adresse';
        var autocompleteWidget = this;
        var caretpos = null;
        var adgangsadresseid = null;
        var skipVejnavn = false;

        var cache = {};

        // perform a GET request to DAWA autocomplete, caching the response
        function get(params, cb) {
          var stringifiedParams = JSON.stringify(params);
          if (cache[stringifiedParams]) {
            return cb(cache[stringifiedParams]);
          }
          $.ajax({
            url: options.baseUrl + '/autocomplete',
            dataType: options.jsonp ? 'jsonp' : 'json',
            data: $.extend({}, params, options.params),
            timeout: options.timeout,
            success: function(data) {
              cache[stringifiedParams] = data;
              cb(data);
            },
            error: options.error
          });
        }

        function stormodtagerAdresseTekst(data) {
          var adresse = data.vejnavn;
          if (data.husnr) {
            adresse += ' ' + data.husnr;
          }
          if (data.etage || data.dr) {
            adresse += ',';
          }
          if (data.etage) {
            adresse += ' ' + data.etage + '.';
          }
          if (data.dr) {
            adresse += ' ' + data.dr;
          }
          adresse += ', ';
          if (data.supplerendebynavn) {
            adresse += data.supplerendebynavn + ', ';
          }
          adresse +=
            data.stormodtagerpostnr + ' ' + data.stormodtagerpostnrnavn;
          return adresse;
        }

        function getAutocompleteResponse(type, q, currentCaretPos, cb) {
          var params = { q: q, type: type, caretpos: currentCaretPos };
          if (options.fuzzy) {
            params.fuzzy = '';
          }
          if (adgangsadresseid) {
            params.adgangsadresseid = adgangsadresseid;
          }
          if (skipVejnavn) {
            params.startfra = 'adgangsadresse';
          }
          skipVejnavn = false;

          var adgangsadresseRestricted = !!adgangsadresseid;

          // Vi begrnser kun til en bestemt adgangsadresseid n gang
          adgangsadresseid = null;

          get(params, function(result) {
            var processedResult = result
              .reduce(function(memo, row) {
                if (
                  (row.type === 'adgangsadresse' || row.type === 'adresse') &&
                  row.data.stormodtagerpostnr
                ) {
                  // Vi har modtaget et stormodtagerpostnr. Her vil vi muligvis gerne vise stormodtagerpostnummeret
                  var stormodtagerEntry = $.extend({}, row);
                  stormodtagerEntry.tekst = stormodtagerAdresseTekst(row.data);
                  stormodtagerEntry.forslagstekst = stormodtagerEntry.tekst;

                  var rows = [];
                  // Omvendt, hvis brugeren har indtastet den almindelige adresse eksakt, s er der ingen
                  // grund til at vise stormodtagerudgaven
                  if (q !== stormodtagerEntry.tekst) {
                    rows.push(row);
                  }

                  // Hvis brugeren har indtastet stormodtagerudgaven af adressen eksakt, s viser vi
                  // ikke den almindelige udgave
                  if (q !== row.tekst) {
                    rows.push(stormodtagerEntry);
                  }

                  // brugeren har indtastet stormodtagerpostnummeret, svi viser stormodtager udgaven frst.
                  if (
                    rows.length > 1 &&
                    q.indexOf(row.data.stormodtagerpostnr) !== -1
                  ) {
                    rows = [rows[1], rows[0]];
                  }
                  memo = memo.concat(rows);
                } else {
                  memo.push(row);
                }
                return memo;
              }, [])
              .map(function(row) {
                return {
                  label: row.forslagstekst,
                  value: row
                };
              });
            if (adgangsadresseRestricted && processedResult.length === 1) {
              // der er kun en adresse p adgangsadressen
              element.val(processedResult[0].value.tekst);
              element.selectionStart = caretpos =
                processedResult[0].value.caretpos;
              element.autocomplete('close');
              autocompleteWidget._trigger(
                'select',
                null,
                processedResult[0].value
              );
            } else {
              cb(processedResult);
            }
          });
        }

        var autocompleteOptions = $.extend({}, options, {
          source: function(request, response) {
            var q = request.term;
            caretpos = element[0].selectionStart;
            return getAutocompleteResponse(targetType, q, caretpos, response);
          },
          select: function(event, ui) {
            event.preventDefault();
            var item = ui.item.value;
            if (element.is('div')) {
              element.html(item.tekst);
              placeCaretAtEnd(element[0]);
            } else {
              element.val(item.tekst);
              angular.element(element).triggerHandler('change');
              element[0].selectionStart = element[0].selectionEnd = caretpos =
                item.caretpos;
            }

            element.next('.dawa-postalcode').val(item.data.postnr);

            if (item.type !== targetType) {
              if (item.type === 'adgangsadresse') {
                adgangsadresseid = item.data.id;
              }
              if (item.type === 'vejnavn') {
                skipVejnavn = true;
              }
              setTimeout(function() {
                element.autocomplete('search');
              });
            } else {
              element.trigger('dawaComplete'); // Used on Survey component
              element.autocomplete('close');
              autocompleteWidget._trigger('select', null, item);
            }
          }
        });
        element.autocomplete(autocompleteOptions);
        element.on('autocompletefocus', function(event) {
          event.preventDefault();
        });
        // der er tilsyneladende ingen skudsikker mde at f en event nr
        // caret positionen ndrer sig, s vi benytter en timer i stedet.
        setInterval(function() {
          var currentCaretpos = element[0].selectionStart;
          if (
            element[0] === document.activeElement &&
            caretpos !== currentCaretpos
          ) {
            caretpos = currentCaretpos;
            element.autocomplete('search');
          }
        }, 100);
      }
    });

    $('.dawa-autocomplete').dawaautocomplete({
      select: function(event, adresse) {
        //Setting up customerAddress for SS Profile Page..setProfileData attribute is used for Profile page only
        if ($(this).attr('setProfileData')) {
          var ctrlName = 'profile';
          var selCtrl = 'div[ng-controller="' + ctrlName + '"]';
          var $scope = angular.element(selCtrl).scope();
          $scope.customerAddress = adresse.data;
          $scope.$apply();
        }
      },
      error: function(xhr, status, error) {
        $(this)
          .closest('div')
          .find('.help-block')
          .text('Der opstod en fejl: ' + status + ' - ' + error);
      }
    });

    // place caret at end of contenteditable div/span
    // source: https://stackoverflow.com/a/4238971
    function placeCaretAtEnd(el) {
      el.focus();
      if (
        typeof window.getSelection != 'undefined' &&
        typeof document.createRange != 'undefined'
      ) {
        var range = document.createRange();
        range.selectNodeContents(el);
        range.collapse(false);
        var sel = window.getSelection();
        sel.removeAllRanges();
        sel.addRange(range);
      } else if (typeof document.body.createTextRange != 'undefined') {
        var textRange = document.body.createTextRange();
        textRange.moveToElementText(el);
        textRange.collapse(false);
        textRange.select();
      }
    }
  }
}
