import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions, $win } from '../base/utils/utils';

export default class Article {
  constructor(config) {
    this.sets = {
      pagesize: 20,
      offset: 0,
      item: null,
      apiAddress: '',
      domOrder: '',
      //initCompleteFunction: null,
      //dataSrcFunction: function(json) { return json.data; },
      //createdRow: null,
      //columnDefs: []
      initCompleteFunction : function() {
        //$('.newslistBottom').html("<a href='/rss/news'><img src='../Images/rss_icon.png' width='45px'></a>");
      },
      createdRow: function(row, data, index) {
        $(row).addClass('list-items-item');
      },
      columnDefs: [
        {
          targets: [0],
          className: "list-items-item-column",
          render: function ( data, type, row ) {
            return '<a class="list-items-item-column-link" href="'+row[0]+'"></a>';
          }
        },
        {
          targets: [1],
          className: "list-items-item-column-image",
          render: function ( data, type, row ) {
            if (!row[1]) return "";
            return '<div class="list-items-item-value list-items-item-value-type-image" style="background-image: url(\''+row[1]+'\')"></div>';
          }
        },
        {
          targets: [2],
          className: "list-items-item-column",
          render: function ( data, type, row ) {
            var dateFromated = moment(row[2]).format(DateFormatingOptions.dateFormat);
            return '<span class="list-items-item-value list-items-item-value-type-date">' + dateFromated + '</span>'+
              '<span class="list-items-item-value list-items-item-value-type-header">' + row[3] + '</span>'+
              '<span class="list-items-item-value list-items-item-value-type-description">' + row[4] + '</span>';
          }
        }
      ],
      language: {
        paginate: {
          next: '<span aria-hidden="true"></span><span class="sr-only">Next</span>',
          previous: '<span aria-hidden="true"></span><span class="sr-only">Previous</span>'
        }
      }
    };

    this.dataTableElem = null;

    this.config = $.extend({
        el: '.article-main-list'
      },config);
    this.$el = $(this.config.el);

    if(this.$el.length){
      this.init();
    }
  }

  init(options) {
    $.extend(this.sets, newsListOptions);
    this.initDataTable();
    this.bindEvents();
  }
  bindEvents() {
    // bind your events here.
  }
  initDataTable() {

    if ($win.width() > 420 && $win.width() < 768) {
      $.fn.dataTable.ext.pager.numbers_length = 6;
    } else if ($win.width() > 330 && $win.width() < 420) {
      $.fn.dataTable.ext.pager.numbers_length = 5;
    } else if ($win.width() <= 330) {
      $.fn.dataTable.ext.pager.numbers_length = 4;
    }


    this.dataTableElem = this.$el.DataTable({
      "ordering": false,
      "info": false,
      "responsive": true,
      "stateSave": true,
      "bLengthChange": false,
      "processing": true,
      "serverSide": true,
      "searching": false,
      /* "ajax": {
        "url": '/api/news',
        "data": {
          "offset": this.sets.offset,
          "item": this.sets.item
        },
        "dataSrc": this.sets.dataSrcFunction
      }, */
      /* "pageLength": this.sets.pagesize, */
      /* "dom": '<"top">t<"newslistBottom">p', */
      "ajax": {
        "url": this.sets.apiAddress,
        "data": {
          "offset": this.sets.offset,
          "item": this.sets.item
        },
        "dataSrc": this.sets.dataSrcFunction
      },
      "pageLength": this.sets.pagesize,
      "dom": this.sets.domOrder,
      "initComplete": this.sets.initCompleteFunction,
      "createdRow": this.sets.createdRow,
      "columnDefs": this.sets.columnDefs,
      "language": this.sets.language
    });
  };
}
