/**
 * Class: HeroSlider
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';

export default class HeroSlider {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.heroSlider'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$closeBtn = this.$el.find('.heroSlider-close');
    this.$heroContent = this.$el.find('.heroSlider-content');
    this.showSlideAt = this.$el.offset().top;
    this.slideIsVisible = true;
    this.$video = this.$el.find('video');
    this.videoLoaded = false;
    this.headerSlider = $('.headerSlider').length;
    var headerSliderSpacerHeight = utils.$body
      .find('.headerSlider-spacer')
      .height();
    var headerClassicSpacerHeight = utils.$body
      .find('.header.headerClassic')
      .height();
    this.headerSpacerHeight = 0;
    this.offset = 5;
    if ($.isNumeric(headerSliderSpacerHeight)) {
      this.headerSpacerHeight = headerSliderSpacerHeight;
    } else if ($.isNumeric(headerClassicSpacerHeight)) {
      this.headerSpacerHeight = headerClassicSpacerHeight;
    }
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    var self = this;
    this.bindEvents();

    // hide content when scrolled down over a certain point on the page;
    emitter.on('scroll', function(obj) {
      //if (responsive.getSize() !== 'small') {
      //    var cutoff = utils.$win.scrollTop();
      //    if(cutoff >= 40) {
      //        cutoff = cutoff + 40;
      //    }
      //    cutoff = cutoff + self.headerSpacerHeight;

      //    if (self.showSlideAt < cutoff) {
      //        self.hideSlide();
      //    } else if (self.showSlideAt >= cutoff){
      //        self.showSlide();
      //    }
      //}
      var offset = self.offset;
      if (utils.$win.height() < self.$el.height()) {
        offset = self.$el.height() - utils.$win.height() + 100;
      }
      if (responsive.getSize() !== 'small') {
        if (obj.top > self.showSlideAt + offset && self.slideIsVisible) {
          self.hideSlide();
        } else if (
          obj.top < self.showSlideAt + offset &&
          !self.slideIsVisible
        ) {
          self.showSlide();
        }
      }
    });

    emitter.on('resize', function(/* obj */) {
      self.setShowSlideAt();
      utils.$win.scroll();
    });

    setTimeout(function() {
      self.showSlide();
    }, 1500);

    if (this.$video.length) {
      // this is some weird quirk on safari that makes the video element too high. Therefore we force a repaint.
      this.$video.on('timeupdate', function() {
        if (!self.videoLoaded) {
          self.videoLoaded = true;
          self.$video.css('display', 'inline');
          self.$video.css('display', 'block');
        }
      });
    }
  }

  setShowSlideAt() {
    this.showSlideAt = this.$el.offset().top;
  }

  bindEvents() {
    var self = this;
    this.$closeBtn.on({
      click: function(/* e */) {
        self.$el.addClass('heroSlider-content-hidden');
      }
    });
  }

  showSlide() {
    this.$el.removeClass('heroSlider-content-hidden');
    this.slideIsVisible = true;
  }

  hideSlide() {
    if (!this.$el.hasClass('slider-always-visible')) {
      this.$el.addClass('heroSlider-content-hidden');
      this.slideIsVisible = false;
    }
  }
}
