/**
 * Class: Tabs
 * Description: Her goes description
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import Video from '../video/video';

export default class Tabs {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.tabs'
      },
      config
    );
    this.$el = $(this.config.el);
    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var elements = this.$el;

    if (!elements.length) return false;

    var self = this;
    emitter.on('resize', function(/* obj */) {
      if (!$('body').hasClass('is-experience-editing')) {
        elements.each(function() {
          var $tabRow = $(this).find('.tabs-tabs'),
            $firstTab = $tabRow.find('.tabs-tabs-tab:eq(0)'),
            $dropDownMenu = $(this).find('.tabs-tabs-more-dropdown'),
            $dropDownMenuTrigger = $(this).find('.tabs-tabs-more');

          // first move them all back
          $dropDownMenu.children().each(function() {
            $(this).insertBefore($dropDownMenu.parent());
          });

          var count = $tabRow.children().length,
            iterations = 0;

          $dropDownMenuTrigger.css('display', 'inline-block');

          // RT: added a bit extra buffer (-5) to please firefox:
          while (
            $tabRow.outerHeight() - 5 > $firstTab.outerHeight() &&
            count > iterations++
          ) {
            $tabRow
              .find('> .tabs-tabs-tab')
              .last()
              .prependTo($dropDownMenu);
          }

          // Only show the 'dropdown' button if there are any dropdown elements
          if ($dropDownMenu.children().length === 0) {
            $dropDownMenuTrigger.hide();
          }
        });
      }
    });

    // This is used because the col-in-viewport has a transform property put on because of animate-in transition, that causes
    // the dropdown to be hidden behind the preceeding row.
    // therefor we remove the .col's transition and transform first time its clicked.
    // probably my finest and proudest work -- Rasmus Elken.
    this.$el.find('.tabs-tabs-more').one({
      click: function(/* e */) {
        if (
          !self.$el
            .parents('.col-in-viewport-inited')
            .hasClass('col-in-viewport-notransform')
        ) {
          self.$el
            .parents('.col-in-viewport-inited')
            .addClass('col-in-viewport-notransform');
        }
      }
    });

    this.$el.on('click', '.tabs-tabs-tab', function(event) {
      event.preventDefault();
      var $tab = $(this),
        $tabsRoot = $tab.parents('.tabs'),
        $allTabs = $tabsRoot.find('.tabs-tabs-tab'),
        $allContentTabs = $tabsRoot.find('.tabs-content-tab'),
        $moreLink = $tabsRoot.find('.tabs-tabs-more');

      if (!$tab.hasClass('is-open')) {
        $allTabs.removeClass('is-open');
        $moreLink.removeClass('is-open');

        $tab.addClass('is-open');
        $allContentTabs.removeClass('is-open');
        $allContentTabs.eq($tab.data('tabs-index')).addClass('is-open');

        Video.resizeParent($allContentTabs.eq($tab.data('tabs-index')));
        if ($tab.parents('.tabs-tabs-more').length > 0) {
          $moreLink.addClass('is-open');
        }
      }
    });
  }
}
