import { TYPES } from './dynamic-loading-options';

import announcement from "../../modules/announcement/announcement";
import calendar from "../../modules/calendar/calendar";
import searchResults from "../../modules/searchResults/searchResults";
import rssFeedSignUp from "../../modules/rssFeed/rssFeedSignUp";
import rssFeedSignOff from "../../modules/rssFeed/rssFeedSignOff";
import jobList from "../../modules/job/jobList";
import jobArticle from "../../modules/job/jobArticle";
import google from "../../modules/activitymap/google.maps.2016";
import formSelector from "../../modules/formSelector/formSelector";
import toaster from "../../modules/toaster/toaster";
import tabs from "../../modules/tabs/tabs";

export default [
  {
    name: 'Announcement',
    importer: () => import(/* webpackChunkName: "annoucement" */ '../../modules/announcement/announcement'),
    type: TYPES.SINGLE,
    selector: '.announcement-module'
  },
  {
    name: 'Calendar',
    importer: () => import(/* webpackChunkName: "calendar" */ '../../modules/calendar/calendar'),
    type: TYPES.MULTIPLE,
    selector: '.calendar'
  },
  {
    name: 'SearchResult',
    importer: () => import(/* webpackChunkName: "search-result" */ '../../modules/searchResults/searchResults'),
    type: TYPES.SINGLE,
    selector: '.search-result-module'
  },
  {
    name: 'RssFeedSignUp',
    importer: () => import(/* webpackChunkName: "rss-sign-up" */ '../../modules/rssFeed/rssFeedSignUp'),
    type: TYPES.SINGLE,
    selector: '.rsssignup'
  },
  {
    name: 'RssFeedSignOff',
    importer: () => import(/* webpackChunkName: "rss-sign-off" */ '../../modules/rssFeed/rssFeedSignOff'),
    type: TYPES.SINGLE,
    selector: '.rsssignoff'
  },
  {
    name: 'JobList',
    importer: () => import(/* webpackChunkName: "job-list" */ '../../modules/job/jobList'),
    type: TYPES.SINGLE,
    selector: '.job-module'
  },
  {
    name: 'JobArticle',
    importer: () => import(/* webpackChunkName: "job-article" */ '../../modules/job/jobArticle'),
    type: TYPES.SINGLE,
    selector: '.job__article'
  },
  {
    name: 'ActivityMap',
    importer: () => import(/* webpackChunkName: "activity-map" */ '../../modules/activitymap/google.maps.2016'),
    type: TYPES.NONE,
    selector: '.mapw'
  },
  {
    name: 'FormSelector',
    importer: () => import(/* webpackChunkName: "form-selector" */ '../../modules/formSelector/formSelector'),
    type: TYPES.SINGLE,
    selector: '.form-selector'
  },
  {
    name: 'Toaster',
    importer: () => import(/* webpackChunkName: "toaster" */ '../../modules/toaster/toaster'),
    type: TYPES.SINGLE,
    selector: '.modal'
  },
  {
    name: 'Tabs',
    importer: () => import(/* webpackChunkName: "tabs" */ '../../modules/tabs/tabs'),
    type: TYPES.SINGLE,
    selector: '.tabs'
  }
];
