import $ from 'jquery';

const className = {
  onFocus: 'focused'
}

export default class CustomSelect {
  constructor(config) {
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    this.init();
  }

  init() {
    this.bindEvents();
  }
  
  bindEvents() {
    this.$el.on('focus', '> *', () => {
      this.$el.addClass(className.onFocus);
    });

    this.$el.on('blur', '> *', () => {
      this.$el.removeClass(className.onFocus);
    });
  }
}
