if (window.app !== undefined) {
    window.app.directive("flowStepComponent", function () {
        return {
            restrict: 'A',
            require: ['^flow'],
            scope: true,
            template: '<div ng-show="state.currentstep.name === step.name" ng-transclude></button>',
            replace: true,
            transclude: true,
            link: function (scope, element, attrs, ctrls) {
                var flow = ctrls[0];

                scope.state = flow.state;

                scope.step = {
                    name: attrs.flowStepComponent
                };
            }
        }
    });
}