/**
 * Class: Table
 * Description: Her goes description
 */
import $ from 'jquery';
import * as utils from '../base/utils/utils';

export default class Table {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    this.config = $.extend(
      {
        el: '.richtext'
      },
      config
    );
    this.$el = $(this.config.el);
    this.init();
  }
  init() {
    this.bindEvents();
    if (!utils.$body.hasClass('is-experience-editing')) {
      this.fixRichtextTableHeader();
      this.addMobileHeader();
    }
  }

  fixRichtextTableHeader() {
    this.$el.find('table').each(function() {
      var thead = $(this).find('th');
      $(this)
        .find('tbody')
        .find('tr')
        .each(function() {
          $(this)
            .find('td')
            .each(function(tdIndex) {
              if (
                typeof thead !== 'undefined' &&
                typeof thead[tdIndex] !== 'undefined'
              ) {
                $(this).attr('data-label', $(thead[tdIndex]).text());
              } else {
                $(this).attr('data-label', '');
              }
            });
        });
    });
  }

  addMobileHeader(){
    this.$el.find("table").each(function() {
        let mobileHeaderText = $(this).attr("summary");
        mobileHeaderText = mobileHeaderText ? mobileHeaderText.trim() : ""; // Cast falsible types to an empty string.
        if (mobileHeaderText !== "") {
          $(this).before(`<div class='table-mobile-head'>${mobileHeaderText}</div>`);
        }
    });
  }

  bindEvents() {
    // bind your events here.

    // Linking TR Rows with data-href attributes
    $('tr[data-href]').on('click', function() {
      document.location = $(this).data('href');
    });
  }
}
