import SelectDropdown from "../selectDropdown/selectDropdown";

export default class MultiSelectDropdown extends SelectDropdown {
  constructor(config) {
    super(config);

    this.selectedOptionsMenu = null;

    this.keepOpen = true;
    this.isMultiSelect = true;
  }

  handleMobileSelection() {
    this.renderDisplay();
    this.buildSelectedOptions();
  }

  handleSelectItem(e) {
    if (e instanceof MouseEvent) {
      this.selectItem(e.target);
      this.buildSelectedOptions();
    } else {
      if (!this.focusedItem) {
        return;
      }

      const parent = this.focusedItem.element.parentElement;

      this.selectItem(this.focusedItem.element);

      this.buildSelectedOptions();
  
      if (parent.classList.contains("dropdown-selected-options")) {
        if (parent.children.length >= 1) {
          this.focusItem(parent.children[0]);
        } else {
          this.focusItem(parent.nextElementSibling.querySelector("li"));
        }
      }
    }
    
    e.preventDefault();
    e.stopPropagation();
  }

  toggleSelectedItem(item) {
    const value = item.dataset["value"];
    const selected = item.classList.contains("selected");
    let selectedIndex = -1;

    for (let i = 0; i < this.selectElement.options.length; i++) {
      if (this.selectElement.options[i].value === value) {
        selectedIndex = i;
        break;
      }
    }

    const menuItems = this.dropdownMenu.querySelectorAll(
      `.dropdown-item[data-value="${value}"]`
    );

    selected
      ? item.removeAttribute("aria-selected")
      : item.setAttribute("aria-selected", "true");
    item.classList.toggle("selected");

    for (let i = 0; i < menuItems.length; i++) {
      if (selected) {
        menuItems[i].removeAttribute("aria-selected");
        menuItems[i].classList.remove("selected");
      }
    }

    this.selectElement.options[selectedIndex].selected = !selected;

    return selectedIndex;
  }

  buildSelectedOptions() {
    const fragment = document.createDocumentFragment();

    this.selectedValues.length = 0;

    for (let i = 0; i < this.selectElement.children.length; i++) {
      const option = this.selectElement.children[i];

      if (!option.selected) {
        continue;
      }

      const li = document.createElement("li");
      li.dataset["value"] = option.value;
      li.textContent = option.textContent;
      li.className = "dropdown-item selected";
      li.setAttribute("aria-selected", "true");

      this.selectedValues.push(option.value);

      fragment.appendChild(li);
    }

    this.selectedOptionsMenu.innerHTML = "";
    this.selectedOptionsMenu.appendChild(fragment);
  }

  focusNextItem() {
    const newFocusedItem = super.focusNextItem();
    const item = this.focusedItem;

    if (!newFocusedItem && item && item.element.parentElement.classList.contains("dropdown-selected-options")) {
      const selectedFirstChild = item.element.parentElement.nextElementSibling.querySelector(
        "li"
      );

      if (selectedFirstChild) {
        this.focusItem(selectedFirstChild, 0);
      }
    }
  }

  focusPreviousItem() {
    const newFocusedItem = super.focusPreviousItem();
    const item = this.focusedItem;

    if (!newFocusedItem && item && item.element.parentElement.classList.contains("dropdown-options")) {
      const selectedLastChild = item && item.element.parentElement.previousElementSibling.querySelector(
        "li:last-child"
      );

      if (selectedLastChild) {
        this.focusItem(selectedLastChild, 0);
      }
    }
  }

  initOptions() {
    super.initOptions();

    const selectedFragment = document.createDocumentFragment();
    const selectedOptions = this.optionsMenu.querySelectorAll('.selected');

    for (let i = 0; i < selectedOptions.length; i++) {
      selectedFragment.appendChild(selectedOptions[i].cloneNode(true));
    }

    this.selectedOptionsMenu = this.dropdownMenu.querySelector(
      ".dropdown-selected-options"
    );

    this.selectedOptionsMenu.innerHTML = "";
    this.selectedOptionsMenu.appendChild(selectedFragment);

    this.buildSelectedOptions();
  }

  selectItemByData(e, data) {
    super.selectItemByData(e, data);
    
    this.buildSelectedOptions();
  }
}
