/**
 * Class: Link
 * Description: Her goes description
 */
import $ from 'jquery';

export default class Link {
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    $('a[href*=\\#]:not([href=\\#])').click(function() {
      if (
        location.pathname.replace(/^\//, '') ==
          this.pathname.replace(/^\//, '') ||
        location.hostname == this.hostname
      ) {
        var target = $(this.hash);
        let headerHeight =
          $('.headerClassic-bottom').height() +
          $('.headerClassic-top').height();
        if (!headerHeight) {
          headerHeight =
            $('.headerSlider').height() + $('.headerSlider-spacer').height();
        }
        target = target.length
          ? target
          : $('[name=' + this.hash.slice(1) + ']');

        target = target.first().parent();

        headerHeight += 20;

        let scrollTo = target.offset().top;
        if (headerHeight) {
          scrollTo -= headerHeight;
        }

        if (target.length) {
          $('html,body').animate(
            {
              scrollTop: scrollTo
            },
            400
          );
          return false;
        }
      }
    });

    $('.skip').click(function(/* event */) {
      var skipTo = '#' + this.href.split('#')[1];

      $(skipTo)
        .attr('tabindex', -1)
        .on('blur focusout', function() {
          $(this).removeAttr('tabindex');
        })
        .focus();
    });
  }
}
