/**
 * Class: JobSearchHero
 */
import $ from "jquery";

export default class JobSearchHero {
  constructor(config) {
    this.config = $.extend(
      {
        $el: ".job-search-hero"
      },
      config
    );

    this.$multiselect = this.config.$el.find(".dropdown__select");
    this.$submitButton = this.config.$el.find(".job-search-hero__btn");
    this.preselected = this.config.$el
      .find(".multiSelectDropdown")
      .data("preselected");

    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    this.$submitButton.on("click", () => {
      this.updateUrl();
    });
  }

  updateUrl() {
    let hash = this.preselected ? this.preselected : "";
    this.$multiselect.each(function() {
      let $selected = $(this).find("option:selected");
      let selectedIds = [];

      if ($selected.length > 0) {
        hash += $(this).data("selectorId") + "=";
        $selected.each((index, option) => {
          selectedIds.push(option.dataset["id"]);
        });

        hash += selectedIds.join(",") + "&";
      }
    });

    let href = this.$submitButton.attr("href");

    if (href) {
      href += (href.match(/\?/) ? "&" : "#") + hash;
      this.$submitButton.attr("href", href);
    }
  }
}
