if (window.app !== undefined) {
    /**
 * Class: Product directive.
 */
    window.app.directive('productselection',
    [
        'FlowService', function(FlowService) {
            return{
                restrict: 'A',
                require: ['^form'],
                scope: true,

                link: function($scope, element, attrs, ctrls) {
                    $scope.productForm = FlowService.model;

                    var form = ctrls[0];
                    if (isNaN(form.SelectedProducts.$modelValue)) {
                        form.SelectedProducts.$setValidity("required", false);
                    }
                }
            }
        }
    ]);
}