if (window.angular !== undefined) {
    /**
     * Class: Product directive.
     */
    window.app.directive('product', ['$location', '$sce', 'productService', 'FlowService', 'priceService', function ($location, $sce, productService, FlowService, priceService) {

        var product = {};
        product.restrict = 'AE';
        product.scope = true;

        product.controller = ['$scope', function ($scope) {

            $scope.productAdded = false;
            $scope.productForm = FlowService.model;


            $scope.categoryParams = productService.model.prod;
            $scope.fixed = parseInt(productService.model.fixed);
            
            // price calculation 
            $scope.showCalculation = function (price) {
                if ($scope.showCalculatedPrice()) {
                    if (price && price.Unit && price.Unit.UnitUrlParam) {
                        return $sce.trustAsHtml($scope.InsertConsumption(price.Unit.Calculation).replace('[price]', $scope.comma(priceService.getUnitPrice(price))));
                    }
                }
                return '';
            }

            $scope.comma = function (inputInt) {
                return inputInt.toString().replace(".", ",");
            }
            
            $scope.showPrice = function (price, isCalculate) {
                return priceService.showPrice(price, isCalculate);
            }

            $scope.TotalPrice = function () {
                if ($scope.fallbackprice) {
                    return priceService.getUnitPrice($scope.fallbackprice).toString();
                }

                var sum = 0;
                angular.forEach($scope.pricearray, function (price) {
                    sum = parseFloat($scope.showPrice(price, true)) + sum;
                });
                if (Math.round(sum) !== sum) {
                    return parseFloat(sum).toFixed(2).toString();
                }
                else{
                    return Math.round(sum).toString();
                }
            }

            $scope.showCalculatedPrice = function () {
                return !$scope.fallbackprice;
            }

            $scope.priceIsCalculated = function (price) {
                if (!price || !price.Unit || !price.Unit.UnitUrlParam) return false;
                return productService.getUnitParam(price.Unit.UnitUrlParam) !== false;
            }

            $scope.showTotalUnit = function (calculatedTotalUnit) {
                if ($scope.showCalculatedPrice) {
                    return calculatedTotalUnit;
                }
                return $scope.fallbackprice.Unit.Unit;
            }

            $scope.InsertConsumption = function (text) {
                if (!text) return "";
                return text.replace('[gas]', $scope.comma(productService.model.gas)).replace('[kwh]', $scope.comma(productService.model.kwh));
            }

            $scope.recommendProduct = function (productCategory, fixedprice) {
                if (($scope.fixed && fixedprice) || (!$scope.fixed && !fixedprice)) {
                    return $scope.categoryParams && $scope.categoryParams.indexOf(productCategory) != -1;
                }
                return false;
            }

            $scope.showBasedOnCategoriProduct = function (productCategory) {
                if (!$scope.categoryParams || $scope.categoryParams == '') return true;
                return $scope.categoryParams && $scope.categoryParams.indexOf(productCategory) != -1;
            }

            $scope.updateProduct = function()
            {
                if ($scope.productExistsInProductString($scope.productid)){
                    if (!$scope.isProductAdded($scope.productid)) {
                        if (!productService.model.products) productService.model.products = [];
                        productService.model.products.push($scope.productid);
                        $scope.productAdded = true;
                    }
                }
            }

            $scope.productExistsInProductString = function(productId) {
                var exists = false;
                var chosenString = FlowService.model.SelectedProducts;
                if (chosenString) {
                    var chosenProducts = chosenString.split(",");
                    angular.forEach(chosenProducts, function (curProduct) {
                        if (curProduct == productId) {
                            exists = true;
                        }
                    });
                }
                return exists;
            }

            $scope.addProduct = function () {
                if (!productService.model.products) productService.model.products = [];
                productService.model.products.push($scope.productid);
                $scope.generateProductString();
                $scope.productAdded = true;
            }

            $scope.removeProduct = function () {
                var index = productService.model.products.indexOf($scope.productid);
                if (index != -1) {
                    productService.model.products.splice(index, 1);
                }
                $scope.generateProductString();
                $scope.productAdded = false;
            }

            $scope.generateProductString = function () {
                var tmpProductSelection = "";
                angular.forEach(productService.model.products, function (curProduct) {
                    if (tmpProductSelection != "") tmpProductSelection += ",";
                    tmpProductSelection += curProduct;
                });
                FlowService.model.SelectedProducts = tmpProductSelection;
            }

            $scope.isProductAdded = function(productId) {
                var productChosen = false;
                angular.forEach(productService.model.products, function (curProduct) {
                    if (curProduct == productId) productChosen = true;
                });
                return productChosen;
            }
        }];

        product.link = function ($scope, $element, $attrs) {
            $scope.pricearray = angular.fromJson($attrs.productpricearray);
            $scope.sumpricearray = angular.fromJson($attrs.sumproductpricearray);
            $scope.producttextcalc = $scope.InsertConsumption(angular.fromJson($attrs.producttextcalc));
            $scope.producttextwithout = $scope.InsertConsumption(angular.fromJson($attrs.producttextwithout));
            $scope.productForm = productService.model;
            $scope.fallbackprice = productService.getFallBackPriceWithoutConsumption($scope.pricearray);
            $scope.productid = $attrs.productid;
            priceService.addProductprice($scope.productid, $scope.pricearray);
            $scope.updateProduct();
        }; 

        return product;
    }]);
}