import { emitter } from "../base/emitter/emitter";

export default class PageLanguageSwitcher {
  constructor(config) {
    this.config = Object.assign({}, config);
    this.element = this.config.el.get(0);
    this.parentElement = this.element.parentElement;
    this.dropdownElement = this.element.querySelector(
      ".selectDropdown.dropdown"
    );
    this.parentElementChildIndex = [].indexOf.call(
      this.parentElement.children,
      this.element
    );
    this.dropdownMenu = this.element.querySelector(
      ".pageLanguageSwitcher-dropdown-menu"
    );
    this.selectElement = this.element.querySelector("select");
    this.dropdownOptions = this.dropdownMenu.querySelector(".dropdown-options");
    this.displayText = this.element.querySelector(".dropdown-display-text");
    this.floatingButtonClass = "pageLanguageSwitcher--floating";
    this.floatingButtonHiddenClass = "pageLanguageSwitcher--hidden";
    this.floatingButtonShowClass = "pageLanguageSwitcher--show";
    this.isFloating = false;
    this.storageKey = "language_switch_scroll_position";
    this.footerElement = document.querySelector("footer");
    this.visible = true;

    this.bindEvents();
    this.setupElements();
  }

  setupElements() {
    // to make the dropdown stay on top of component below
    $(this.element)
      .closest("section")
      .get(0).style.zIndex = 1;

    const scrollPosition = localStorage.getItem(this.storageKey);

    if (scrollPosition) {
      window.scrollTo(0, +scrollPosition);
      localStorage.removeItem(this.storageKey);
    }
  }

  renderDesktopButton() {
    if (this.isFloating) {
      document.body.removeChild(this.element);
      this.parentElement.insertBefore(
        this.element,
        this.parentElement.children[this.parentElementChildIndex]
      );
    }

    this.element.classList.remove(this.floatingButtonClass);
    this.dropdownMenu.classList.remove(this.floatingButtonClass);
    this.isFloating = false;
    this.visible = true;
    this.dropdownElement.direction = "bottom";
    this.element.style.display = "block";
    this.renderLongText();
  }

  renderMobileFloatingButton() {
    if (!this.isFloating) {
      this.parentElement.removeChild(this.element);
      this.element.classList.add(this.floatingButtonClass);
      this.dropdownMenu.classList.add(this.floatingButtonClass);
      document.body.appendChild(this.element);
      this.dropdownElement.direction = "top";

      this.isFloating = true;
      this.renderShortText();
    }
  }

  updateDisplayText() {
    const selectedItem = this.dropdownOptions.querySelector(
      ".dropdown-item.selected"
    );

    if (selectedItem) {
      this.displayText.textContent = selectedItem.textContent;
    }
  }

  renderShortText() {
    const items = this.dropdownOptions.querySelectorAll(".dropdown-item");

    for (let i = 0; i < items.length; i++) {
      if (items[i].dataset.shortDisplayText) {
        const shortText = items[i].dataset.shortDisplayText;
        this.selectElement.children[i].textContent = shortText;
        items[i].childNodes[items[i].childNodes.length - 1].textContent = shortText;
      }
    }

    this.updateDisplayText();
  }

  renderLongText() {
    const items = this.dropdownOptions.querySelectorAll(".dropdown-item");

    for (let i = 0; i < items.length; i++) {
      if (items[i].dataset.displayText) {
        const displayText = items[i].dataset.displayText;
        this.selectElement.children[i].textContent = displayText;

        items[i].childNodes[items[i].childNodes.length - 1].textContent = displayText;
      }
    }

    this.updateDisplayText();
  }

  languageChanged(e, data) {
    localStorage.setItem(this.storageKey, window.scrollY);
    window.location = data.selectedValues[0];
  }

  toggleFloatingButtonVisible() {
    if (!this.isFloating) {
      return;
    }

    const scrollPosition = window.scrollY;
    const offset = document.body.offsetHeight - this.footerElement.offsetHeight;

    if (window.innerHeight + scrollPosition >= offset) {
      this.visible = false;
      this.element.classList.add(this.floatingButtonHiddenClass);
    } else {
      if (!this.visible) {
        this.visible = true;
        this.element.classList.remove(this.floatingButtonHiddenClass);
        this.element.style.display = "block";
        this.element.classList.add(this.floatingButtonShowClass);
      }
    }
  }

  dropdownLoaded() {
    if (this.isFloating) {
      this.renderShortText();
    } else {
      this.renderLongText();
    }
  }

  animationEnded(e) {
    if (!this.visible) {
      this.element.style.display = "none";
      this.element.classList.remove(this.floatingButtonHiddenClass);
    } else {
      this.element.classList.remove(this.floatingButtonShowClass);
    }
  }

  bindEvents() {
    emitter.on("responsive/sizechange", size => {
      if (size === "small") {
        this.renderMobileFloatingButton();
      } else {
        this.renderDesktopButton();
      }
    });

    window.addEventListener(
      "scroll",
      this.toggleFloatingButtonVisible.bind(this)
    );

    this.element.addEventListener(
      "animationend",
      this.animationEnded.bind(this)
    );

    $(this.dropdownElement).on("change", this.languageChanged.bind(this));

    $(this.dropdownElement).on("load", this.dropdownLoaded.bind(this));
  }
}
