/**
 * Class: form
 * Description: Her goes description
 * NOTE about sitecore-forms: sitecore forms only has single-file selectors and they use a different datepicker plugin than the GW prototype.
 */
import $ from 'jquery';
import * as utils from '../base/utils/utils';

export default class Form {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    this.async = this.config.async;
    this.isSitecoreForm =
      this.$el.attr('data-wffm-ajax') !== undefined ||
        this.$el.attr('data-wffm') !== undefined
        ? true
        : false;
    this.isSitecoreExperienceForm =
      this.$el.attr('action') &&
        this.$el.attr('action').indexOf('/formbuilder') === 0
        ? true
        : false;
    this.$inputs = this.$el.find(
      'input[type="text"], input[type="number"], input[type="password"], input[type="email"], input[type="search"], textarea, input[type="tel"]'
    );
    this.$inlineInputs = this.$el.find(
      '.radio, .checkbox, select, input[type="file"], .g-recaptcha'
    );
    this.$textAreas = this.$el.find('textarea');
    this.inputBinds =
      'input[type="text"], input[type="number"], input[type="password"], input[type="email"], input[type="search"], textarea, input[type="tel"]';
    this.$formAttachmentsFiles = this.$el.find('.form-attachment-file');
    this.formAttachmentsTemplate = this.$formAttachmentsFiles
      .first()
      .parent()
      .html();
    if (utils.$win.width() <= 768) {
      $('.input-group').css('margin-bottom', '10px');
    }
    if (!this.$el.hasClass('sitecore-form-initialized')) {
      this.init();
    }
  }

  init() {
    // Add Danish to the datepicker

    if (this.isSitecoreForm) {
      this.setInlineFields();
      this.setHelpTexts();
      this.setTextareaMaxLengthLabels();
      this.fixFileFields();

      this.$el.addClass('sitecore-form-initialized');
    }

    if (this.isSitecoreExperienceForm) {
      this.$el.addClass('sitecore-form-initialized');
    }

    this.bindEvents();
    this.setActiveLabels();

    //this script is added to enable the validation for checkboxes
    if (typeof $scw === 'undefined') {
      window.$scw = $;
    }
    window.$scw.validator.unobtrusive.adapters.addBool('ischecked', 'required');
  }

  bindEvents() {
    //Spaces for Date field in mobile view
    utils.$win.resize(function () {
      if ($(this).width() <= 768) {
        $('.input-group').css('margin-bottom', '10px');
      }
    });

    utils.$win.on('orientationchange', function (e) {
      var displayUIdatepicker = $('.ui-datepicker').css('display');
      if (displayUIdatepicker == 'block') {
        $('.ui-datepicker').css('display', 'none');
        e.stopImmediatePropagation();
      }
    });

    // Reapplying styles after ajax error
    (function (open) {
      XMLHttpRequest.prototype.open = function () {
        this.addEventListener(
          'readystatechange',
          function () {
            if (this.readyState === 4) {
              $('form', $('#main')).each(function () {
                var isSitecoreForm =
                  $(this).attr('data-wffm-ajax') !== undefined ||
                    $(this).attr('data-wffm') !== undefined
                    ? true
                    : false;
                //var isSsForm = $(this).attr("ssForm") !== undefined ? true : false;
                var isExperienceForm = $(this) && $(this)[0].action.indexOf("/formbuilder") >= 0;

                if (isExperienceForm) {
                  var formId = $(this)[0].id;

                  setTimeout(function () {
                    var $form = $("#" + formId);

                    new Form({
                      el: $form
                    });
                  }, 5);
                }
                else if (isSitecoreForm) {
                  // eslint-disable-next-line no-unused-vars
                  var form = new Form({
                    el: this
                  });
                }
              });
            }
          },
          false
        );
        open.apply(this, arguments);
      };
    })(XMLHttpRequest.prototype.open);

    // Focus labels
    this.$el.on(
      {
        focus: function () {
          var $focusElement = $(this);
          if ($focusElement.hasClass('tt-input')) {
            $focusElement
              .parent()
              .siblings('label.show-as-placeholder')
              .addClass('is-active');
          } else {
            $focusElement
              .siblings('label.show-as-placeholder')
              .addClass('is-active');
            $focusElement.siblings('label.control-label').addClass('is-active');

            // To handle show password place
            if ($('label[for=Password]').hasClass('is-active')) {
              console.log(
                $focusElement.parent('.form-group').hasClass('has-success')
              );
              if (
                $focusElement.parent('.form-group').hasClass('has-success') ||
                $focusElement.parent('.form-group').hasClass('has-danger')
              ) {
                $('.show-password').css('right', '65px');
              } else {
                $('.show-password').css('right', '40px');
              }
            }
          }
        },
        blur: function () {
          var $focusOutElement = $(this);

          if ($focusOutElement.val() == '') {
            $focusOutElement.siblings('label').removeClass('is-active');

            if ($focusOutElement.hasClass('datepicker')) {
              $focusOutElement.siblings('label').addClass('is-active');
            }
            // To handle show password place
            if (
              $focusOutElement.parent('.form-group').hasClass('has-success') ||
              $focusOutElement.parent('.form-group').hasClass('has-danger')
            ) {
              $('.show-password').css('right', '65px');
            } else {
              $('.show-password').css('right', '40px');
            }
          }
        },
        change: function () {
          // special case when user choose rememebered account and browser paste password
          var $focusOutElement = $(this);
          if ($focusOutElement.val() == '') {
            $focusOutElement.siblings('label').removeClass('is-active');
            if ($focusOutElement.hasClass('datepicker')) {
              $focusOutElement.siblings('label').addClass('is-active');
            }
          } else {
            $focusOutElement.siblings('label').addClass('is-active');
          }
        }
      },
      this.inputBinds
    );

    // Textarea maxLength indicator
    this.$el.on('keyup', 'textarea', function (/* event */) {
      var $textarea = $(this),
        maxLength =
          $textarea.attr('maxlength') || $textarea.attr('data-val-length-max'),
        $maxLengthIndicator = $textarea.siblings('.form-maxlength-indicator');
      if ($maxLengthIndicator.length > 0) {
        $maxLengthIndicator.text($textarea.val().length + ' of ' + maxLength);
      }
    });

    this.$el
      .find('.form-control-datepicker')
      .on('changeDate', function (e) {
        e.currentTarget.dispatchEvent(
          new CustomEvent('input', { bubbles: true })
        );
        e.currentTarget.dispatchEvent(
          new CustomEvent('change', { bubbles: true })
        );
      });

    this.$el.each(function (iter, el) {
      var $focusOutElement = $(el);
      if ($focusOutElement.val() !== '') {
        $focusOutElement.siblings('label').addClass('is-active');
      }
      if ($focusOutElement.hasClass('datepicker')) {
        $focusOutElement.siblings('label').addClass('is-active');
      }
    });

    var self = this;

    this.addAttachmentBinding();
    this.$el.on('submit', function () {
      //prevents multiple submit with double-click. The button will be enabled automatically because WFFM resets the form after each request
      if (self.async) {
        // If async form, the form is itself responsible for enabling/disabling spinner, as it should be turned off through a promise.
        return;
      }

      if (typeof $scw === 'undefined') {
        window.$scw = $;
      }

      var scwResult = window.$scw(this);
      if (
        scwResult.length > 0 &&
        scwResult.validate !== undefined &&
        (scwResult.is('form') || scwResult[0].form) &&
        scwResult.valid()
      ) {
        var button = $(this).find('input[type="submit"]');
        button.attr('disabled', 'disabled');
        if (button.parent().find('.formLoaderContainer').length === 0) {
          button
            .parent()
            .append(
              '<div class="formLoaderContainer"><div style="display:inline-block"><div class="formLoader"></div></div></div>'
            );
          button.css('cssText', 'padding-left: 45px !important');
          button.css('-webkit-transition', 'none');
          button.css('transition', 'none');
        }
      }
    });
  }

  //adds a lable to textarea's to contain max-length
  setTextareaMaxLengthLabels() {
    this.$textAreas.each(function (index, el) {
      var $this = $(el);
      if ($this.is('[data-val-length-max]')) {
        $this.after(
          "<span class='form-maxlength-indicator'>0 of " +
          $this.attr('data-val-length-max') +
          '</span>'
        );
      }
    });
  }

  // add classes to form-fields that should be inline
  // also fixes radio and checkboxes
  // also fixes sitecore select-boxes.
  setInlineFields() {
    if (!utils.$body.hasClass('is-experience-editing')) {
      // remove long text from checkboxes in EE

      this.$inlineInputs.each(function (index, el) {
        if ($(el).hasClass('fileupload-with-label')) {
          $(el)
            .parents('.form-group')
            .addClass('is-inline-form-control'); //add overall classe
        } else {
          $(el)
            .parent()
            .addClass('is-inline-form-control'); //add overall classe
        }

        //fix radio and checkboxes, the text now highlights correctly
        $(el)
          .find('input[type="radio"], input[type="checkbox"]')
          .each(function (index, el2) {
            if (!$(el2).hasClass('checkbox-with-html')) {
              var txt = $(el2)
                .parent()
                .text();
              $(el2)
                .parent()
                .contents()
                .filter(function () {
                  return this.nodeType === 3;
                })
                .remove();
              $(el2)
                .parent()
                .append('<span>' + txt + '</span>');
            }
          });

        //fix selects to have maine-styling
        if ($(el).is('select')) {
          $(el)
            .removeClass('form-control')
            .wrap("<div class='form-select customSelect'></div>");

          // fix to remove validation checkmark from selects
          $(el)
            .parent()
            .parent()
            .addClass('disable-validation-checkmark');
        }
      });
    }
  }

  //Turns sitecore help-blocks into our kind of tooltips
  setHelpTexts() {
    this.$el
      .find('.help-block:not(.field-validation-valid)')
      .each(function (index, el) {
        var $this = $(el);
        var label = $this
          .parent()
          .find('.control-label')
          .first(); //need to find the field's label to insert the help-block inside
        $this.parent().addClass('has-inner-label'); //we add this to fix the required field asterisk (it would show under the help-block)
        var helpText = $this.text();
        $this.html('');
        label.html(
          "<span class='inner-label'>" +
          label.text() +
          "</span><a href='javascript:void(null);' role='tooltip' class='form-control-help-bs-tooltip' data-toggle='tooltip' data-placement='bottom' title='" +
          helpText +
          "' data-container='body'><span class='sr-only'>Tooltip for input-field</span><span aria-hidden='true'>?</span></a>"
        );
        $('[data-toggle="tooltip"]', label).tooltip(); //'show'
      });
  }

  makeid() {
    var text = '';
    var possible =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    for (var i = 0; i < 5; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }

  //a fix for sitecore to make file-fields look nicer.
  fixFileFields() {
    this.$el.find('input[type="file"]').each(function (index, el) {
      let id = $(el).attr('id');
      if (!$(el).hasClass('fileupload-with-label')) {
        $(el)
          .wrap('<div class="form-attachment"></div>')
          .wrap('<div class="form-attachment-file"></div>')
          .after(
            '<label class="btn bt-blue" for="' +
            id +
            '">Tilfj fil</label><a href="#" class="form-attachment-remove-file btn btn-round btn-round-small icon icon-close"></a>'
          );
      }
    });
    this.$formAttachmentsFiles = this.$el.find('.form-attachment-file');
  }

  addAttachmentField($appendEl) {
    var $template = $(this.formAttachmentsTemplate),
      uniqueId = this.makeid();
    $template.find('input').attr('id', uniqueId);
    $template.find('label').attr('for', uniqueId);
    $appendEl.append($template);
    this.addAttachmentBinding();
  }

  addAttachmentBinding() {
    this.$formAttachmentsFiles = this.$el
      .find('.form-attachment-file')
      .not('.has-event-binding');
    var currentcontext = this;

    this.$formAttachmentsFiles.each(function (index, el) {
      var context = currentcontext;
      var $this = $(el),
        $input = $this.find('input[type="file"]'),
        // $parent = $input.parent(),
        $label = $this.find('label'),
        labelVal = $label.html(),
        $removeFileBtn = $this.find('.form-attachment-remove-file');

      $this.addClass('has-event-binding'); // set flag that input has eventbinding.
      $input.on({
        change: function (e) {
          var fileName = '';
          if (this.files && this.files.length > 1) {
            fileName = (
              this.getAttribute('data-multiple-caption') || ''
            ).replace('{count}', this.files.length);
          } else if (e.target.value) {
            fileName = e.target.value.split('\\').pop();
          }
          if (fileName) {
            $label.html(fileName);
            $label.removeClass('btn-blue');
            $label.addClass('btn-white');
            $this.addClass('has-file');
            if (!context.isSitecoreForm)
              context.addAttachmentField($this.parent());
          } else {
            $label.html(labelVal);
          }
        }
      });
      $removeFileBtn.on({
        click: function (e) {
          e.preventDefault();

          if (context.isSitecoreForm) {
            $input.val('');
            $this.removeClass('has-file');
            $label.addClass('btn-blue');
            $label.removeClass('btn-white');
            $label.html(labelVal);
          } else {
            $this.remove();
          }
        }
      });

      // Firefox bug fix
      $input.on({
        focus: function () {
          $input.addClass('has-focus');
        },
        blur: function () {
          $input.removeClass('has-focus');
        }
      });
    });
  }

  setActiveLabels() {
    let thiz = this;
    setTimeout(function () {
      thiz.$inputs.each(function () {
        var $focusElement = $(this);
        var hasBeenAutofilled =
          getComputedStyle(this).backgroundColor !== 'rgb(255, 255, 255)';
        if ($focusElement.val() != '' || hasBeenAutofilled) {
          if ($focusElement.hasClass('tt-input')) {
            $focusElement
              .parent()
              .siblings('label.show-as-placeholder')
              .addClass('is-active');
          } else {
            $focusElement
              .siblings('label.show-as-placeholder')
              .addClass('is-active');
            $focusElement.siblings('label.control-label').addClass('is-active');
          }
        }
      }),
        200;
    });
  }

  triggerChange() {
    this.$el.find(this.inputBinds).trigger('change');
  }
}

$(document).ready(function () {
  $('.input-group-addon').css('display', 'none');
  $('.wfmDatebox')
    .prev('label')
    .addClass('is-active');
  $('.input-group').css('padding-right', '8px');
  $('.datepicker').attr('readonly', true);
});
