if (window.app !== undefined) {
    function addDots(n) {
        var rx = /(\d+)(\d{3})/;
        return String(n).replace(/^\d+/, function (w) {
            while (rx.test(w)) {
                w = w.replace(rx, '$1,$2');
            }
            return w;
        });
    }
    window.app.filter('format_comma', function () {
        return function (text) {
            text = addDots(text).toString();
            if (typeof text === 'string') return text.replace(".", ";").replace(",", ".").replace(";", ",");
            return text;
        };
    });
}