/**
 * Class: Interactive Timeline
 */
import { emitter } from '../base/emitter/emitter';
import TweenMax from 'gsap/src/uncompressed/TweenMax';

export default class InteractiveTimeline {
	/**
	 * @param {number} param this is param.
	 * @return {number} this is return.
	 */
  constructor(config) { // put in defaults here
    //defaults
    this.config = $.extend({
      el: '.interactive-timeline',
      contentBaseUrl: '/'
    }, config);
    this.$el = $(this.config.el);
    this.$anchorContainer = this.$el.find('.interactive-timeline-anchor-container');
    this.anchorClass = '.timeline-anchor';
    this.anchorClassActive = 'timeline-anchor-active';
    this.ctaClass = '.link-arrow';
    this.$anchors = this.$el.find(this.anchorClass);
    this.$anchorContentContainer = this.$el.find('.interactive-timeline-anchor-content-container');
    this.anchorContentClass = '.interactive-timeline-anchor-content';
    this.anchorContentClassActive = 'interactive-timeline-anchor-content-active';
    this.$anchorContents = this.$el.find(this.anchorContentClass);
    this.$mobileSelect = this.$el.find('.select-anchors select');
    this.activeId = 1;
    this.animateClass = '';
    this.direction = '';
    this.maxHeight = this.$el.data('maxHeight');
    this.$btnUp = this.$el.find('.timeline-anchor-container-arrow-up');
    this.$btnDown = this.$el.find('.timeline-anchor-container-arrow-down');
    this.$leftContainer = this.$el.find('.timeline-main-left');
    this.$scrollContainer = this.$el.find('.timeline-main-left-inner');
    this.$modalDialog = this.$el.find('.interactive-timeline-modal');
    this.$modalDialogVisible = false;
    this.$modalDialogTitle = this.$modalDialog.find('.timeline-modal-header-title');
    this.$modalDialogClose = this.$modalDialog.find('.timeline-modal-header-close');
    this.$modalDialogContent = this.$modalDialog.find('.timeline-modal-content');
    this.$iframe = this.$modalDialog.find('iframe');
    this.activeIframeUrl = '';
    this.touchstartX = 0;
    this.touchstartY = 0;
    this.touchendX = 0;
    this.touchendY = 0;
    this.defaultImage = $(".teaser-img").first().css('background-image');

		this.loadedContainer = [];
		
		if (this.$el.length){				
			this.externalAnchor = $('a[name]').is(':empty')? $('a[name]'): null;	
      this.init();
    }
  }
  init() {
    this.bindEvents();
    var activeId = 0;

    if (this.externalAnchor) {
      this.externalAnchorIEfix(this.externalAnchor);
    }

    if (window.location.hash && !this.matchAnchorHash()) {
      var hashtag = window.location.hash.substring(1);
      activeId = this.$el.find('[data-tagid=' + hashtag + ']').attr('data-id');
    }
    else {
      activeId = this.$el.find("." + this.anchorContentClassActive).attr('data-id');
    }

    this.$modalDialog.detach();
    $('main').append(this.$modalDialog);

    this.toggleActive(activeId);
  }

  bindEvents() {
    var self = this;

    this.$el.bind('touchstart', function (event) {
        self.touchstartX = event.changedTouches[0].screenX;
        self.touchstartY = event.changedTouches[0].screenY;
      });

    this.$el.bind('touchend', function (event) {
        self.touchendX = event.changedTouches[0].screenX;
        self.touchendY = event.changedTouches[0].screenY;
        self.handleGesture();
    });
    
    window.addEventListener('popstate', self.stateChanged.bind(self));

    window.addEventListener('hashchange', self.hashChanged.bind(self));

    this.$el.on({
      click: function (e) {
        const id = $(this).data('id');
        self.toggleActive(id, true);
        self.updateMobileTimeline();
      }
    }, this.anchorClass);
    this.$btnUp.on({
      click: function (e) {
        self.scrollTimelineUp($(this));
      }
    })
    this.$btnDown.on({
      click: function (e) {
        self.scrollTimelineDown($(this));
      }
    })
    this.$mobileSelect.on({
      change: function (e) {
        self.toggleActive($(this).val(), true);
      }
    })

    this.$el.find(this.ctaClass).on('click', function (e) {
      self.openModal(this.dataset['title'], this.dataset['url']);
    });

    this.$modalDialogClose.on('click', function () {
      window.history.back();
    });

    this.$modalDialog.on('transitionend', self.modalShown.bind(self));

    this.$iframe.on('load', self.frameLoaded.bind(self));

    $(document).ready(function () {
      var trueheight = $(".timeline-main-left.has-overflow").height() - 30;
      var trueheightstring = trueheight + "px";
      if (navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1) {
        $(".timeline-main-left-inner").css({
          "maxHeight": trueheight
        });
        ;
      }
    });
  }
  updateMobileTimeline() {
    if(this.$anchorContainer.width() === 3000){
      const activeElement = this.$el.find("." + this.anchorClassActive);
      let xOffset = -38 * (activeElement.index() === 0) ? 0 : activeElement.index() - 1;

      TweenMax.to(this.$anchorContainer, 0.2, {
        "margin-left": (-38 * xOffset) + 'vw'
      });
    }
  }
  handleGesture() {
    if (this.touchendX < this.touchstartX) {
        const activeElement = this.$el.find("." + this.anchorClassActive);

        const nextID = activeElement.next().data('id');
        if(nextID) {
          this.toggleActive(nextID, true);
          this.updateMobileTimeline();
        }
    }

    if (this.touchendX > this.touchstartX) {
      const activeElement = this.$el.find("." + this.anchorClassActive);

      const previousID = activeElement.prev().data('id');
      if(previousID) {
        this.toggleActive(previousID, true);
        this.updateMobileTimeline();
      }
    }
  }
  externalAnchorIEfix($el) {
    //IE fix
    $el.attr("id", $el.attr("name")).css('display', 'inline-block');
  }
  matchAnchorHash() {
    var match = false;
    if (this.externalAnchor) {
      this.externalAnchor.each(function () {
        if ($(this).attr('name') === window.location.hash.substring(1)) {
          match = true;
        }
      });
    }
    return match;
  }
  disableAllArrows() {
    this.$btnUp.removeClass('timeline-arrow-disabled');
    this.$btnDown.removeClass('timeline-arrow-disabled');
  }
  scrollTimelineUp($arrow) {
    var self = this;
    var scrollTop = this.$scrollContainer.scrollTop() - this.$scrollContainer.height();
    this.$scrollContainer.animate({ scrollTop: scrollTop }, '100', 'swing', function () {
      self.disableAllArrows();
      if (self.$scrollContainer.scrollTop() == 0) {
        $arrow.addClass('timeline-arrow-disabled');
      }
    });
  }
  scrollTimelineDown($arrow) {
    var self = this;
    var scrollTop = this.$scrollContainer.scrollTop() + this.$scrollContainer.height() - 60;
    this.$scrollContainer.animate({ scrollTop: scrollTop }, '100', 'swing', function () {
      self.disableAllArrows();
      if (self.$scrollContainer.scrollTop() >= self.$anchorContainer.outerHeight() - self.$scrollContainer.outerHeight() - 60) {
        $arrow.addClass('timeline-arrow-disabled');
      }
    });
  }
  toggleActive(id, changed) {
    var self = this;
    this.$mobileSelect.val(id);
    this.resetActive(id);
    this.setActive(id, changed);
    self.lastId = id;

    this.updateMobileTimeline();
  }
  setActive(id, changed) {
    const activeElements = this.getElementsById(id);
    activeElements.$anchor.addClass(this.anchorClassActive);

    activeElements.$content.addClass('interactive-timeline-anchor-content-active');

    const cloneReference = $( ".teaser-media > :first-child").clone().empty().appendTo( ".teaser-media" );
    const imageURL = activeElements.$anchor.data('url');
    if(imageURL) {
      $(".teaser-img").first().css("background-image", "url('"+ activeElements.$anchor.data('url') +"')");
      setTimeout(function() {
        $(".teaser-media").css("display", "block");
      }, 1000);
    }else if(this.defaultImage){
      $(".teaser-img").first().css("background-image", this.defaultImage);
      $(".teaser-media").css("display", "block");
    }
    setTimeout(function() {
        cloneReference.remove();
      }, 1000);

    if (!!activeElements.$content.attr('data-tagid') && !this.matchAnchorHash()) {
      window.location.hash = activeElements.$content.attr('data-tagid');
    }

    if (!!activeElements.$content.attr('data-tagid') && changed === true) {
      window.location.hash = activeElements.$content.attr('data-tagid');
    }
    this.activeId = id;
  }
  resetActive(id) {
    var $activeContent = this.$el.find("." + this.anchorContentClassActive);
    var $activeAnchor = this.$el.find("." + this.anchorClassActive);
    $activeContent.removeClass(this.anchorContentClassActive);
    $activeAnchor.removeClass(this.anchorClassActive);

  }
  getElementsById(id) {
    return {
      $anchor: this.$anchorContainer.find('[data-id=' + id + ']'),
      $content: this.$anchorContentContainer.find('[data-id=' + id + ']')
    }
  }
  openModal(title, url) {
    if (this.$iframe.attr('data-src') !== url) {
      this.$modalDialogContent.addClass('loading');
      this.$iframe.attr('data-src', url);
    }

    this.$modalDialogTitle.text(title);

    window.history.replaceState({ ...window.history.state, type: 'page' }, document.title);
    window.history.pushState({ ...window.history.state, type: 'modal', src: url }, document.title);

    this.modalDialogVisible = true;

    this.$modalDialog.removeClass('closed').addClass('open');
  }
  closeModal() {
    this.$modalDialog.removeClass('open').addClass('closed');

    this.modalDialogVisible = false;
  }
  modalShown() {
    if (this.modalDialogVisible && this.$iframe.attr('data-src') !== this.activeIframeUrl) {
      this.$iframe.contents()[0].location.replace(this.$iframe.attr('data-src'));
      this.activeIframeUrl = this.$iframe.attr('data-src');
    }
  }
  frameLoaded() {
    this.$modalDialogContent.removeClass('loading');
    
    var base = document.createElement('base');
    base.setAttribute('target', '_blank');

    this.$iframe.contents().find('head').append(base);
  }
  stateChanged() {
    if (!window.history.state) {
      return;
    }

    const type = window.history.state.type;
    const src = window.history.state.src;

    if (this.modalDialogVisible) {
      if (type === 'modal' || type ==='page') {
        this.closeModal();
      }
    } else {
      if (type === 'modal') {
        if (this.activeIframeUrl !== src) {
          this.$iframe.attr('data-src', src);
        }

        this.$modalDialog.removeClass('closed').addClass('open');
        this.modalDialogVisible = true;
      }
    }
  }
  hashChanged(e) {
    const activeHash = this.$el.find('.' + this.anchorContentClassActive).data('tagid');
    const currentHash = window.location.hash.replace('#', '');
    const currentId = this.$el.find(this.anchorContentClass + '[data-tagid=' + currentHash + ']').data('id');

    if (activeHash !== currentHash) {
      this.toggleActive(currentId, false);
    }
  }
}
