/**
 * Class: RssFeed
 * Description: Her goes description
 */
import $ from 'jquery';

export default class RssFeed {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    //defaults
    this.config = $.extend(
      {
        el: '.rsssignoff',
        postUrl: '/api/feature/rssfeed/signoff'
      },
      config
    );
    this.$el = $(this.config.el);

    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var element = this.$el;
    this.submitForm(element);
  }

  validateForm(element) {
    element.validate({
      highlight: function(element /* , errorClass */) {
        $(element)
          .parent()
          .addClass('has-error');
      },
      unhighlight: function(element /* , errorClass, validClass */) {
        $(element)
          .parent()
          .removeClass('has-error');
      },
      messages: {
        Email: {
          required: element.find('#Email').data('required-text'),
          email: element.find('#Email').data('required-invalid-email')
        }
      }
    });
  }

  submitForm(element) {
    this.validateForm(element);

    var self = this;
    element.submit(function(e) {
      e.preventDefault();

      if (!$(this).valid()) return false;

      var formData = {
        email: $(this)
          .find('#Email')
          .val(),
        datasourceId: $(this)
          .find('#DataSourceId')
          .val()
      };
      var $button = element.find('.rsssignup-submit');

      self.addSpinner($button);
      self.sendData(formData);
    });
  }

  addSpinner($el) {
    $el
      .attr('disabled', 'disabled')
      .parent()
      .append(
        '<div class="formLoaderContainer"><div style="display:inline-block"><div class="formLoader"></div></div></div>'
      );
    $el.css('cssText', 'padding-left: 45px !important');
    $el.css('-webkit-transition', 'none');
    $el.css('transition', 'none');
    $el.find('formLoaderContainer').append();
  }
  sendData(data) {
    var element = this.$el;
    var self = this;
    var request = $.ajax({
      type: 'POST',
      url: self.config.postUrl,
      data: data
    });

    request.done(function() {
      var $formContainer = element.parent('.form-container');
      var $signUpConfirmation = $formContainer.next('.rsssignup-confirmation');
      $signUpConfirmation.removeClass('d-none');
      $formContainer.addClass('d-none');
    });

    request.fail(function() {
      element.find('.rsssignup-error').removeClass('d-none');
    });
  }
}
