/**
 * Class: TopTasks
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';

export default class TopTasks {
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.topTasks'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$trigger = this.$el.find('.topTasks-trigger');
    this.$expander = this.$el.find('.topTask-expander');
    this.$list = this.$el.find('.topTasks-list');
    this.$listItems = this.$list.find('.topTasks-task');
    this.$back = this.$el.find('.topTasks-back');
    this.showAt = -200;
    this.alwaysFixed = this.$el.hasClass('topTasks-fixed-always');
    this.IsFixed = false;
    this.isVisible = false;

    this.onExpanderClick = this.onExpanderClick.bind(this);

    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.bindEvents();

    this.maxItems = responsive.getSize() === 'small' ? 3 : 6;
    var self = this;

    this.$list.find('.topTasks-task').map(function(idx, listItem) {
      if (idx < self.maxItems) {
        $(listItem).attr('aria-hidden', 'false');
      }
    });

    if (this.$listItems.length > this.maxItems) {
      this.$expander.attr('aria-hidden', 'false');
    }
  }

  bindEvents() {
    var self = this;

    this.$expander.on('click', event => this.onExpanderClick(event));

    this.setShowAt();
    this.$trigger.on({
      click: function() {
        if (!self.isVisible) {
          // if mobile -> go to top
          responsive.getSize() === 'small' ? self.goToTop() : self.show();
        } else if (self.isVisible) {
          self.hide();
        }
      }
    });

    this.$back.on({
      click: function() {
        self.hide();
      }
    });

    emitter.on('resize', function() {
      self.maxItems = responsive.getSize() === 'small' ? 3 : 6;

      self.setShowAt();
      utils.$win.scroll();
      if (self.IsFixed) {
        self.setFixed();
      } else {
        self.unsetFixed();
      }

      self.showTopTaskItems();
    });

    emitter.on('scroll', function(obj) {
      if (self.IsFixed && self.isVisible) {
        self.hide();
      }
      if (obj.top > self.showAt && !self.IsFixed) {
        self.setFixed();
      } else if (obj.top < self.showAt && self.IsFixed) {
        self.unsetFixed();
      }
    });
  }

  onExpanderClick(event) {
    event.preventDefault();

    if (this.$expander.hasClass('expanded')) {
      this.$expander.removeClass('expanded');
    } else {
      this.$expander.addClass('expanded');
    }

    this.showTopTaskItems();
  }

  showTopTaskItems() {
    var self = this;
    let isExpanded = this.$expander.hasClass('expanded');

    this.$listItems.map(function(idx, listItem) {
      var $listItem = $(listItem);
      if (isExpanded) {
        $listItem.attr('aria-hidden', 'false');
      } else {
        let hideTask = idx >= self.maxItems;
        $listItem.attr('aria-hidden', hideTask);
      }
    });
  }

  setShowAt() {
    this.showAt = Infinity; // Disable hiding of TopTasks
  }

  unsetFixed() {
    this.$el.removeClass('topTasks-fixed');
    if (!this.alwaysFixed) {
      this.$el.css('margin-top', '');
      utils.$main.css('margin-top', '');
    }
    this.IsFixed = false;
  }

  setFixed() {
    this.$el.addClass('topTasks-fixed');
    if (!this.alwaysFixed) {
      if (
        $('header').hasClass('headerSlider') &&
        responsive.getSize() !== 'small'
      ) {
        this.$el.css('margin-top', -1 * this.$el.height() + 70);
        utils.$main.css('margin-top', this.$el.height());
      } else {
        let headerMargin = $('header').hasClass('headerSlider') ? 0 : 30;
        this.$el.css('margin-top', -1 * this.$el.height() - headerMargin);
        utils.$main.css('margin-top', this.$el.height() - headerMargin);
      }
    }
    this.IsFixed = true;
  }

  hide() {
    this.$el.removeClass('topTasks-fixed-show');
    this.isVisible = false;
  }

  show() {
    this.$el.addClass('topTasks-fixed-show');
    this.isVisible = true;
  }

  goToTop() {
    $('html,body').scrollTop(0);
  }
}
