/**
 * Class: customVideoControls
 * Description: Her goes description
 */
import $ from 'jquery';

export default class CustomVideoControls {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(/* config */) {
    var self = this;
    $('.controlled-video').on('play', function() {
      self.animatePlayPauseControls(this, 1);
    });
    $('.controlled-video').on('pause', function() {
      self.animatePlayPauseControls(this, 0);
    });

    this.animatePlayPauseControls = function($el, $animate_in) {
      if ($animate_in) {
        $($el)
          .parent()
          .children('.custom-video-play-pause')
          .animate({ height: '250px', width: '250px', opacity: '0' }, 'fast');
      } else {
        $($el)
          .parent()
          .children('.custom-video-play-pause')
          .animate({ height: '50px', width: '50px', opacity: '1' }, 'fast');
      }
    };

    $('.custom-video-play-pause').on('click', function() {
      var $vid = $(this)
        .parent()
        .children('.controlled-video')
        .get(0);
      $vid.paused ? $vid.play() : $vid.pause();
    });
  }
}
