/**
 * Class: RssFeed
 * Description: Her goes description
 */
import $ from 'jquery';

export default class RssFeed {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    //defaults
    this.config = $.extend(
      {
        el: '.rsssignup',
        postUrl: '/api/feature/rssfeed/signup'
      },
      config
    );
    this.$el = $(this.config.el);

    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var element = this.$el;

    this.submitForm(element);
  }
  validateForm(element) {
    element.validate({
      errorPlacement: function errorPlacement(error, element) {
        if (element.is('#Privacy')) {
          error.insertAfter('.privacy-confirmation-text');
        } else {
          error.insertAfter(element);
        }
      },
      highlight: function(element /* , errorClass */) {
        $(element)
          .parent()
          .addClass('has-error');
      },
      unhighlight: function(element /* , errorClass, validClass */) {
        $(element)
          .parent()
          .removeClass('has-error');
      },
      messages: {
        FirstName: element.find('#FirstName').data('required-text'),
        LastName: element.find('#LastName').data('required-text'),
        Privacy: element.find('#Privacy').data('required-text'),
        Email: {
          required: element.find('#Email').data('required-text'),
          email: element.find('#Email').data('required-invalid-email')
        }
      }
    });
  }
  submitForm(element) {
    var self = this;
    this.validateForm(element);

    element.submit(function(e) {
      e.preventDefault();

      if (!$(this).valid()) return false;

      var formData = {
        firstname: $(this)
          .find('#FirstName')
          .val(),
        lastname: $(this)
          .find('#LastName')
          .val(),
        email: $(this)
          .find('#Email')
          .val(),
        dataSources: [
          {
            datasourceId: $(this)
              .find('#DataSourceId')
              .val()
          }
        ]
      };

      $(this)
        .find('.additional-signups input')
        .each(function() {
          var $thisInput = $(this);
          if ($thisInput.is(':checked')) {
            formData.dataSources.push({
              datasourceId: $thisInput.val()
            });
          }
        });
      var $button = element.find('.rsssignup-submit');
      self.sendData(formData);
      self.addSpinner($button);
    });
  }
  addSpinner($el) {
    $el
      .attr('disabled', 'disabled')
      .parent()
      .append(
        '<div class="formLoaderContainer"><div style="display:inline-block"><div class="formLoader"></div></div></div>'
      );
    $el.css('cssText', 'padding-left: 45px !important');
    $el.css('-webkit-transition', 'none');
    $el.css('transition', 'none');
    $el.find('formLoaderContainer').append();
  }
  sendData(data) {
    var element = this.$el;
    var self = this;
    var request = $.ajax({
      type: 'POST',
      url: self.config.postUrl,
      data: data
    });

    request.done(function(data) {
      var $formContainer = element.parent('.form-container');
      var $signUpConfirmation = $formContainer
        .parent()
        .find('.rsssignup-confirmation');
      var $signUpConfirmationAlready = $formContainer
        .parent()
        .find('.rsssignup-confirmation-already');
      $formContainer.remove();
      $.each(data.dataSources, function(index, value) {
        if (value.status === 'AlreadySignedUp') {
          $signUpConfirmationAlready.removeClass('d-none');
        } else if (
          value.status === 'Failed' ||
          value.status === 'NoDatasource'
        ) {
          element.find('.rsssignup-error').removeClass('d-none');
        } else {
          $signUpConfirmation.removeClass('d-none');
        }
      });
    });

    request.fail(function() {
      element.find('.rsssignup-error').removeClass('d-none');
    });
  }
}
