/**
 * Class: JobArticle
 * Description: JobArticle script
 */
//import {m, utils} from '../../js/main';
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions } from '../base/utils/utils';

export default class JobArticle {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */

  constructor(config) {
    this.config = $.extend(
      {
        el: ".job",
        date: ".job-details-box-date",
        applyButton: ".job-details-apply",
        printButton: ".job-print-page",
      },
      config
    );

    this.$body = $('body');
    this.$el = $(this.config.el);
    this.$date = this.$el.find(this.config.date);
    this.$applyButton = this.$el.find(this.config.applyButton);
    this.$printButton = $(this.config.printButton);

    if (this.$el.length) {
      this.init();
    }
  }

  init(options) {
    this.$date.each(function (index, item) {
        var $item = $(item);
        $item.text(
            moment($item.text().trim()).format(
                DateFormatingOptions.jobdateFormat
            )
        );
    });
    this.bindEvents();

    if (this.$el.length > 0) {
      this.$body.addClass('job-page-wrapper');
    }
  }
  bindEvents() {
    var self = this;
    self.$printButton.on("click", function (e) {
      e.preventDefault();
      self.$applyButton.attr("style", "display: none !important;");
      $(this)
        .parents(".job-apply-button-container")
        .attr("style", "display: none !important;");
      $(this)
        .parents(".container-fluid")
        .siblings()
        .attr("style", "display: none !important;");
      window.print();
      self.$applyButton.show();
      $(this).parents(".job-apply-button-container").show();
      $(this).parents(".container-fluid").siblings().show();
      return false;
    });
  }
}
