/**
 * Class: CustomLogin
 * Description: Her goes description
 */
import $ from 'jquery';
import 'jquery-validation';

export default class CustomLogin {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.newloginbox'
      },
      config
    );
    this.$el = $(this.config.el);
    //this.$loginBtn = this.$el.find('.login-button');
    //this.$logoutLink = this.$el.find('.logout-button-link');

    this.init();
  }

  init() {
    this.initFormValidation();
    this.bindEvents();
  }

  bindEvents() {
    var self = this;
    this.newLoginBoxValidationRules();
    self.$el.on('click', 'button[name=submittingPrivateData]', function() {
      $('input#Email.form-control.text-box.single-line').val(
        $('input#Email.form-control.text-box.single-line')
          .val()
          .replace(/\s/g, '')
      );

      if (
        $('input#Email.form-control.text-box.single-line').val() != '' &&
        $('input#Password.form-control.text-box.single-line').val() != '' &&
        $('[id=EmailToRemindPassword]').is(':focus') == false
      ) {
        self.$el.find('.div-form-check').hide();
        self.$el.find('.privatelogin').hide();
        self.$el.find('.login-show-password-reminder').hide();
        self.$el.find('.form-check').hide();
        self.$el.find('.login-form-email').hide();
        self.$el.find('.login-form-password').hide();
        self.$el.find('.text-danger').hide();
        self.$el.find('.login-message').hide();
        self.$el.find('.loader-private').show();
      }
    });

    self.$el.on('click', 'button[name=submittingBusinessData]', function() {
      if (
        $('input#CustomerNumber.form-control.text-box.single-line').val() !=
          '' &&
        $(
          'input#PointOfDeliveryNumber.form-control.text-box.single-line'
        ).val() != '' &&
        $('[id=EmailToRemindPasswordCn]').is(':focus') == false
      ) {
        self.$el.find('.div-form-check').hide();
        self.$el.find('.businesslogin').hide();
        self.$el.find('.login-show-password-reminder').hide();
        self.$el.find('.form-check').hide();
        self.$el.find('.form-business .form-check').hide();
        self.$el.find('.login-form-custnumber').hide();
        self.$el.find('.login-form-pod').hide();
        self.$el.find('.text-danger').hide();
        self.$el.find('.login-message').hide();
        self.$el.find('.loader-bussiness').show();
      }
    });
  }

  initFormValidation() {
    this.$el.find('form').validate({
      rules: {
        Email: {
          required: true,
          customEmailAndCustomerNumber: true
        },
        Password: {
          required: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
    this.$el.find('.customerNumberform').validate({
      rules: {
        CustomerNumber: {
          required: true,
          customerNumber: true
        },
        PointOfDeliveryNumber: {
          required: true
          //pointOfDeliveryNumber: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
    this.$el.find('.forgotEmailPasswordForm').validate({
      rules: {
        EmailToRemindPassword: {
          required: true,
          customEmailAndCustomerNumber: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
    this.$el.find('.forgotCustomerNumberPasswordForm').validate({
      rules: {
        EmailToRemindPasswordCn: {
          required: true,
          customerNumber: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
  }

  newLoginBoxValidationRules() {
    $.validator.addMethod(
      'customerNumber',
      function(value /*, element */) {
        if (
          /((^K?\s*(\d\s*){5,8}\d[\s]*$)|(^(\d[ -]*){5}\d$))|((^(\d[\s]*){8}\d$)|(^(\d[\s]*){4}\d$))/.test(
            value
          )
        ) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Customer Number'
    );
    $.validator.addMethod(
      'pointOfDeliveryNumber',
      function(value /* element */) {
        if (/^\d[\d ]*\d$/.test(value)) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Point of delivery number'
    );
    $.validator.addMethod(
      'customEmailAndCustomerNumber',
      function(value /* , element */) {
        //if(/((^K?(\d[ -]*){6,8}\d$)|(^(\d[ -]*){5}\d$))|(^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$)/.test(value)){
        if (
          // eslint-disable-next-line no-useless-escape
          /(^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$)/.test(
            value
          ) ||
          /((^K?\s*(\d\s*){5,8}\d[\s]*$)|(^(\d[ -]*){5}\d$))|((^(\d[\s]*){8}\d$)|(^(\d[\s]*){4}\d$))/.test(
            value
          )
        ) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Customer Number or email'
    );
  }
}
