/**
 * Class: Exploration
 * Description: Her goes description
 */
import Gsap from 'gsap';
import TweenMax from 'gsap/src/uncompressed/TweenMax';
import $ from 'jquery';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';

var Power1 = window.Power1 || null;

export default class Exploration {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.exploration'
      },
      config
    );
    this.$el = $(this.config.el);

    this.dataPath = this.$el.data('jsonPath');
    this.data = null;
    this.$landingLeft = this.$el.find('.landing-left');
    this.$landingRight = this.$el.find('.landing-right');
    this.$categoryItems = this.$el.find('.category-items');
    this.$allCategoryItems = this.$categoryItems.find('.category-item');
    this.$header = this.$el.find('.exploration-header');
    this.$categoryItem = this.$categoryItems.find('.category-item-image-block');
    this.$categoryLinks = this.$el.find('.exploration-category-link');
    this.$categoryLinkContainer = this.$el.find('.exploration-categories');
    this.$categoryLinksMobileSelect = this.$el.find(
      '.exploration-categories-mobile-select'
    );
    this.$viewLanding = this.$el.find('.exploration-landing');
    this.$viewLandingAnimates = this.$el.find(
      '.exploration-landing .landing-animate'
    );

    this.$scrollContainer = this.$el.find('.exploration-scroll');
    this.$scrollDots = this.$el.find('.exploration-scroll-dots');
    this.$scrollUp = this.$el.find('.exploration-scroll-up');
    this.$scrollDown = this.$el.find('.exploration-scroll-down');
    this.$scrollArrow = this.$el.find('.exploration-scroll-arrow');

    this.$scrollIndicator = this.$el.find('.exploration-scroll-indicator');
    this.$scrollIndicatorCurrent = this.$scrollIndicator.find(
      '.exploration-scroll-indicator-current'
    );
    this.$scrollIndicatorTotal = this.$scrollIndicator.find(
      '.exploration-scroll-indicator-total'
    );

    this.scrollTimer = null;

    this.$baseView = this.$el.find('.exploration-base-view');
    this.$detailsView = this.$el.find('.exploration-details-view');

    this.$detailsBack = this.$el.find('.details-right-back');
    this.$detailsNext = this.$el.find('.details-navigation-next');
    this.$detailsPrev = this.$el.find('.details-navigation-prev');
    this.$detailsGridTrigger = this.$el.find(
      '.details-navigation-overview-trigger'
    );
    this.detailsZindex = 100;

    this.viewState = 'random';
    this.$currentView = this.$viewLanding;

    //templates
    this.templateCategoryItemText = this.$el
      .find('#template-category-item-text')
      .html();
    this.templateCategoryItemImage = this.$el
      .find('#template-category-item-image')
      .html();
    this.templateCategoryItemDetails = this.$el
      .find('#template-category-item-details')
      .html();

    this.animDuration = 0.4;
    this.animDurationMiliseconds = 400;

    //tweens
    this.TweenMax = Gsap.TweenMax;
    this.timerDuration = 2500;

    this.init();
  }
  init() {
    var self = this;
    this.loadData(function() {
      if (responsive.getSize() == 'small') {
        self.insertRandomItems(2);
      } else {
        self.insertRandomItems();
      }
      self.bindEvents();
      self.hideScroll();
      self.gotoTileByHash();
    });
  }
  loadData(callback) {
    var self = this;
    if (this.dataPath) {
      $.getJSON(this.dataPath, function(data) {
        self.data = data;
        // take all items and put them into an array and shuffle them --> random items.
        self.dataRandomItems = utils.shuffleArray(
          self.createRandomItemsArray(self.data)
        );
        //call callback
        callback.call(self);
      });
    }
  }
  resetMobileSelect() {
    this.$categoryLinksMobileSelect.val('-1');
  }

  gotoTileByHash() {
    var self = this;
    const s = window.location.hash;
    const r = /cat(\d+)&tile(\d+)/;
    const m = s.match(r);
    if (m) {
      const catId = parseInt(m[1]);
      const itemId = parseInt(m[2]);
      var item = self.getItem(catId, itemId);
      if (item && item.imagePath) {
        // we have a personblock
        self.gotoDetailById(catId, itemId);
      }
    }
  }
  bindEvents() {
    var self = this;
    //$(document).ready( function(){gotoTileByHash()});

    this.$categoryLinks.on({
      click: function(e) {
        e.preventDefault();
        const data = $(this).data();
        self.updateActiveCategoryLink(data.categoryId);
        self.showCategoryItems(data.categoryId);
      }
    });
    this.$categoryLinksMobileSelect.on({
      change: function(e) {
        e.preventDefault();
        const categoryId = $(this).val();
        self.updateActiveCategoryLink(categoryId);
        self.showCategoryItems(categoryId);
      }
    });
    this.$header.on({
      click: function(e) {
        e.preventDefault();
        self.resetActiveCategoryLink();
        if (responsive.getSize() == 'small') {
          self.insertRandomItems(2);
        } else {
          self.insertRandomItems();
        }
        self.resetMobileSelect();
      }
    });
    this.$scrollUp.on({
      click: function(e) {
        e.preventDefault();
        var activeIndex = self.$scrollDots
          .find('.exploration-scroll-dot-active')
          .index();
        if (responsive.getSize() == 'small') {
          const itemWidth = self.$landingRight.width();
          // const numberOfItems = self.getItemsInContainer().length;
          // const scrollWidth =
          //   numberOfItems * self.$landingRight.width() + itemWidth;
          const currentPosition = self.$landingRight.scrollLeft();
          if (currentPosition >= itemWidth) {
            activeIndex = currentPosition / itemWidth;
          } else {
            activeIndex = 0;
          }
        }
        self.scrollToIndex(activeIndex - 1);
      }
    });
    this.$scrollDown.on({
      click: function(e) {
        e.preventDefault();
        var activeIndex = self.$scrollDots
          .find('.exploration-scroll-dot-active')
          .index();
        if (responsive.getSize() == 'small') {
          const itemWidth = self.$landingRight.width();
          const numberOfItems = self.getItemsInContainer().length;
          const scrollWidth = numberOfItems * self.$landingRight.width();
          const currentPosition = self.$landingRight.scrollLeft();
          if (currentPosition < scrollWidth && currentPosition != 0) {
            activeIndex = Math.floor(currentPosition / itemWidth);
            if (activeIndex > numberOfItems - 2) {
              activeIndex = numberOfItems - 2;
            }
          } else {
            activeIndex = 0;
          }
        }
        self.scrollToIndex(activeIndex + 1);
      }
    });
    this.$landingRight.on({
      scroll: function(/* e */) {
        if (responsive.getSize() == 'small') {
          var currentScrollIndex = self.getCurrentScrollIndex() + 1;
          if (currentScrollIndex === Infinity) {
            currentScrollIndex = 1;
          }
          self.updateIndicator(
            currentScrollIndex,
            self.getItemsInContainer().length
          );
          if (self.scrollTimer !== null) {
            clearTimeout(self.scrollTimer);
          }
          self.scrollTimer = setTimeout(function() {
            self.scrollToIndex(self.getCurrentScrollIndex());
          }, 150);
        }
      }
    });
  }

  updateUrl(hash) {
    history.replaceState(undefined, undefined, '#' + hash);
  }
  getCurrentScrollIndex() {
    const itemWidth = this.$landingRight.width();
    const currentPosition = this.$landingRight.scrollLeft();
    return Math.round(currentPosition / itemWidth);
  }
  resetScroll() {
    TweenMax.set(this.$landingRight, {
      scrollTo: {
        y: 0
      }
    });
  }
  scrollToIndex(index) {
    var self = this;
    var numberOfSlides = self.getNumberOfSlides();
    if (responsive.getSize() == 'small') {
      numberOfSlides = self.getItemsInContainer().length;
      // const scrollWidth = numberOfSlides * self.$landingRight.width();
      const landingWidth = this.$landingRight.width();
      if (index >= 0 && index < numberOfSlides) {
        position = index * landingWidth;
        TweenMax.to(this.$landingRight, 0.5, {
          scrollTo: {
            x: position,
            ease: Power1.easeInOut
          }
        });
      }
    } else if (index >= 0 && index < numberOfSlides) {
      var position = this.$landingRight.height() * index;
      TweenMax.to(this.$landingRight, 0.5, {
        scrollTo: {
          y: position,
          ease: Power1.easeInOut
        }
      });
    }
    var nextIndex = index + 1;
    if (nextIndex < 1) {
      nextIndex = 1;
    }
    if (nextIndex > numberOfSlides) {
      nextIndex = numberOfSlides;
    }
    this.updateScrollDots(nextIndex);
    // this.updateIndicator(nextIndex, numberOfSlides);
  }
  updateScrollDots(gotoSlide) {
    var self = this;
    var numberOfSlides = self.getNumberOfSlides();
    if (responsive.getSize() == 'small') {
      numberOfSlides = self.getItemsInContainer().length;
    }
    this.$scrollArrow.removeClass('exploration-scroll-arrow-inactive');
    if (gotoSlide == 1) {
      this.$scrollUp.addClass('exploration-scroll-arrow-inactive');
    } else if (gotoSlide == numberOfSlides) {
      this.$scrollDown.addClass('exploration-scroll-arrow-inactive');
    }
    this.$scrollDots.find('.exploration-scroll-dot').off();
    this.$scrollDots.html('');
    for (var i = 0; i < numberOfSlides; i++) {
      this.$scrollDots.append('<div class="exploration-scroll-dot"></div>');
    }
    this.$scrollDots.find('.exploration-scroll-dot').on({
      click: function(/* e */) {
        self.scrollToIndex($(this).index());
      }
    });
    this.$scrollDots
      .find('.exploration-scroll-dot-active')
      .removeClass('exploration-scroll-dot-active');
    this.$scrollDots
      .find('.exploration-scroll-dot')
      .eq(gotoSlide - 1)
      .addClass('exploration-scroll-dot-active');
  }

  getNumberOfSlides() {
    return Math.ceil(
      this.$categoryItems.height() / this.$landingRight.height()
    );
  }

  updateIndicator(current, total) {
    this.$scrollIndicatorCurrent.html(current);
    this.$scrollIndicatorTotal.html(total);
  }
  showScroll() {
    this.updateScrollDots(1);
    this.updateIndicator(1);
    if (responsive.getSize() == 'small') {
      TweenMax.to(this.$scrollContainer, 0.2, {
        maxHeight: 45,
        maxWidth: 'none'
      });
    } else {
      TweenMax.to(this.$scrollContainer, 0.2, {
        maxWidth: 45,
        maxHeight: 'none'
      });
    }
  }
  hideScroll() {
    if (responsive.getSize() == 'small') {
      TweenMax.to(this.$scrollContainer, 0.2, {
        maxHeight: 0
      });
    } else {
      TweenMax.to(this.$scrollContainer, 0.2, {
        maxWidth: 0
      });
    }
  }
  showCategoryItems(categoryId) {
    if (this.currentCategoryId == categoryId) {
      return false;
    } else {
      this.currentCategoryId = categoryId;
    }

    var self = this;
    this.stopCountDownTimer();
    this.viewState = 'category';
    this.animateItemsOut(function() {
      self.$categoryItems.removeClass('random-view');
      self.$categoryItems.addClass('category-view');
      //when done, remove items
      self
        .getItemsInContainer()
        .off()
        .remove();
      // self.bindItemsEvents();
      self.$categoryItems.append(self.createCategoryItems(categoryId));
      // and animate new ones in.
      self.bindItemsEvents();
      self.animateItemsIn();
      self.showScroll();
      this.resetScroll();
      self.updateIndicator(1, this.getItemsInContainer().length);
    });
  }
  insertRandomItems(numberOfItems) {
    this.viewState = 'random';
    this.currentCategoryId = null;
    numberOfItems = numberOfItems || 9;
    var self = this;
    // if theres items shown, animate out and then in
    if (self.getItemsInContainer().length) {
      this.animateItemsOut(function() {
        self.$categoryItems.removeClass('category-view');
        self.$categoryItems.addClass('random-view');
        self.stopCountDownTimer();
        //when done, remove items
        self
          .getItemsInContainer()
          .off()
          .remove();
        // self.bindItemsEvents();
        this.$categoryItems.append(self.createRandomItems(numberOfItems));
        // and animate new ones in.
        self.bindItemsEvents();
        self.animateItemsIn(function() {
          self.startCountDownTimer();
        });
        self.hideScroll();
      });
    } else {
      // else just animate in
      this.$categoryItems.append(self.createRandomItems(numberOfItems));
      this.bindItemsEvents();
      this.animateItemsIn(function() {
        self.startCountDownTimer();
      });
      self.hideScroll();
    }
  }
  animateItemsIn(callback) {
    var self = this;
    if (self.getItemsInContainer().length) {
      TweenMax.staggerFromTo(
        self.getItemsInContainer(),
        self.animDuration,
        {
          opacity: 0,
          scale: 0.95,
          ease: Power1.easeInOut
        },
        {
          opacity: 1,
          scale: 1,
          ease: Power1.easeInOut
        },
        0.04,
        function() {
          if (callback) {
            callback.call(self);
          }
        }
      );
    }
  }
  animateItemsOut(callback) {
    var self = this;
    if (self.getItemsInContainer().length) {
      TweenMax.staggerTo(
        self.getItemsInContainer(),
        self.animDuration,
        {
          opacity: 0,
          scale: 0.95,
          ease: Power1.easeInOut
        },
        0.04,
        function() {
          if (callback) {
            callback.call(self);
          }
        }
      );
    }
  }
  prepItem(item, categoryId) {
    var self = this;
    item.categoryId = categoryId;
    if (!item.theme) {
      item.theme = self.data.theme;
    }
    var prevItem = self.data.categories[item.categoryId].items[item.id - 1];
    var addToPrevIndex = 1;
    if (prevItem && !prevItem.imagePath) {
      addToPrevIndex = 2;
      prevItem =
        self.data.categories[item.categoryId].items[item.id - addToPrevIndex];
    }
    if (prevItem && prevItem.imagePath) {
      item.prevId = item.id - addToPrevIndex;
      item.prevTitle =
        self.data.categories[item.categoryId].items[
          item.id - addToPrevIndex
        ].title;
    }

    var nextItem = self.data.categories[item.categoryId].items[item.id + 1];
    var addToNextIndex = 1;
    if (nextItem && !nextItem.imagePath) {
      addToNextIndex = 2;
      nextItem =
        self.data.categories[item.categoryId].items[item.id + addToNextIndex];
    }
    if (nextItem) {
      item.nextId = item.id + addToNextIndex;
      item.nextTitle =
        self.data.categories[item.categoryId].items[
          item.id + addToNextIndex
        ].title;
    }
    return item;
  }
  createRandomItemsArray(data) {
    var self = this;
    var i = 0;
    var allItems = [];
    self.dataAllItems = [];
    while (data.categories[i]) {
      var j = 0;
      if (data.categories[i].items) {
        while (data.categories[i].items[j]) {
          data.categories[i].items[j] = self.prepItem(
            data.categories[i].items[j],
            data.categories[i].id
          );
          allItems.push(data.categories[i].items[j]);
          j++;
        }
      }
      i++;
    }
    return allItems;
  }
  createRandomItems(numberOfItems) {
    var self = this;
    var randomItemsHtml = '';
    var i = 0;
    var numItems = self.data.initialItems.items.length;
    while (i < Math.min(numberOfItems, numItems)) {
      var item = self.data.initialItems.items[i];
      if (!item.theme) {
        item.theme = self.data.theme;
      }
      if (self.data.initialItems.items[i].imagePath) {
        randomItemsHtml += self.getImageBlockHtml(item);
      } else {
        randomItemsHtml += self.getTextBlockHtml(item);
      }
      i++;
    }
    return randomItemsHtml;
  }
  createCategoryItems(categoryId) {
    var self = this;
    var items = this.getCategory(categoryId).items;
    var itemsHtml = '';
    var i = 0;
    while (i < items.length - 1 * 0) {
      var item = items[i];
      if (items[i].imagePath) {
        itemsHtml += self.getImageBlockHtml(item);
      } else {
        itemsHtml += self.getTextBlockHtml(item);
      }
      i++;
    }
    return itemsHtml;
  }
  bindDetailsEvents() {
    var self = this;
    const $detailsNext = this.$currentDetails.find('.details-navigation-next');
    const $detailsPrev = this.$currentDetails.find('.details-navigation-prev');
    const $detailsBack = this.$currentDetails.find('.details-right-back');
    const $detailsShowAll = this.$currentDetails.find(
      '.details-navigation-overview-trigger'
    );
    const $showMore = this.$currentDetails.find(
      '.details-right-content-show-btns'
    );
    var detailsExpanded = false;

    $showMore.on({
      click: function(/* e */) {
        const $detailsContent = self.$currentDetails.find(
          '.details-right-content > *'
        );
        if (detailsExpanded) {
          $detailsContent
            .parent()
            .removeClass('details-right-content-expanded');
          $(this).removeClass('details-right-content-expanded');
          detailsExpanded = false;
          self.scrollToTop();
        } else {
          $detailsContent.parent().addClass('details-right-content-expanded');
          $(this).addClass('details-right-content-expanded');
          detailsExpanded = true;
        }
      }
    });

    $detailsNext.on({
      click: function() {
        self.gotoNextDetail();
        self.scrollToTop();
      }
    });

    $detailsPrev.on({
      click: function() {
        self.gotoPrevDetail();
        self.scrollToTop();
      }
    });

    $detailsBack.on({
      click: function(/* e */) {
        self.updateUrl('');
        self.animateDetailsOutFade(self.$currentDetails);
        self.scrollToTop();
      }
    });
    $detailsShowAll.on({
      click: function(/* e */) {
        self.updateUrl('');
        const currentData = self.$currentDetails.data();
        self.updateActiveCategoryLink(currentData.categoryId);
        self.showCategoryItems(currentData.categoryId);
        self.animateDetailsOutFade(self.$currentDetails);
        self.scrollToTop();
      }
    });
  }

  unbindDetailsEvents() {
    const $detailsNext = this.$currentDetails.find('.details-navigation-next');
    const $detailsPrev = this.$currentDetails.find('.details-navigation-prev');
    const $detailsBack = this.$currentDetails.find('.details-right-back');
    const $detailsShowAll = this.$currentDetails.find(
      '.details-navigation-overview-trigger'
    );

    $detailsNext.off();
    $detailsPrev.off();
    $detailsBack.off();
    $detailsShowAll.off();
  }

  bindItemsEvents(/* $item */) {
    var self = this;
    var $items = this.getItemsInContainer().not('.category-item-textblock');
    $items.off().on({
      click: function(/* e */) {
        const itemData = $(this).data();
        self.gotoDetailById(itemData.categoryId, itemData.itemId);
      }
    });
  }

  gotoDetailById(catId, itemId) {
    var self = this;
    var detailsHtml = this.createDetails(catId, itemId);

    self.updateUrl('cat' + catId + '&tile' + itemId);

    this.currentDetailCategory = catId;
    this.currentDetailItem = itemId;
    this.insertDetail(detailsHtml, function() {
      self.animateDetailsInHandler(self.$currentDetails);
    });
  }

  insertDetail(detailsHtml, callback) {
    var self = this;
    this.$detailsView.append(detailsHtml);
    this.$currentDetails = this.$detailsView
      .find('.exploration-details')
      .last();
    this.bindDetailsEvents();
    var $contentContainer = this.$currentDetails.find('.details-right-content');
    this.stopCountDownTimer();
    this.loadDetailsContent($contentContainer, function() {
      callback.call(self);
    });
  }

  loadDetailsContent($contentContainer, callback) {
    const contentPath = $contentContainer.data().detailContentUrl;
    if (callback) {
      callback.call(this);
    }
    $contentContainer.load(
      contentPath + ' .ajaxContent',
      function(/* $data */) {}
    );
  }

  createDetails(catId, itemId) {
    var item = this.getItem(catId, itemId);
    return utils.TemplateEngine(this.templateCategoryItemDetails, item);
  }

  gotoAdjecentDetail(increment) {
    var self = this;
    var item = this.getItem(
      this.currentDetailCategory,
      this.currentDetailItem + increment
    );
    if (item && !item.imagePath) {
      // we have a textblock, therefore take the next.
      this.currentDetailItem += increment;
      this.gotoAdjecentDetail(increment);
      return false;
    }
    if (item) {
      const nextDetailHtml = this.createDetails(
        this.currentDetailCategory,
        this.currentDetailItem + increment
      );
      self.updateUrl(
        'cat' +
          this.currentDetailCategory +
          '&tile' +
          (this.currentDetailItem + increment)
      );
      this.currentDetailItem += increment;
      var $animateOutdetails = this.$detailsView
        .find('.exploration-details')
        .first();
      self.insertDetail(nextDetailHtml, function() {
        self.animateDetailsInHandler(
          this.$detailsView.find('.exploration-details').last(),
          increment > 0 ? 'right' : 'left',
          $animateOutdetails
        );
      });
    }
  }

  gotoNextDetail() {
    this.gotoAdjecentDetail(1);
  }

  gotoPrevDetail() {
    this.gotoAdjecentDetail(-1);
  }

  bindCategoryItems() {
    var self = this;
    this.$categoryItems.on(
      'click',
      '.category-item-image-block',
      function(/* e */) {
        self.animateDetailsInHandler($(this));
      }
    );
  }

  unbindCategoryItems() {
    this.getItemsInContainer().off();
  }

  resetActiveCategoryLink() {
    this.$categoryLinkContainer
      .find('.exploration-category-item-active')
      .removeClass('exploration-category-item-active');
  }

  updateActiveCategoryLink(index) {
    var $setActive = this.$categoryLinkContainer
      .find('.exploration-category-item')
      .eq(index);
    this.resetActiveCategoryLink();
    $setActive.addClass('exploration-category-item-active');
  }

  startCountDownTimer() {
    const self = this;
    this.cycleInterval = setInterval(function() {
      self.exchangeRandomItem();
    }, self.timerDuration);
  }

  stopCountDownTimer() {
    clearInterval(this.cycleInterval);
  }

  exchangeRandomItem() {
    var self = this;
    var randomItemHtml = this.getRandomBlockHtml();
    var typeOfBlock =
      randomItemHtml.indexOf('category-item-image-block') > -1
        ? 'image'
        : 'text';
    var $landingItems = this.getItemsInContainer();
    var $replaceItemsPool = $landingItems.filter('.category-item');
    var textRandomNumbers =
      responsive.getSize() == 'small' ? [1] : [0, 2, 4, 6, 8];
    var randomId = Math.floor(Math.random() * $replaceItemsPool.length);
    if ($replaceItemsPool.length > 0) {
      if (typeOfBlock != 'image') {
        var randomNumber = Math.floor(
          Math.random() * textRandomNumbers.length - 1
        );
        randomId = textRandomNumbers[randomNumber];
      }
      if (responsive.getSize() == 'small') {
        if (this.lastRandomId == 0) {
          randomId = 1;
        } else {
          randomId = 0;
        }
      }
      this.lastRandomId = randomId;
      var $replaceItem = $($replaceItemsPool[randomId]);
      // console.log(textRandomNumbers[Math.floor(Math.random() * textRandomNumbers.length())];);

      TweenMax.to($replaceItem, self.animDuration, {
        opacity: 0,
        scale: 0.95,
        ease: Power1.easeInOut,
        onComplete: function() {
          var indexOfItem = $replaceItem.index();
          $replaceItem.after(randomItemHtml);
          $replaceItem.remove();
          $landingItems = self.getItemsInContainer();
          var $replacementItem = $landingItems.eq(indexOfItem);
          self.bindItemsEvents();
          TweenMax.fromTo(
            $replacementItem,
            self.animDuration,
            {
              opacity: 0,
              scale: 0.95
            },
            {
              opacity: 1,
              scale: 1
            }
          );
        }
      });
    }
  }

  animateBaseViewOut() {
    var self = this;
    TweenMax.to(this.$baseView, self.animDuration, {
      opacity: 0
      // delay: self.animDuration
    });
    TweenMax.set(this.$baseView, {
      maxHeight: 0,
      maxWidth: 0
      // delay: self.animDuration
    });
  }

  animateBaseViewIn() {
    var self = this;
    TweenMax.to(this.$baseView, self.animDuration * 2, {
      opacity: 1,
      maxHeight: '5000px',
      maxWidth: '5000px'
    });
  }  

  scrollToTop() {
    var self = this;
    $("html,body").animate(
      { scrollTop: self.$el.offset().top - 130 },
      self.animDurationMiliseconds
    );
  }

  animateDetailsInHandler($elIn, type, $elOut) {
    var self = this;
    switch (type) {
      case 'left':
        self.animateDetailsOutLeft($elOut);
        self.animateDetailsInLeft($elIn);
        break;
      case 'right':
        self.animateDetailsOutRight($elOut);
        self.animateDetailsInRight($elIn);
        break;
      case 'fade':
        if (responsive.getSize() == 'small') {
          self.animateDetailsOutFade($elOut);
        } else {
          self.animateDetailsOut($elOut);
        }
        self.animateDetailsInFade($elIn);
        break;
      default:
        if ($elOut !== undefined) {
          self.animateDetailsOutFade($elOut);
        }
        if (responsive.getSize() == 'small') {
          self.animateDetailsInFade($elIn);
        } else {
          self.animateDetailsIn($elIn);
        }
    }
    if (self.viewState == 'random') {
      self.startCountDownTimer();
    } else {
      self.stopCountDownTimer();
    }
  }

  animateDetailsIn($el) {
    var $itemInContainer = this.getItemInContainer($el.data());
    var self = this;
    var $image = $el.find('.details-image');
    var $navigation = $el.find('.details-navigation');
    var $right = $el.find('.details-right');
    var position, height, width;
    if ($itemInContainer !== undefined && $itemInContainer.length !== 0) {
      position = $itemInContainer.position();
      height = $itemInContainer.outerHeight(true);
      width = $itemInContainer.outerWidth(true);
    } else {
      position = { top: 0, left: 0 };
      height = 0;
      width = 0;
    }
    TweenMax.set($el, {
      zIndex: 1
    });
    this.animateBaseViewOut();

    TweenMax.fromTo(
      $navigation,
      self.animDuration,
      {
        opacity: 0
      },
      {
        opacity: 1,
        delay: self.animDuration / 2
      }
    );
    TweenMax.fromTo(
      $image,
      self.animDuration,
      {
        top: position.top,
        left: position.left,
        width: width,
        height: height,
        position: 'absolute',
        ease: Power1.easeInOut
      },
      {
        position: 'absolute',
        width: '100%',
        height: self.$el.height(),
        left: 0,
        top: 0,
        ease: Power1.easeInOut
      }
    );
    //
    TweenMax.fromTo(
      $right,
      self.animDuration,
      {
        position: 'absolute',
        opacity: 0,
        right: -200,
        ease: Power1.easeInOut
      },
      {
        opacity: 1,
        right: 0,
        ease: Power1.easeInOut,
        onComplete: function() {
          self.scrollToTop();
        }
      }
    );
  }

  animateDetailsOut($el) {
    var $itemInContainer = this.getItemInContainer($el.data());
    if (!$itemInContainer.length) {
      this.animateDetailsOutFade($el);
      return false;
    }
    var self = this;
    var $image = $el.find('.details-image');
    var $navigation = $el.find('.details-navigation');
    var $right = $el.find('.details-right');
    var position = $itemInContainer.position();
    var height = $itemInContainer.outerHeight(true);
    var width = $itemInContainer.outerWidth(true);
    TweenMax.set($el, {
      zIndex: 0
    });

    this.animateBaseViewIn();
    TweenMax.fromTo(
      $navigation,
      self.animDuration,
      {
        opacity: 1
      },
      {
        opacity: 0
      }
    );
    TweenMax.fromTo(
      $image,
      self.animDuration,
      {
        position: 'absolute',
        width: '100%',
        height: self.$el.height(),
        left: 0,
        top: 0,
        ease: Power1.easeInOut
      },
      {
        top: position.top,
        left: position.left,
        width: width,
        height: height,
        position: 'absolute',
        ease: Power1.easeInOut
      }
    );
    //
    TweenMax.fromTo(
      $right,
      self.animDuration,
      {
        opacity: 1,
        right: 0,
        ease: Power1.easeInOut
      },
      {
        position: 'absolute',
        opacity: 0,
        right: -200,
        ease: Power1.easeInOut,
        onComplete: function() {
          self.unbindDetailsEvents();
          $el.remove();
        }
      }
    );
  }

  animateDetailsInRight($el) {
    var self = this;
    var $left = $el.find('.details-left');
    var $right = $el.find('.details-right');

    TweenMax.set($el, {
      zIndex: 1
    });
    TweenMax.fromTo(
      $left,
      self.animDuration * 2,
      {
        left: -200,
        top: 0,
        opacity: 0,
        position: 'absolute',
        height: self.$el.height(),
        ease: Power1.easeInOut
      },
      {
        left: 0,
        opacity: 1,
        top: 0,
        ease: Power1.easeInOut
      }
    );
    //
    TweenMax.fromTo(
      $right,
      self.animDuration * 2,
      {
        position: 'absolute',
        opacity: 0,
        left: '40%',
        ease: Power1.easeInOut
      },
      {
        opacity: 1,
        left: '50%',
        ease: Power1.easeInOut
      }
    );
  }

  animateDetailsOutRight($el) {
    var self = this;
    var $left = $el.find('.details-left');
    // var $navigation = $el.find('.details-navigation');
    var $right = $el.find('.details-right');
    TweenMax.to($left, self.animDuration * 2, {
      left: 300,
      opacity: 0,
      ease: Power1.easeInOut
    });
    TweenMax.to($right, self.animDuration * 2, {
      opacity: 0,
      left: '70%',
      ease: Power1.easeInOut,
      onComplete: function() {
        $el.remove();
      }
    });
  }

  animateDetailsInLeft($el) {
    var self = this;
    var $left = $el.find('.details-left');
    var $right = $el.find('.details-right');

    TweenMax.set($el, {
      zIndex: 1
    });
    TweenMax.fromTo(
      $left,
      self.animDuration * 2,
      {
        left: 200,
        top: 0,
        opacity: 0,
        position: 'absolute',
        height: self.$el.height(),
        ease: Power1.easeInOut
      },
      {
        left: 0,
        opacity: 1,
        top: 0,
        ease: Power1.easeInOut
      }
    );
    //
    TweenMax.fromTo(
      $right,
      self.animDuration * 2,
      {
        position: 'absolute',
        opacity: 0,
        left: '60%',
        ease: Power1.easeInOut
      },
      {
        opacity: 1,
        left: '50%',
        ease: Power1.easeInOut
      }
    );
  }
  animateDetailsOutLeft($el) {
    var self = this;
    var $left = $el.find('.details-left');
    // var $navigation = $el.find('.details-navigation');
    var $right = $el.find('.details-right');
    TweenMax.to($left, self.animDuration * 2, {
      left: -300,
      opacity: 0,
      ease: Power1.easeInOut
    });
    TweenMax.to($right, self.animDuration * 2, {
      opacity: 0,
      left: '30%',
      ease: Power1.easeInOut,
      onComplete: function() {
        $el.remove();
      }
    });
  }
  animateDetailsInFade($el) {
    // var $itemInContainer = this.getItemInContainer($el.data());
    var self = this;
    // var $image = $el.find('.details-image');
    var $navigation = $el.find('.details-navigation');
    // var $right = $el.find('.details-right');
    // var position = $itemInContainer.position();
    // var height = $itemInContainer.outerHeight(true);
    // var width = $itemInContainer.outerWidth(true);
    TweenMax.set($el, {
      zIndex: 1
    });
    this.animateBaseViewOut();

    TweenMax.fromTo(
      $navigation,
      self.animDuration,
      {
        opacity: 0
      },
      {
        opacity: 1,
        delay: self.animDuration / 2
      }
    );
    TweenMax.fromTo(
      $el,
      self.animDuration,
      {
        opacity: 0
      },
      {
        opacity: 1,
        delay: self.animDuration / 2,
        onComplete: function() {
          self.scrollToTop();
        }
      }
    );
  }
  animateDetailsOutFade($el) {
    // this.startCountDownTimer();
    var self = this;
    this.animateBaseViewIn();
    TweenMax.to($el, self.animDuration, {
      opacity: 0,
      scale: 0.5,
      ease: Power1.easeInOut,
      onComplete: function() {
        $el.remove();
      }
    });
  }
  getRandomBlockHtml() {
    //var data = this.dataRandomItems[Math.floor(Math.random() * this.dataRandomItems.length)];
    var distinctItemsArr = [];
    if (this.dataRandomItems.length > 9) {
      var $landingItems = this.getItemsInContainer();
      var $replaceItemsPool = $landingItems.filter('.category-item');
      var i;
      for (i = 0; i < this.dataRandomItems.length; ++i) {
        var filterItem = $replaceItemsPool.filter(
          "[data-category-id='" +
            this.dataRandomItems[i]['categoryId'] +
            "'][data-item-id='" +
            this.dataRandomItems[i]['id'] +
            "']"
        );
        if (filterItem.length <= 0) {
          distinctItemsArr.push(this.dataRandomItems[i]);
        }
      }
    } else {
      distinctItemsArr = this.dataRandomItems;
    }
    var data =
      distinctItemsArr[Math.floor(Math.random() * distinctItemsArr.length)];
    if (data) {
      if (data.imagePath) {
        return this.getImageBlockHtml(data);
      } else {
        return this.getTextBlockHtml(data);
      }
    }
  }
  getImageBlockHtml(data) {
    return utils.TemplateEngine(this.templateCategoryItemImage, data);
  }
  getTextBlockHtml(data) {
    return utils.TemplateEngine(this.templateCategoryItemText, data);
  }
  getRandomItem() {
    return this.dataRandomItems[
      Math.floor(Math.random() * this.dataRandomItems.length)
    ];
  }
  getCategory(categoryId) {
    return this.data.categories[categoryId];
  }
  getItem(categoryId, itemId) {
    var item = this.data.categories[categoryId].items[itemId];
    if (item) {
      return item;
    } else {
      return false;
    }
  }
  getItemsInContainer() {
    return this.$baseView.find('.category-item');
  }
  getItemInContainer(objData) {
    return this.$categoryItems.find(
      `[data-item-id="${objData.id}"][data-category-id="${objData.categoryId}"]`
    );
  }
}
