import { emitter } from "../base/emitter/emitter";
export default class FactBox {
  constructor(config) {
    this.config = Object.assign({}, config);
    this.element = this.config.el.get(0);
    this.isTwoColumn = this.element.classList.contains("factBox-2-columns");
    this.innerContainer = this.element.querySelector(".factBox__inner");
    this.button = this.element.querySelector(".factBox-cta");
    this.buttonParent = null;
    this.buttonReferenceNode = null;

    this.init();
  }

  init() {
    if (this.button) {
      this.buttonParent = this.button.parentNode;
      this.buttonReferenceNode = this.button.nextElementSibling;
    }

    this.bindEvents();
  }

  positionButtonBottom() {
    if (this.button) {
      this.buttonParent.removeChild(this.button);
      this.innerContainer.appendChild(this.button);
    }
  }

  resetButtonPosition() {
    if (this.button && this.button.parentElement === this.innerContainer) {
      this.innerContainer.removeChild(this.button);
      this.buttonParent.insertBefore(this.button, this.buttonReferenceNode);
    }
  }

  bindEvents() {
    emitter.on("responsive/sizechange", size => {
      if (!this.isTwoColumn) {
        return;
      }

      if (size === "small") {
        this.positionButtonBottom();
      } else {
        this.resetButtonPosition();
      }
    });
  }
}
