const $ = window.$;

if (window.app !== undefined) {
    window.app.directive("flowStep", function () {
        return {
            restrict: 'A',
            require: ['^flow', '?form'],
            link: function (scope, element, attrs, ctrls) {
                var flow = ctrls[0];
                var form = ctrls[1];

                var step = {};
                step.name = attrs.flowStep;
                step.path = '/' + step.name.toLowerCase().replace(' ', '-').replace('', 'ae').replace('', 'oe').replace('', 'aa');

                step.Show = function () {
                    $(element).show();
                };

                step.Hide = function () {
                    $(element).hide();
                }

                step.IsValid = function () {
                    form.$setSubmitted();
                    return flow.skipValidation || form.$valid;
                };

                step.Submit = function () {
                    form.$setSubmitted();
                    return step.IsValid();
                }

                flow.RegisterStep(step);
            }
        }
    });
}
