import InViewPort from "../base/inViewPort/inViewPort";
import { getElement } from "../base/utils/utils";

const defaultOptions = {};

function initSection() {
  new InViewPort({
    el: this.$el,
    inViewinitedClass: "col-in-viewport-inited",
    inViewClass: "col-was-in-viewport",
    inViewCurrentClass: "col-in-viewport",
    eventName: "columnInView",
    offsetBottom: -50,
    offsetTop: 800
  });
}

class Section {
  constructor(options) {
    this.options = {
      ...defaultOptions,
      ...options
    };

    this.$el = getElement(this.options.el);

    if (!this.$el) {
      throw new Error("Selected section is not exist");
    }

    initSection.bind(this)();
  }
}

export default Section;
