/**
 * Class: Search
 * Description: Type-ahead search function
 * Revision: 23032017
 * Notes: Animation tool could possible be exchanged with transitjs or velocityjs for better results.
 **/

import $ from 'jquery';
import 'typeahead.js';
import Bloodhound from 'typeahead.js';

export default class Search {
  constructor(config) {
    this.config = $.extend({}, config);

    this.$el = $(this.config.el);
    // Get inline-html settings
    //this.$width = ($(this.$el).data('width') ? $(this.$el).data('width') + 'px' : '250px');

    // Force the size of the input field dependant on data-width
    // This adds easy implemented dynamic sizing - and it forces
    // Twitter typeahead to match the size.
    //$(this.$el).css({ width: this.$width });

    // Initiate Bloodhound
    this.states = new Bloodhound({
      datumTokenizer: Bloodhound.tokenizers.whitespace,
      queryTokenizer: Bloodhound.tokenizers.whitespace,
      remote: {
        url: '/api/search/ankiro/typeahead?phrase=%QUERY',
        wildcard: '%QUERY',
        filter: function(response) {
          return response.List;
        }
      }
    });
    // Bind Typeahead
    $(this.$el).typeahead(
      {
        minLength: 3,
        highlight: true,
        hint: false,
        limit: 10
      },
      {
        name: 'List',
        //display: 'Term',
        displayKey: function(states) {
          return states.Term;
        },
        source: this.states
      }
    );

    this.$twitterParent = this.$el.parents('.twitter-typeahead');
    this.init();
  }

  init() {
    var self = this;

    // Bind Search Button
    // Dual-functionality to show field and to submit, when field is shown.
    $('button.search-button').on('click', function(e) {
      // Find the Nearest input wrapper
      var elem = $(this).closest('.input-wrapper');

      // Check wether or not the input field is visible
      // Show if it isn't visibile and prevent default
      // Default behaviour when the field is shown - else we can't search.
      if (
        $(elem)
          .find('input')
          .is(':visible')
      ) {
        //self.hideSearch(elem);
      } else {
        e.preventDefault();
        self.showSearch(elem, self.$width);
      }
    });

    // Register When Field Loses focus - and hides it.
    // ** Wanted behaviour? **
    $('.input-wrapper input').focusout(function(/* e */) {
      var elem = $(this).closest('.input-wrapper');
      self.hideSearch(elem);
    });

    // Bind Escape to hide Search Field
    // Possibly clear search query (?)
    $(document).keyup(function(e) {
      var elem = $(self.$el).closest('.input-wrapper');

      // Check if input is visible
      if (
        $(elem)
          .find('input')
          .is(':visible')
      ) {
        // Check for ESC hit
        if (e.keyCode === 27) {
          // Hide the search field
          self.hideSearch(elem);
        }
      }
    });

    // this.$searchInput.on("typeahead:active", function() {
    //     //if a search has previously been made, and the search-field has been de-focused, remove old results.
    // });
    // this.$el is the inputfield for the searchbar
    this.$el.on('typeahead:render', function() {
      if (self.$twitterParent.find('.tt-open.tt-empty').length > 0) {
        //no results
        //console.log("open, no results, remove class");
        self.$el.parent().removeClass('awesomplete-open');
      } else {
        //console.log("open, has results, add class");
        self.$el.parent().addClass('awesomplete-open');

        // This is used because the col-in-viewport has a transform property put on because of animate-in transition, that causes
        // the dropdown to be hidden behind the preceeding row.
        // therefor we remove the .col's transition and transform first time its clicked.
        if (self.$el.parents('.col-in-viewport-inited').length !== 0) {
          if (
            !self.$el
              .parents('.col-in-viewport-inited')
              .hasClass('col-in-viewport-notransform')
          ) {
            self.$el
              .parents('.col-in-viewport-inited')
              .addClass('col-in-viewport-notransform');
          }
        }
      }
    });
    this.$el.on('typeahead:close', function() {
      //console.log("close");
      self.$el.parent().removeClass('awesomplete-open');
      //console.log("emptying");
      self.$twitterParent.find('.tt-dataset.tt-dataset-List').empty(); //Comment this out if you want to keep the results in the DOM (for debugging)
    });

    this.$el.on('awesomplete-open', function() {
      self.$el.parent().addClass('awesomplete-open');
    });
    this.$el.on('awesomplete-close', function() {
      self.$el.parent().removeClass('awesomplete-open');
    });
  }

  // Hide Search Field
  // Subject to change.
  hideSearch(elem) {
    $(elem)
      .find('input')
      .fadeOut('fast');
    $(elem)
      .delay(300)
      .animate({
        width: '41px'
      })
      .delay(300)
      .queue(function() {
        $(elem)
          .addClass('hidden')
          .dequeue();
      });
  }

  // Show Search Field
  showSearch(elem, width) {
    $(elem)
      .stop()
      .removeClass('hidden')
      .delay(300)
      .animate({
        width: width
      });
    $(elem)
      .find('input')
      .stop()
      .delay(500)
      .fadeIn()
      .delay(200)
      .queue(function() {
        $(elem)
          .find('input')
          .focus()
          .dequeue();
      });
  }
}
