import isElement from "./isElement";

function getElement(el) {
  if (typeof el === "string") {
    return document.querySelector(el);
  }

  if (el instanceof NodeList) {
    return el[0];
  }

  if (isElement(el)) {
    return el;
  }

  if (el && el.nodeType === Node.ELEMENT_NODE) {
    return el;
  }

  return null;
}

export default getElement; 
