/**
 * Class: ExpandCollapseRectangleItem
 * Description: Her goes description
 */
//import {m, utils} from '../../js/main';
import { emitter } from '../base/emitter/emitter';
export default class ExpandCollapseRectangleItem {
    /**
     * @param {number} param this is param.
     * @return {number} this is return.
     */
    constructor(config) { // put in defaults here
        
    }

    init(contextElement) {
       
        this.$toggle = contextElement.find('.js-rectangle-presentation-expand-toggle');
        
        // clickhandler bind
        this.$toggle.on('click', function(e) {
            e.preventDefault();
           
            var contextitem = $($(this).parents(".rectangle-presentation-item"));
            
            if (contextitem.hasClass('is-expanded')) {
                contextitem.removeClass('is-expanded');
                $(this).text($(this).attr('data-open'));
            } else {
                contextitem.addClass('is-expanded');
                $(this).text($(this).attr('data-close'));
            }
        });
    }    
}

