/**
 * Video Class
 * - Video fallback resizing
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { $win } from '../base/utils/utils';

export default class Video {
  constructor(config) {
    this.config = $.extend(
      {
        rootEl: $('body')
      },
      config
    );
    this.init();

    // references: https://stackoverflow.com/a/24869361, https://developers.google.com/youtube/player_parameters?hl=en
    window.onYouTubeIframeAPIReady = function() {
      let YT = window.YT || null;
      $('.teaser-video-iframe').each(function(/* index, value */) {
        var iframe = $(this);
        var player = new YT.Player(iframe.attr('id'), {
          events: {
            onReady: onPlayerReady,
            onStateChange: onPlayerStateChange
          },
          playlist: ''
        });

        function onPlayerReady() {
          if (iframe.data('muted') == '1') {
            player.mute();
          }
          if (iframe.data('autoplay') == '1') {
            player.playVideo();
          }
        }

        emitter.emit('video/youtube-ready');

        function onPlayerStateChange(e) {
          if (iframe.data('loop') == '1' && e.data === YT.PlayerState.ENDED) {
            player.playVideo();
          }
        }
      });
    };
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    // Video height hacks. Sitecore <video>'s height needs to be adjusted (youtube videos are fine).
    // Flooring the pixel height to avoid overspill and errors in rendering
    var self = this;

    // Trigger a better responsive handling of the iframe video
    self.config.rootEl.find('.yt-embed__iframe').height('100%');
    if ($('.teaser-video-iframe').length) {
      Video.loadYoutubeApi();
    }
    $win.on('ready', function() {
      Video.resizeParent(self.config.rootEl);
    });

    emitter.on('resize', function() {
      Video.resizeParent(self.config.rootEl);
    });

    $win.on('timeline-load', function() {
      Video.resizeParentTimelineLoad(self.config.rootEl);
    });

    $(document).on(
      'click touchstart',
      '.video-click-playpause .vjs-tech',
      function() {
        this.paused ? this.play() : this.pause();
      }
    );

    $(document).ready(function() {
      Video.hidePlayerButton(self.config.rootEl);
    });
  }

  static loadYoutubeApi() {
    var tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/player_api';
    var firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
  }

  static hidePlayerButton(rootEl) {
    rootEl.find('.vjs-tech').each(function() {
      var self = $(this);
      var parent = self.parent();

      //  fix to hide player button in firefox and IE edge if autoplay is true
      if (
        self.get(0).autoplay &&
        !self.get(0).paused &&
        !parent.hasClass('vjs-has-started')
      ) {
        console.info('hidding big play button');
        parent.addClass('vjs-has-started');
      }
    });
  }

  static resizeParent(rootEl) {
    rootEl.find('.teaser-video').each(function() {
      var self = $(this);

      if (!self.hasClass('infographic__video')) {
        var videoHeight = Math.floor(
          $(this)
            .closest('.teaser-media')
            .height()
        );
        $(this).css('min-height', videoHeight);
      }
    });
  }

  static resizeParentTimelineLoad(rootEl) {
    rootEl.find('video.teaser-video').each(function() {
      var self = $(this);
      if (!self.hasClass('infographic__video')) {
        setTimeout(function() {
          var videoHeight = Math.floor(self.height());
          self.parent('.teaser-video-container').css('height', videoHeight);
        }, 1000);
      }
    });
  }
}
