/**
 * Class: HeaderClassic
 * Description: Her goes description
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';
import MegaNavigation from '../megaNavigation/megaNavigation';

export default class HeaderClassic {
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.headerClassic'
      },
      config
    );
    this.$el = $(this.config.el);
    //headervars
    this.offsetYClass = 'header-offsetY';
    this.$navigation = this.$el.find('.megaNavigation');
    this.headerClassicTop = this.$el.find('.headerClassic-top');
    this.FIREFOX = /Firefox/i.test(navigator.userAgent);
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    if (
      !utils.$body.hasClass('is-experience-editing') &&
      !utils.$body.hasClass('is-experience-preview')
    ) {
      $('.headerClassic-spacer').height($(this.$el).height());
    }
    this.bindEvents();
    this.megaNavigation = new MegaNavigation({
      //FIXME: before, the megamenu was saved globally. consider making it singleton if we need it: = m.megaNavigation
      el: '.megaNavigation'
    });
    utils.fixExperienceEditorNavigation(this.$el);
  }
  bindEvents() {
    var self = this;

    emitter.on('responsive/sizechange', function(size) {
      if (size == 'small') {
        self.onScrollUp();
      }
    });
    emitter.on('scroll', function(obj) {
      let isMobile = responsive.getSize() === 'small';
      let hasQuickLinks = self.headerClassicTop.find('a').length;
      if (Math.abs(obj.direction) > 1 && !isMobile && hasQuickLinks) {
        if (obj.direction > 0) {
          self.onScrollUp();
        } else if (obj.direction < 0 && obj.top > 40) {
          self.onScrollDown();
        }
      }
    });

    emitter.on('resize', function(/* size */) {
      if (
        !utils.$body.hasClass('is-experience-editing') &&
        !utils.$body.hasClass('is-experience-preview')
      ) {
        $('.headerClassic-spacer').height($(self.$el).height());
      }
    });
  }
  onScrollUp() {
    if (this.FIREFOX) {
      //fix for firefox menu displacement when scrolling
      var self = this;
      var top =
        $('.header')
          .first()
          .height() - 33;
      $('.active-sub').each(function() {
        var itm = $(this)
          .find('.megaNavigation-submenu')
          .first();
        self.setParentTransition(
          itm,
          'top',
          '200ms',
          'cubic-bezier(0, 0.27, 0.215, 0.975)',
          function() {
            itm.css('top', top);
          }
        );
      });
    }

    utils.$body.removeClass(this.offsetYClass);
  }
  onScrollDown() {
    if (!utils.$body.hasClass('megaNavigation-menu-visible')) {
      if (this.FIREFOX) {
        //fix for firefox menu displacement when scrolling
        var self = this;
        var top =
          $('.header-megaNavigation')
            .first()
            .height() + 1;
        $('.active-sub').each(function() {
          var itm = $(this)
            .find('.megaNavigation-submenu')
            .first();
          self.setParentTransition(
            itm,
            'top',
            '200ms',
            'cubic-bezier(0, 0.27, 0.215, 0.975)',
            function() {
              itm.css('top', top);
            }
          );
        });
      }
      utils.$body.addClass(this.offsetYClass);
    }
  }

  setParentTransition(id, prop, delay, style, callback) {
    $(id).css({ '-webkit-transition': prop + ' ' + delay + ' ' + style });
    $(id).css({ '-moz-transition': prop + ' ' + delay + ' ' + style });
    $(id).css({ '-o-transition': prop + ' ' + delay + ' ' + style });
    $(id).css({ transition: prop + ' ' + delay + ' ' + style });
    callback();
  }
}
