const noop = () => { };

const defaultOptions = {
  swipeLeft: noop,
  swipeRight: noop,
  swipeUp: noop,
  swipeDown: noop
};

function getTouches(evt) {
  return evt.touches;
}

function handleTouchStart(evt) {
  const firstTouch = getTouches(evt)[0];
  this.xDown = firstTouch.clientX;
  this.yDown = firstTouch.clientY;
};

function handleTouchMove(evt) {
  if (!this.xDown || !this.yDown) {
    return;
  }

  var xUp = evt.touches[0].clientX;
  var yUp = evt.touches[0].clientY;

  var xDiff = this.xDown - xUp;
  var yDiff = this.yDown - yUp;

  if (Math.abs(xDiff) > Math.abs(yDiff)) {/*most significant*/
    if (xDiff > 0) {
      this.options.swipeLeft(this, evt);
    } else {
      this.options.swipeRight(this, evt);
    }
  } else {
    if (yDiff > 0) {
      this.options.swipeUp(this, evt);
    } else {
      this.options.swipeDown(this, evt);
    }
  }

  /* reset values */
  this.xDown = null;
  this.yDown = null;
};

function bindEvents() {
  if (this.$el.addEventListener) {
    this.xDown = null;
    this.yDown = null;

    this.$el.addEventListener('touchstart', handleTouchStart.bind(this), false);
    this.$el.addEventListener('touchmove', handleTouchMove.bind(this), false);
  }
}

class SwipeEvents {
  constructor($el, _options) {
    this.$el = $el;
    this.options = {
      ...defaultOptions,
      ..._options
    };

    bindEvents.bind(this)();
  }
}

export default SwipeEvents;
