/**
 * Class: Twitter
 * Description: Her goes description
 */
import $ from 'jquery';
import { $win } from '../base/utils/utils';

export default class Twitter {
  constructor(config) {
    this.config = $.extend({}, config);
    this.$el = $(this.config.el);
    this.init();
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;
    if (window.twttr) {
      window.twttr.ready(function(twttr) {
        twttr.events.bind('loaded', function() {
          self.resizeTwitterIframe();
        });
      });
      $win.resize(function() {
        self.resizeTwitterIframe();
      });
    }
  }

  resizeTwitterIframe() {
    var width = $('.twitter-feed').width();
    var widthstring = width + 'px';
    $('.twitter-feed iframe').css('width', widthstring);
  }
}
