/**
 * Class: JobList
 */
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions, $win } from '../base/utils/utils';

export default class JobList {
  constructor(config) {
    this.sets = {
      pagesize: 10,
      offset: 0,
      item: null,
      apiAddress: '',
      domOrder: '',
      createdRow: function(row) {
        $(row).addClass('list-items-item');
      },
      columnDefs: [
        {
          targets: [0],
          className: 'list-items-item-column  list-items-item-column-haslink',
          searchable: true,
          render: function(data, type, row) {
            return (
              "<a class='list-items-item-column-link' href='" +
              row['Link'] +
              "'></a><span class='list-items-item-value list-items-item-value-type-text'>" +
              row['JobName'] +
              '<span>'
            );
          },
          defaultContent: ''
        },
        {
          targets: [1],
          className: 'list-items-item-column  list-items-item-column list-items-item-column-hide-md',
          render: function(data, type, row) {
            return (
              "<span class='list-items-item-value list-items-item-value-type-text'>" +
              row['AreaOfWork'] +
              '</span>'
            );
          },
          defaultContent: ''
        },
        {
          targets: [2],
          className: 'list-items-item-column  list-items-item-column list-items-item-column-hide-md',
          render: function(data, type, row) {
            return (
              "<span class='list-items-item-value list-items-item-value-type-text'>" +
              row['EmploymentType'] +
              "</span>"
            );
          },
          defaultContent: ''
        },
        {
          targets: [3],
          className: 'list-items-item-column  list-items-item-column',
          render: function(data, type, row) {
            return (
              "<span class='list-items-item-value list-items-item-value-type-text'>" +
              row['PositionLocation'] +
              '</span>'
            );
          },
          defaultContent: ''
        },
        {
          targets: [4],
          className: 'list-items-item-column  list-items-item-column list-items-item-column-hide-md',
          render: function(data, type, row) {
            return (
              "<span class='list-items-item-value list-items-item-value-type-text'>" +
              row['PositionWorkPlace'] +
              '</span>'
            );
          },
          defaultContent: ''
        },
        {
          targets: [5],
          className: 'list-items-item-column  list-items-item-column list-items-item-column-hide-md',
          render: function(data, type, row) {
            var dateFromated = moment(row['ApplicationDue']).format(
              DateFormatingOptions.jobdateFormat
            );
            if (dateFromated == "31.12.9999") {
              dateFromated="Ongoing"
            }
            return (
              "<span class='list-items-item-value list-items-item-value-type-text'>" +
              dateFromated +
              '</span>'
            );
          },
          defaultContent: ''
        }
      ],
      language: {
        paginate: {
          next:
            '<span aria-hidden="true"></span><span class="sr-only">Next</span>',
          previous:
            '<span aria-hidden="true"></span><span class="sr-only">Previous</span>'
        },
        emptyTable: 'No data available in table',
      },
      resultCountTemplate: ''
    };

    this.dataTableElem = null;

    this.config = $.extend(
      {
        el: '.job-module',
        table: '.job-list',
        filters: '.job-filters',
        areaOfWork: '.job-filter-areaOfWork',
        employmentType: '.job-filter-employmentType',
        country: '.job-filter-country',
        workplace: '.job-filter-workplace',
        search: '.job-filters-search',
        header: 'header',
        resultCount: '#result-count',
      },
      config
    );

    this.$el = $(this.config.el);
    this.$table = this.$el.find(this.config.table);
    this.$filters = $(this.config.filters);

    this.$areaOfWork = this.$filters.find(this.config.areaOfWork);
    this.$employmentType = this.$filters.find(this.config.employmentType);
    this.$country = this.$filters.find(this.config.country);
    this.$workplace = this.$filters.find(this.config.workplace);
    this.$search = this.$filters.find(this.config.search);
    this.$header = $(this.config.header);
    this.$resultCount = $(this.config.resultCount);

    this.triggerWorkplace = true;

    this.jobFiltersName = ['cat', 'type', 'ctry'];

    if (this.$el.length) {
      this.init();
    }
  }

  init() {
    let JobListOptions = window.JobListOptions || {};
    $.extend(true, this.sets, JobListOptions);
    $.extend(true, this.sets, {
      columnDefs: [
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[0]);
          }
        },
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[1]);
          }
        },
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[2]);
          }
        },
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[3]);
          }
        },
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[4]);
          }
        },
        {
          createdCell: function(td) {
            $(td).attr('data-label', JobListOptions.columnDataLabels[5]);
          }
        }
      ]
    });
    this.bindEvents();
    this.assignFiltersFromHash();
  }

  initDataTable() {
    if ($win.width() > 420 && $win.width() < 768) {
      $.fn.dataTable.ext.pager.numbers_length = 6;
    } else if ($win.width() > 330 && $win.width() < 420) {
      $.fn.dataTable.ext.pager.numbers_length = 5;
    } else if ($win.width() <= 330) {
      $.fn.dataTable.ext.pager.numbers_length = 4;
    }

    var oldStart = 0;
    var that = this;
    this.dataTableElem = this.$table.DataTable({
      ordering: true,
      info: false,
      stateSave: true,
      bLengthChange: false,
      processing: false,
      responsive: true,
      serverSide: true,
      searching: false,
      ajax: {
        type: 'post',
        contentType : 'application/json',
        url: this.sets.apiAddress,
        data: function(d) {
          d.offset = that.sets.offset;
          d.item = that.sets.item;
          d.country = that.$country[0].value.length > 0 ? that.$country[0].value.join(',') : '';
          d.category = that.$areaOfWork[0].value.length > 0 ? that.$areaOfWork[0].value.join(',') : '';
          d.jobtitle = that.$search.val();
          d.workplace = that.$workplace[0].value.length > 0 ? that.$workplace[0].value.join(',') : '';
          d.employmenttype = that.$employmentType[0].value.length > 0 ? that.$employmentType[0].value.join(',') : '';
          d.countryFilterID = that.sets.countryFilterID;

          return JSON.stringify(d);
        },
        dataSrc: this.sets.dataSrcFunction
      },
      pageLength: this.sets.pagesize,
      dom: this.sets.domOrder,
      createdRow: this.sets.createdRow,
      columnDefs: this.sets.columnDefs,
      language: this.sets.language,
      fnServerParams: function(data) {
        // Now you can get columns in backend by names
        data['order'].forEach(function(items, index) {
          data['order'][index]['column'] =
            data['columns'][items.column]['data'];
        });
      },
      drawCallback: function(settings) {
        const resultsCount = settings.json.data.length;

        if (resultsCount === 0) {
          that.$table.addClass('no-results');
        } else {
          that.$table.removeClass('no-results');
        }

        that.$resultCount.text(that.sets.resultCountTemplate.replace('{0}', resultsCount));

        if (settings._iDisplayStart != oldStart) {
          var targetOffset = that.$filters.offset().top - that.$header.height();
          $('html,body').animate({ scrollTop: targetOffset }, 500);
          oldStart = settings._iDisplayStart;
        }

        that.$country.trigger('update-dropdown-position');
      }
    });
  }

  isBackwardCompatible(hashes) {
    return hashes.indexOf('=') === -1;
  }

  assignFiltersFromHash() {
    const hashes = window.location.hash;
    if (hashes.indexOf('#') !== 0) {
      return;
    }

    const params = hashes.substr(1).split('&');

    if (this.isBackwardCompatible(hashes)) {
      this.jobFiltersName.forEach(name => {
        const keypairs = params.find(p => p.indexOf(name) === 0);

        if (keypairs) {
          this.$filters.find(`.${name}`).trigger('select-data', { options: [keypairs.substr(name.length)] });
        }
      })
    } else {
      this.jobFiltersName.forEach(name => {
        const keypairs = params.find(p => p.indexOf(name) === 0);

        if (keypairs) {
          const values = keypairs.split('=');
          this.$filters.find(`.${name}`).trigger('select-data', { options: values[1].split(',') });
        }
      })
    }
  }

  updateActiveUrl() {
    let hash = '';

    this.jobFiltersName.forEach((name, index) => {
      const filter = this.$filters.find(`.${name}`);

      if (filter.length > 0) {
        const $selected = filter.find('option:selected');
        let selectedIds = [];

        if ($selected.length > 0) {
          hash += `${name}=`;

          $selected.each((index, option) => {
            selectedIds.push(option.dataset['id']);
          })

          hash += selectedIds.join(',') + '&';
        }
      }
    });

    history.replaceState(undefined, undefined, '#' + hash);
  }

  debounce(func, wait = 300) {
    let timeout;

    return function(...args) {
      clearTimeout(timeout);

      timeout = setTimeout(() => {
        func.apply(this, args);
      }, wait);
    };
  }

  search() {
    this.dataTableElem.draw();
  }

  filter() {
    this.dataTableElem.draw();
    this.updateActiveUrl();
  }

  bindEvents() {
    const self = this;
    const searchDebounce = this.debounce(this.search.bind(this), 300);

    $(document).ready(function () {
      self.initDataTable();

      self.dataTableElem.on('xhr.dt', function (e, settings, json, xhr) {
        const hasLocationResult = (json && typeof json.locations !== 'undefined' && json.locations.length > 0);
        const locations = [];
        
        self.$workplace[0].disabled = !hasLocationResult;

        if (hasLocationResult) {
          json.locations.forEach((l, index) => {
            locations.push({
              text: l.locationName,
              value: l.locationName,
              data: {
                id: index + 1
              }
            })
          })
        }

        // trigger on page load only
        if (self.triggerWorkplace) {
          self.$workplace.trigger('init-options-data', { options: locations })
        }
        
        if (self.jobFiltersName.indexOf('loc') === -1) {
          self.jobFiltersName.push('loc');
          self.assignFiltersFromHash();
          self.dataTableElem.draw();
        } else if (self.triggerWorkplace) {
          self.updateActiveUrl();
          self.dataTableElem.draw();
        }
        
        self.triggerWorkplace = false;
      });
    });

    this.$areaOfWork.on('change', function() {
      self.filter();
    });

    this.$employmentType.on('change', function () {
      self.filter();
    });

    this.$country.on('change', function() {
      self.triggerWorkplace = true;
      self.$workplace.trigger('init-options', { options: [] })
      self.filter();
    });

    this.$workplace.on('change', function() {
      self.filter();
    });

    this.$search.on('input', function(e) {
      if (e.target.value.length > 3 || e.target.value.length === 0) {
        searchDebounce();
      }
    })
  }
}
