if (window.app !== undefined) {
    window.app.directive("flowProgressbar", function () {
        return {
            restrict: 'A',
            require: ['^flow'],
            scope: true,
            link: function (scope, element, attrs, ctrls) {
                var flow = ctrls[0];

                scope.state = flow.state;

                scope.NavigateTo = function (step) {
                    return flow.NavigateToStep(step);
                }
            }
        }
    });
}