/**
 * Class: HeaderSearch
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { $win } from '../base/utils/utils';

export default class HeaderSearch {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.headerSearch'
      },
      config
    );

    this.$el = $(this.config.el);

    this.$search = this.$el;

    this.$corporate$searchForm = this.$search.parent('.header-search-ov');

    if (this.$corporate$searchForm.length == 0) {
      this.$searchClose = this.$el.find('.headerSearch-close');
      this.$searchIcon = this.$el.find('.headerSearch-icon');
      // this.$searchForm = this.$search.find('.headerSearch-form');
      this.$searchForm = this.$el.parent('.header-search');
      this.$searchFormInput = this.$searchForm.find('input');
      this.$searchInput = this.$el.find('.headerSearch-input');
    } else {
      this.$searchFormInput = this.$corporate$searchForm.find('input');
    }
    if (this.$el.length) {
      this.init();
    }
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var self = this;
    var searchFormInput = this.$searchFormInput;

    if (this.$corporate$searchForm.length == 0) {
      this.$searchIcon.on({
        click: function(e) {
          if (searchFormInput.val() === '') {
            e.preventDefault();
            self.showSearch();
          } else {
            self.createSearchRequest();
          }
        }
      });
      this.$searchIcon.keyup(function(e) {
        if (e.keyCode == 32) {
          // user has pressed space, show the search-box
          self.showSearch();
          e.stopPropagation();
        }
      });
      this.$search.on({
        click: function(e) {
          e.stopPropagation();
        }
      });
      this.$searchClose.on({
        click: function(/* e */) {
          self.hideSearch();
        }
      });
      this.$searchFormInput.on({
        blur: function(/* e */) {
          self.hideSearch();
        }
      });
      emitter.on('keyEvent', function(key) {
        if (key == 'escape') {
          self.hideSearch();
        }
      });

      this.$searchForm.on({
        submit: function(e) {
          e.preventDefault();
          self.createSearchRequest();
          return false;
        }
      });
    } else {
      this.$corporate$searchForm.on({
        submit: function(e) {
          e.preventDefault();
          self.createSearchRequest();
          return false;
        }
      });
    }
  }

  showSearch() {
    var self = this;
    this.$search.addClass('headerSearch-visible');
    // this.$searchIcon.hide();
    // this.$searchForm.show();
    this.$searchFormInput.focus();
    $win.one({
      click: function(/* e */) {
        self.hideSearch();
      }
    });
  }

  hideSearch() {
    this.$search.removeClass('headerSearch-visible');
    // this.$searchIcon.show();
    // this.$searchForm.hide();
  }

  createSearchRequest() {
    document.cookie = 'RedrawTable=true';
    document.location = '/search?search=' + this.$searchFormInput.val();
  }
}
