/**
 * Class: Expand
 * Description: Her goes description
 */
import $ from 'jquery';
import * as utils from '../base/utils/utils';

export default class Expand {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.expand'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$trigger = this.$el.find('.expand-trigger');
    this.$content = this.$el.find('.expand-content');
    this.$content.data('height', utils.actualSize(this.$content));
    this.isVisible = false;
    this.classVisible = 'expand-content-visible';

    if (this.$el.length) {
      this.init();
      this.$el.data('module', this);
    }
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;
    this.$trigger.on({
      click: function(/* e */) {
        if (self.isVisible) {
          self.close();
        } else {
          self.open();
        }
      }
    });
  }
  close() {
    this.$el.removeClass(this.classVisible);
    this.$content.css('height', '');
    this.isVisible = false;
  }
  open() {
    this.$el.addClass(this.classVisible);
    var height = utils.actualSize(this.$content).height;
    this.$content.height(height);
    this.isVisible = true;
  }
}
