import {
  createElementFromHTML,
  debounce,  
  getDevice,  
  getElement,
  isElementInViewport,
  isExperienceEditing,
  scrollTo,
  setupListenerOnViewportHeight
} from "../base/utils/utils";
import Modernizr from "modernizr";
import VideoBox from "../videobox/videobox";
import SwipeEvents from "../swipe-events/swipe-events";

const className = {
  videos: 'video-carousel__videos',
  video: 'video-carousel__video',
  videoShow: 'video-carousel__video--show',
  list: 'video-carousel__list',
  item: 'video-carousel__list-item',
  content: 'video-carousel__content',
  contentActive: 'video-carousel__content--active',
  contentInactive: 'video-carousel__content--inactive',
  itemActive: 'video-carousel__list-item--active',
  itemInactive: 'video-carousel__list-item--inactive',
  itemWrapper: 'video-carousel__item-wrapper',
  listNextBtn: 'video-carousel__button-next',
  listPrevBtn: 'video-carousel__button-prev',
  progressBar: 'video-carousel__progressbar-playbar',
  progressBarNoTransition: 'video-carousel__progressbar-playbar--no-transition',
  itemThumbnail: 'video-carousel__item-thumbnail',
  thumbnailImage: 'video-carousel__thumbnail-image',
  buttonToaster: 'video-carousel__button-toaster'
}

const defaultOptions = {
  maxVideoDuration: 10,
  offsetNavigation: '.headerSlider-top'
};

const stopVideo = (video) => {
  video.pause();
  if (video.currentTime) {
    video.currentTime = 0;
  }
  return video;
}

function videoErrorHandler(item) {
  const duration = +this.options.maxVideoDuration * 1000;
  const $progressBar = item.item.querySelector(`.${className.progressBar}`);

  let start;
  let percentage = 0;

  function step(timestamp) {
    if (start === undefined) start = timestamp;
    const elapsed = timestamp - start;
    percentage = Math.floor((100 * elapsed) / duration);

    if (elapsed < duration) {
      // Stop the animation after 2 seconds
      $progressBar.style.transform = `scaleX(${percentage/100})`;
      item.videoErrorTimer = window.requestAnimationFrame(step.bind(this));
    } else {
      percentage = 0;
      nextItem.bind(this)();
    }
  }

  item.videoErrorTimer = window.requestAnimationFrame(step.bind(this));
}

function initVideo(selectedItem) {
  const { video, item } = selectedItem;

  video.muted = true;
  if (typeof video.disablePictureInPicture === 'boolean') {
    video.disablePictureInPicture = true;
  }
  video.autoplay = false;
  video.setAttribute('poster', video.dataset.poster);
  video.src = video.dataset.src;

  function videoTimeUpdateHandler(e) {
    if (video.error) {
      selectedItem.videoError = true;
      videoErrorHandler.bind(this)(selectedItem);
      return;
    }

    const $progressBar = item.querySelector(`.${className.progressBar}`);
    let videoDuration = video.duration;
    if (this.options.maxVideoDuration) {
      videoDuration = video.duration < +this.options.maxVideoDuration ? video.duration : +this.options.maxVideoDuration;
    }
    let percentage = Math.floor(100 * video.currentTime / videoDuration);
    if (percentage >= 100) {
      percentage = 100;
      $progressBar.style.transform = `scaleX(${percentage/100})`;
      stopVideo(video);
      nextItem.bind(this)();
    } else {
      $progressBar.style.transform = `scaleX(${percentage/100})`;
    }
  }

  video.addEventListener('timeupdate', videoTimeUpdateHandler.bind(this), false);

  selectedItem.hasInited = true;
}

function pauseItem() {
  this.currentItem.video.pause();
}

function playItem() {
  this.currentItem.video.play();
}

function nextItem() {
  selectItem.bind(this)(this.currentItemIndex === this.itemList.length - 1 ? 0 : this.currentItemIndex + 1);
}

function prevItem() {
  selectItem.bind(this)(this.currentItemIndex === 0 ? this.itemList.length - 1 : this.currentItemIndex - 1);
}

function selectItem(index) {
  if (!this.progressBarRemove) {
    clearTimeout(this.progressBarRemove);
  }

  if (!this.contentRemove) {
    clearTimeout(this.contentRemove);
  }

  if (!this.itemRemove) {
    clearTimeout(this.itemRemove);
  }

  const selectedItem = this.itemList[index];
  const { currentItem } = this;

  if (currentItem.videoError) {
    window.cancelAnimationFrame(currentItem.videoErrorTimer);
  }

  if (selectedItem.videoError) {
    videoErrorHandler.bind(this)(selectedItem);
  }

  currentItem.item.classList.remove(className.itemActive);
  currentItem.item.classList.add(className.itemInactive);
  currentItem.video.classList.remove(className.videoShow);
  currentItem.content.classList.remove(className.contentActive);
  currentItem.content.classList.add(className.contentInactive);
  const $progressBar = currentItem.item.querySelector(`.${className.progressBar}`);
  $progressBar.classList.add(className.progressBarNoTransition);
  $progressBar.style.transform = "scaleX(0)";

  function removeProgressBarHandler() {
    $progressBar.classList.remove(className.progressBarNoTransition);
    this.progressBarRemove = null;
  }

  function removeContentHandler() {
    currentItem.content.classList.remove(className.contentInactive);
    this.contentRemove = null;
  }

  function removeItemHandler() {
    currentItem.item.classList.remove(className.itemInactive);
    this.itemRemove = null;
  }

  this.progressBarRemove = setTimeout(removeProgressBarHandler.bind(this), 100);
  this.contentRemove = setTimeout(removeContentHandler.bind(this), 400);
  this.itemRemove = setTimeout(removeItemHandler.bind(this), 400);

  stopVideo(currentItem.video);
  
  if (!selectedItem.hasInited) {
    initVideo.bind(this)(selectedItem);
  }
  selectedItem.item.classList.add(className.itemActive);
  selectedItem.video.classList.add(className.videoShow);
  selectedItem.content.classList.add(className.contentActive);
  this.currentItemIndex = index;
  this.currentItem = selectedItem;
  this.swiper.slideTo(this.currentItemIndex);

  if (isElementInViewport(this.$el)) {
    if (isExperienceEditing()) {
      pauseItem.bind(this)();
      return;
    }
    playItem.bind(this)();
  }
}

function itemClickHandler(e) {
  const clickedItem = e.currentTarget;
  if (clickedItem.classList.contains(className.itemActive)) return;
  const selectedItemIndex = this.itemList.findIndex(data => data.item === clickedItem);
  if (selectedItemIndex === this.currentItemIndex) return;

  const elBound = this.$el.getBoundingClientRect();

  const position = () => {
    return (
      document.documentElement.scrollTop ||
      document.body.parentNode.scrollTop ||
      document.body.scrollTop
    );
  }

  let offset = 0;
  const $nav = getElement(this.options.offsetNavigation);
  if ($nav) {
    offset = $nav.clientHeight;
  }
  scrollTo(position() + elBound.top - offset, 400);
  selectItem.bind(this)(selectedItemIndex);
}

function videoBoxOnOpenHandler() {
  pauseItem.bind(this)();
}

function videoBoxOnCloseHandler() {
  nextItem.bind(this)();
}

function mapItem(item, index) {
  const { $el } = this;
  const video = $el.querySelector(item.dataset.video);
  const content = $el.querySelector(item.dataset.content);

  if (isExperienceEditing()) {
    const btnToChange = createElementFromHTML(`<button class="xp-vc-select-btn" style="display: block; position: absolute; bottom: 0; z-index: 9999; height: 20px; left: 50%; transform: translateX(-50%); line-height: 20px; font-size: 12px; cursor: pointer;">Select item</button>`);
    item.appendChild(btnToChange);
    const thisScope = this;
    btnToChange.addEventListener('click', (e) => {
      const selectedItem = e.currentTarget.closest(`.${className.item}`);
      const selectedItemIndex = this.itemList.findIndex(data => data.item === selectedItem);
      if (selectedItemIndex === this.currentItemIndex) return;
      selectItem.bind(thisScope)(selectedItemIndex);
    }, false);

    item.querySelector(`.${'video-carousel__item-title'} span.scWebEditInput`).addEventListener('click', (e) => {
      e.target.focus();
    });
    item.querySelector(`.${'video-carousel__item-subtitle'} span.scWebEditInput`).addEventListener('click', (e) => {
      e.target.focus();
    })
  } else {
    item.addEventListener('click', itemClickHandler.bind(this), false);
  }

  const $itemWrapper = item.querySelector(`.${className.itemWrapper}`);
  $itemWrapper.style.left = `-${index * 100}%`;

  let videoBox = null;
  if (isExperienceEditing()) {
  } else {
    const $buttonToaster = content.querySelector(`.${className.buttonToaster}`);
    videoBox = new VideoBox({
      el: $buttonToaster,
      ...$buttonToaster.dataset,
      ... {
        onOpen: videoBoxOnOpenHandler.bind(this),
        onClose: videoBoxOnCloseHandler.bind(this)
      }
    });
  }

  return {
    item: item,
    video,
    content,
    videoBox
  };
};

function fallback() {
  let isLegacyEdge = getDevice() === "MS Edge (EdgeHtml)";

  if (!Modernizr.objectfit || isLegacyEdge) {
    const $thumbnailNodes = this.$el.querySelectorAll(`.${className.itemThumbnail}`);
    $thumbnailNodes.forEach($thumbnail => {
      const $thumbnailImg = $thumbnail.querySelector(`.${className.thumbnailImage}`);
      const imgUrl = $thumbnailImg.src;
      if (imgUrl) {
        $thumbnail.style.backgroundImage = `url("${imgUrl}")`;
      }
    });
  }
}

function initVideoCarousel() {
  const { $el } = this;

  const $itemNodes = $el.querySelectorAll(`.${className.item}`);
  const $contentList = $el.querySelector(`.${className.list}`);

  this.itemList = [...$itemNodes].map(mapItem.bind(this));
  this.currentItemIndex = this.itemList.findIndex(data => data.item.classList.contains(className.itemActive));
  this.currentItemIndex = this.currentItemIndex < 0 ? 0 : this.currentItemIndex;
  this.currentItem = this.itemList[this.currentItemIndex];

  this.swiper = new Swiper($contentList, {
    slidesPerView: 4,
    slidesPerGroup: 4,
    loop: false,
    nextButton: $el.querySelector(`.${className.listNextBtn}`),
    prevButton: $el.querySelector(`.${className.listPrevBtn}`),
    followFinger: false,
    breakpoints: {
      768: {
        slidesPerView: this.itemList.length,
        slidesPerGroup: false
      }
    }
  });

  selectItem.bind(this)(this.currentItemIndex);
  fallback.bind(this)();
}

function onVisibilityChange(el, callback) {
  let old_visible;
  return debounce(function(e) {
    let visible = isElementInViewport(el);
    if (visible != old_visible) {
      old_visible = visible;
      if (typeof callback == 'function') {
        callback(e, old_visible);
      }
    }
  }, 50);
}

function getCurrentVideo() {
  return this.currentItem.video;
}

function scrollHandler() {
  return onVisibilityChange(this.$el, (e, isVisible) => {
    const $currentVideo = getCurrentVideo.bind(this)();

    if (isVisible) {
      if (isExperienceEditing()) {
        $currentVideo.pause();
        return;
      }
      $currentVideo.play();
    } else {
      $currentVideo.pause();
    }
  }).bind(this);
};

function handleVisibilityChange(e) {
  const $currentVideo = getCurrentVideo.bind(this)();
  const isVisible = document.visibilityState == "visible";

  if (isVisible) {
    if (isExperienceEditing()) {
      $currentVideo.pause();
      return;
    }
    $currentVideo.play();
  } else {
    $currentVideo.pause();
  }
}

function swipeLeftHandler() {
  if (this.itemList.length < 2) return;
  nextItem.bind(this)();
}

function swipeRightHandler() {
  if (this.itemList.length < 2) return;
  prevItem.bind(this)();
}

function setupEventListener() {
  if (window.addEventListener) {
    setupListenerOnViewportHeight();
    addEventListener('DOMContentLoaded', scrollHandler.bind(this)(), false);
    addEventListener('load', scrollHandler.bind(this)(), false);
    addEventListener('scroll', scrollHandler.bind(this)(), false);
    addEventListener('resize', scrollHandler.bind(this)(), false);

  }
  
  if (document.addEventListener) {
    document.addEventListener('visibilitychange', handleVisibilityChange.bind(this), false);
  }

  new SwipeEvents(this.$el, {
    swipeLeft: swipeLeftHandler.bind(this),
    swipeRight: swipeRightHandler.bind(this)
  });
}

export default class VideoCarousel {
  constructor(options) {
    this.options = { ...defaultOptions, ...options };

    this.$el = getElement(this.options.el);

    if (!this.$el) {
      console.warn('There is no valid node');
      return;
    }
    
    // Exposing this object on window
    if (typeof window.oVideoCarousel === 'undefined' ) {
      window.oVideoCarousel = [];  
    }
    window.oVideoCarousel.push(this);


    initVideoCarousel.bind(this)();
    setupEventListener.bind(this)();
  }

  next() {
    nextItem.bind(this)();
  }

  prev() {
    prevItem.bind(this)();
  }

  pause() {
    pauseItem.bind(this)();
  }

  play() {
    playItem.bind(this)();
  }
}
