// Fixing aria-labelleby and other accesibility issues (mostly when the aria-stuff needs to span multiple, or global, elements).
$(function() {
  $("section").each(function() {
    var $this = $(this);
    var headings = $this.find(
      "h1,h2,h3,.heroSlider-headline,.onward-tagline,.coloredContentBox-headline,.topTasks-task-link"
    );
    if (headings.length !== 0) {
      var guid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
          var r = (Math.random() * 16) | 0,
            v = c == "x" ? r : (r & 0x3) | 0x8;
          return v.toString(16);
        }
      );
      $(headings[0]).attr("id", guid);
      $this.attr("aria-labelledby", guid);
    }
  });
});

// BOOTSTRAP INITS
$(function() {
  // if($('[data-toggle="popover"]').length){
  // 	$('[data-toggle="popover"]').popover()
  // }
  // $("#customerDetailsModal").modal();
  $("[data-toggle=popover]").popover({
    html: true,
    content: function() {
      var content = $(this).attr("data-popover-content");
      return $(content)
        .children(".popover-body")
        .html();
    },
    title: function() {
      var title = $(this).attr("data-popover-content");
      return $(title)
        .children(".popover-heading")
        .html();
    }
  });

  $(document).on("click", function(e) {
    $('[data-toggle="popover"],[data-original-title]').each(function() {
      //the 'is' for buttons that trigger popups
      //the 'has' for icons within a button that triggers a popup
      if (
        !$(this).is(e.target) &&
        $(this).has(e.target).length === 0 &&
        $(".popover").has(e.target).length === 0 &&
        !e.target.classList.contains("showLogin")
      ) {
        (
          (
            $(this)
              .popover("hide")
              .data("bs.popover") || {}
          ).inState || {}
        ).click = false; // fix for BS 3.3.6
      }
    });
  });
});

