$(document).ready(function() {
  //Hide chart tooltip if clicked outside the tooltip
  //Mouse down because ng-click fires after mousedown, and it would hide the just opened tooltip if using click or mouseup
  $(document).mousedown(function(e) {
    var container = $(".custom-tooltip");

    if (!container.is(":visible")) return;

    if (
      !container.is(e.target) && // if the target of the click isn't the container...
      container.has(e.target).length === 0
    ) {
      // ... nor a descendant of the container
      container.hide();
    }
  });
});

window.shortYear = function(value) {
  if (typeof value !== "string") return value.toString().slice(-2);
  else return value.slice(-2);
};

