/**
 * Class: HeaderSlider
 * Description: Her goes description
 */
import $ from 'jquery';
import { emitter } from '../base/emitter/emitter';
import { responsive } from '../base/responsive/responsive';
import * as utils from '../base/utils/utils';
import MegaNavigation from '../megaNavigation/megaNavigation';

export default class HeaderSlider {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.headerSlider'
      },
      config
    );
    this.$el = $(this.config.el);
    this.offsetYClass = 'headerSlider-offsetY';
    this.$desktopTrigger = this.$el.find(
      '.headerSlider-navigation-trigger-desktop'
    );
    //this.$desktopTrigger = this.$el.find('.nav-trigger');
    this.$navigation = this.$el.find('.megaNavigation');
    this.menuVisibleClass = 'headerSlider-navigation-open';

    // menuItems
    this.$menuItems = this.$el.find('.megaNavigation-menu-item');
    this.$menuItemLinks = this.$el.find('.megaNavigation-menu-item-link');
    this.menuVisible = false;
    if (this.$el.length) {
      this.init();
    }
  }
  init() {
    this.megaNavigation = new MegaNavigation({
      //FIXME: before, the megamenu was saved globally. consider making it singleton if we need it: = m.megaNavigation
      el: '.megaNavigation',
      menuItemStayOnHover: true
    });
    this.bindEvents();
    utils.fixExperienceEditorNavigation(this.$el);
    utils.$body.addClass('has-slider-menu');
  }
  bindEvents() {
    var self = this;
    this.$el.keyup(function(e) {
      if (e.keyCode == 27) {
        e.preventDefault();
        self.hideDesktopMenu();
      }
    });
    this.$desktopTrigger.on({
      click: function(/* e */) {
        self.toggleDesktopMenu();
      }
    });
    this.$desktopTrigger.keyup(function(e) {
      if (e.keyCode == 32) {
        self.toggleDesktopMenu();
        e.stopPropagation();
      }
    });
    emitter.on('responsive/sizechange', function(size) {
      if (size == 'small') {
        // self.onScrollUp();
        self.hideDesktopMenu();
      } else {
        self.onScrollUp();
      }
    });
    // this.$searchInput.on("awesomplete-open", function() {
    // 	self.$searchInput.parent().addClass('awesomplete-open');
    // });
    // $searchInput.on("awesomplete-close", function() {
    // 	self.$searchInput.parent().removeClass('awesomplete-open');
    // });
    emitter.on('scroll', function(obj) {
      if (Math.abs(obj.direction) > 1 && responsive.getSize() == 'small') {
        if (obj.direction > 0) {
          self.onScrollUp();
        } else if (obj.direction < 0 && obj.top > 40) {
          self.onScrollDown();
        }
      }
    });
  }
  onScrollUp() {
    this.$el.removeClass(this.offsetYClass);
  }
  onScrollDown() {
    if (!this.$navigation.hasClass('megaNavigation-menu-visible')) {
      this.$el.addClass(this.offsetYClass);
    }
  }

  toggleDesktopMenu() {
    if (this.menuVisible) {
      this.hideDesktopMenu();
    } else {
      this.showDesktopMenu();
      this.showDesktopSubMenuSelctedLink();
    }
  }
  showDesktopMenu() {
    utils.disableBodyScroll();
    this.$el.addClass(this.menuVisibleClass);
    this.menuVisible = true;
  }
  hideDesktopMenu() {
    utils.enableBodyScroll();
    this.$el.removeClass(this.menuVisibleClass);
    this.menuVisible = false;
  }
  showDesktopSubMenuSelctedLink() {
    try {
      this.$menuItemLinks.parent().removeClass('active-sub');
      this.$menuItemLinks.removeClass('megaNavigation-submenu-visible');
      var navMenu = this.$el.find('.megaNavigation-menu');
      var animateSubMenuItem;
      this.$menuItems.each(function(menuItemindx, menuItem) {
        var isItemActive = false;
        if (!isItemActive) {
          $(menuItem)
            .find('.megaNavigation-submenu-item-link')
            .each(function(submenuItemLinkIndx, submenuItemLink) {
              if (
                $(submenuItemLink).hasClass(
                  'megaNavigation-submenu-item-link-active'
                )
              ) {
                //$(menuItem).find('.megaNavigation-menu-item-link').addClass("megaNavigation-submenu-visible");
                if (!$(menuItem).hasClass('active-sub')) {
                  $(menuItem).addClass('active-sub');
                }
                isItemActive = true;
                animateSubMenuItem = submenuItemLink;
                setTimeout(function() {
                  navMenu.animate({
                    scrollTop:
                      $(animateSubMenuItem).offset().top -
                      navMenu.offset().top +
                      navMenu.scrollTop() -
                      60,
                    duration: 200
                  });
                }, 400);
              }
            });
          $(menuItem)
            .find('.megaNavigation-submenu-item-submenu-item-link')
            .each(function(
              submenuItemSubMenuItemLinkIndx,
              submenuItemSubMneuItemLink
            ) {
              if (
                $(submenuItemSubMneuItemLink).hasClass(
                  'megaNavigation-item-active'
                )
              ) {
                if (!$(menuItem).hasClass('active-sub')) {
                  $(menuItem).addClass('active-sub');
                }
                isItemActive = true;
                animateSubMenuItem = submenuItemSubMneuItemLink;
                setTimeout(function() {
                  navMenu.animate({
                    scrollTop:
                      $(animateSubMenuItem).offset().top -
                      navMenu.offset().top +
                      navMenu.scrollTop() -
                      60,
                    duration: 200
                  });
                }, 400);
              }
            });
        }
        if (!isItemActive) {
          if (
            $(menuItem)
              .find('.megaNavigation-menu-item-link')
              .hasClass('megaNavigation-submenu-visible')
          ) {
            $(menuItem)
              .find('.megaNavigation-menu-item-link')
              .removeClass('megaNavigation-submenu-visible');
          }
        }
      });
    } catch (err) {
      // do nothing
    }
  }
}
