// import {utils} from '../../js/main';
import $ from "jquery";
import { emitter } from "../base/emitter/emitter";

export default class TeaserMedium {
  constructor(config) {
    this.config = $.extend(
      {
        $el: ".image"
      },
      config
    );
    this.$el = $(this.config.$el);
    this.init();
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;

    $(function() {
      TeaserMedium.setBackgroundSize($(this));
    });

    emitter.on("resize", function() {
      TeaserMedium.changeTwoColumnTeaserStyling(self.$el);
    });
  }

  static changeTwoColumnTeaserStyling($el) {
    var teaserImages = $el.find(".teaser-svg-image");
    teaserImages.each(function() {      
      TeaserMedium.setBackgroundSize($(this));
    });
  }

  static setBackgroundSize($this) {    
    var imgWidth = $this.data("svg-width");
    var imgHeight = $this.data("svg-height");

    var containerWidth = $this.parent().width();
    var containerHeight = $this.parent().height();

    var imgRatio = imgHeight / imgWidth;
    var containerRatio = containerHeight / containerWidth;

    var finalWidth;
    var finalHeight;

    if (containerRatio > imgRatio) {
      finalHeight = containerHeight;
      finalWidth = containerHeight / imgRatio;
    } else {
      finalWidth = containerWidth;
      finalHeight = containerWidth * imgRatio;
    }
    $this.css("background-size", finalWidth + "px " + finalHeight + "px");
  }
}
