/**
 * Class: Rendercharts
 * copy-paste from the assets-file...
 */
import Chart from 'chart.js';
import dateFormat from 'dateformat';
import $ from 'jquery';
import { $win } from '../base/utils/utils';

export default class Rendercharts {
  constructor(/* config */) {
    var locale = navigator.language || navigator.userLanguage;
    // var dateFormatString = 'dd/MM/yyyy';
    var dtoDateFormatString = 'dd-mm-yyyy';
    var pagingData;
    var isScreenSmall = $win.width() < 768;
    var forwardHourlyParam = {
      region: '',
      hasPrevData: {},
      hasNextData: {},
      prevChartDate: new Date(),
      chartDate: new Date(),
      formattedChartDate: function(date) {
        return dateFormat(
          date || forwardHourlyParam.chartDate,
          dtoDateFormatString
        );
      },
      chartData: {},
      clearCache: function() {
        forwardHourlyParam.hasPrevData = {};
        forwardHourlyParam.hasNextData = {};
        forwardHourlyParam.prevChartDate = new Date();
        forwardHourlyParam.chartDate = new Date();
        forwardHourlyParam.chartData = {};
      }
    };

    $(function() {
      $('.marketchart').each(function(index) {
        $(this)
          .find('canvas')
          .attr('data-chart-id', '' + index);
        updateChart($(this));
      });

      $('.energymarketTableContainer').each(function() {
        updateTable($(this));
      });
    });

    $('.marketchart form').change(function() {
      var chartType = $(this)
        .parent()
        .find('canvas')
        .attr('data-chart-type');
      if (chartType == 'forwardhourly') {
        forwardHourlyParam.clearCache();
      }
      updateChart($(this).parent());
    });

    $('.energymarketTableContainer form').change(function() {
      updateTable($(this).parent());
    });

    $win.resize(function() {
      var width = window.outerWidth;
      var smallScreenNewValue = width < 768;
      if (smallScreenNewValue != isScreenSmall) {
        isScreenSmall = smallScreenNewValue;
        updateChartPaging($('#hourlyMarketChart'));
      }
    });

    function formatCurrency(num, numDigits) {
      try {
        num = num.toLocaleString(locale, {
          minimumFractionDigits: numDigits,
          maximumFractionDigits: numDigits
        });
      } catch (err) {
        num = num.toFixed(numDigits);
      }
      return num;
    }

    function formatCurrencyArr(arr, numDigits) {
      for (var i = 0; i < arr.length; i++) {
        arr[i] = formatCurrency(arr[i], numDigits);
      }
      return arr;
    }

    function updateTable(obj) {
      var currency = obj.find('[data-selector=currency]').val();
      var region = obj.find('[data-selector=region]').val();
      if (!region) {
        region = $('input[name="regionRadio"]:checked').val();
      }

      var period = obj.find('[data-selector=period]').val();
      // var product = obj.find('[data-selector=product]').val();

      var col1 = [];
      var col2 = [];
      var col3 = [];
      var col4 = [];

      var apiEndpoint = '';
      var inputData = {};

      apiEndpoint = '/api/energymarket/spotprices/daily';
      inputData = { currency: currency, period: period };

      //console.log(apiEndpoint);
      //console.log(inputData);
      var output = '';
      var priceUnit = currency + '/MWh';
      var priceInfo = '';
      obj.find('.chartLoader').show();
      $.ajax({
        url: apiEndpoint,
        data: inputData
      })
        .then(function(msg) {
          col1 = msg.period;
          col2 = msg.vestdk;
          col3 = msg.oestdk;
          col4 = msg.system;

          if (col1.length === 0) {
            priceInfo =
              'Der er endnu ikke data for denne selektion. Prv venligst igen senere';
            output = '';
          } else {
            col2 = formatCurrencyArr(col2, 2);
            col3 = formatCurrencyArr(col3, 2);
            col4 = formatCurrencyArr(col4, 2);

            output = "<table class='table table-no-collapse'>";
            output +=
              "<thead class='theme-primary-color-background theme-color-base'>";
            output += '<tr>';
            output +=
              '<th>Periode</th><th>Vest-DK</th><th>st-DK</th><th>System</th>';
            output += '</tr>';
            output += '</thead>';
            output += '<tbody>';
            for (var i = 0; i < col1.length; i++) {
              output += '<tr>';
              output += '<td>' + col1[i] + '</td>';
              output += '<td>' + col2[i] + '</td>';
              output += '<td>' + col3[i] + '</td>';
              output += '<td>' + col4[i] + '</td>';
              output += '</tr>';
            }
            output += '</tbody>';
            output += '</table>';
            priceInfo = 'Priserne er vist som ' + priceUnit;
          }

          obj.find('.energymarketTable').html(output);
          obj.find('.energymarketInformation').html(priceInfo);
          obj.find('.chartLoader').hide();
        })
        .catch(function() {
          console.log(
            'There was an error loading table data from API. Try again..'
          );
        });
    }

    function updateChart(obj) {
      var currency = obj.find('[data-selector=currency]').val();
      var region = obj.find('[data-selector=region]').val();
      var period = obj.find('[data-selector=period]').val();
      var product = obj.find('[data-selector=product]').val();
      var periodLength = obj.find('[data-selector=periodLength]').val();
      if (!region) {
        region = $('input[name=regionRadio]:checked').val();
      }

      var data;
      var labels;
      var unit;
      var dateFormatString = obj.find('canvas').attr('data-date-format');
      var chartType = obj.find('canvas').attr('data-chart-type');
      var xlabel = obj.find('canvas').attr('data-xlabel');

      if (chartType === 'naturalgas') {
        if (product === 'NPG') {
          obj
            .find("[data-selector='period']")
            .children()
            .hide();
          obj
            .find("[data-selector='period']")
            .children()
            .first()
            .show();
          obj
            .find("[data-selector='period']")
            .children()
            .first()
            .attr('selected', 'selected');
          period = obj.find('[data-selector=period]').val();
        } else {
          obj
            .find("[data-selector='period']")
            .children()
            .show();
        }
      }

      var apiEndpoint = '';
      var inputData = {};
      if (chartType === 'forwardlive') {
        apiEndpoint = '/api/energymarket/forwardprices/live';
        // Date(year, month, day, hours, minutes, seconds, milliseconds)
        var res = period.split(';');
        inputData = {
          region: region,
          currency: currency,
          startdate: res[0],
          enddate: res[1]
        };
        unit = 'MWh';
      } else if (chartType === 'forwardhistoric') {
        apiEndpoint = '/api/energymarket/forwardprices/historic';
        inputData = { region: region, currency: currency, period: period };
        unit = 'MWh';
      } else if (chartType === 'spot') {
        apiEndpoint = '/api/energymarket/spotprices/period';
        inputData = { region: region, currency: currency, period: period };
        unit = 'MWh';
      } else if (chartType === 'naturalgas') {
        apiEndpoint = '/api/energymarket/gasprices';
        inputData = { period: period, currency: currency, region: product };
        unit = 'm3';
        if (currency === 'DKK') {
          currency = 're';
        } else if (currency === 'EUR') {
          // This is weird, but this is how it is on the current Dong site.
          currency = 'EUR';
          unit = 'MWh';
        }
      } else if (chartType === 'co2prices') {
        apiEndpoint = '/api/energymarket/co2prices';
        inputData = { period: period, currency: currency, region: product };
        unit = 'Ton';
      } else if (chartType === 'forwardhourly') {
        apiEndpoint = '/api/energymarket/forwardprices/hourly';
        inputData = {
          region: region,
          period: forwardHourlyParam.formattedChartDate()
        };
        unit = 'kWh';
        if (currency === 'DKK') {
          currency = 're';
        } else if (!currency) {
          // This is weird, but this is how it is on the current Dong site.
          currency = 're';
        }
      }

      var ylabel = currency + '/' + unit;
      //console.log(inputData);
      //console.log(apiEndpoint);

      obj.find('.chartLoader').show();
      $.ajax({
        url: apiEndpoint,
        data: inputData
      })
        .then(function(msg) {
          data = msg.yvalues;
          labels = msg.xvalues;

          var chartobj =
            energyMarketCharts[obj.find('canvas').attr('data-chart-id')];

          if (data.length === 0 || labels.length === 0) {
            if (chartType === 'forwardhourly') {
              forwardHourlyParam.chartDate.setDate(
                forwardHourlyParam.prevChartDate.getDate()
              );
              changeHourlyChartPage(chartobj, 0);
            } else {
              obj
                .find('.chartErrorText')
                .html(
                  'Der er endnu ikke data for denne selektion. Prv venligst igen senere'
                );
              obj.find('.chartErrorText').show();
              obj.find('canvas').hide();
            }
          } else {
            if (periodLength && periodLength < 24) {
              var filterResult = filterDataAndLabels(
                data,
                labels,
                periodLength
              );
              data = filterResult.data;
              labels = filterResult.labels;
            }

            chartobj.options.scales.yAxes[0].scaleLabel.labelString = ylabel;
            chartobj.options.scales.xAxes[0].scaleLabel.labelString = xlabel;

            if (chartType === 'forwardhourly') {
              if (forwardHourlyParam.region == '') {
                forwardHourlyParam.region = region;
              } else if (forwardHourlyParam.region != region) {
                forwardHourlyParam.clearCache();
                forwardHourlyParam.region = region;
              }
              if (
                // eslint-disable-next-line no-prototype-builtins
                !forwardHourlyParam.chartData.hasOwnProperty(
                  forwardHourlyParam.formattedChartDate()
                )
              ) {
                forwardHourlyParam.chartData[
                  forwardHourlyParam.formattedChartDate()
                ] = data;
              }
              forwardHourlyParam.hasPrevData[
                forwardHourlyParam.formattedChartDate()
              ] = msg.prev;
              forwardHourlyParam.hasNextData[
                forwardHourlyParam.formattedChartDate()
              ] = msg.next;
              $('#hourlyChartPrevButton').css(
                'display',
                forwardHourlyParam.hasPrevData[
                  forwardHourlyParam.formattedChartDate()
                ]
                  ? 'block'
                  : 'none'
              );
              $('#hourlyChartNextButton').css(
                'display',
                forwardHourlyParam.hasNextData[
                  forwardHourlyParam.formattedChartDate()
                ]
                  ? 'block'
                  : 'none'
              );
              setXaxisLabel();
            }
            if (chartobj.config.type === 'bar') {
              if (chartType === 'forwardhourly') {
                pagingData = {
                  Labels: labels,
                  Data: data,
                  Region: region,
                  Page: 1,
                  PageSize: 24,
                  StepSize: 3
                };
              } else {
                pagingData = {
                  Labels: labels,
                  Data: data,
                  Region: region,
                  Page: 1,
                  PageSize: 6,
                  StepSize: 3
                };
              }
              if (
                data.filter(function() {
                  return this < 0;
                }).length === 0
              ) {
                chartobj.options.scales.yAxes[0].ticks.beginAtZero = true;
              }
              for (var i = 0; i < labels.length; i++) {
                var date = new Date(labels[i]);
                date = date.getHours();
                if (date < 10) {
                  date = '0' + date;
                }
                labels[i] = date;
              }

              chartobj.options.tooltips.titleFontFamily = 'OrstedSansRegular';
              chartobj.options.tooltips.bodyFontFamily = 'OrstedSansRegular';
              chartobj.options.tooltips.callbacks.title = function(
                tooltipItem,
                data
              ) {
                var n = data['labels'][tooltipItem[0]['index']];
                var nplusone = parseInt(n) + 1;
                if (nplusone < 10) {
                  nplusone = '0' + nplusone;
                }
                return 'Kl. ' + n + '-' + nplusone;
              };
            } else {
              for (i = 0; i < labels.length; i++) {
                labels[i] = dateFormat(labels[i], dateFormatString);
              }
            }
            if (chartobj.config.type === 'bar') {
              for (i = 0; i < labels.length; i++) {
                if (i < 10) {
                  labels[i] = '0' + i;
                } else {
                  labels[i] = i.toString();
                }
              }
              chartobj.options.tooltips.callbacks.label = function(
                tooltipItem,
                data
              ) {
                var n =
                  data.datasets[tooltipItem.datasetIndex].data[
                    tooltipItem.index
                  ];
                n = n.toLocaleString('da-DK', {
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2
                });
                return n + ' ' + ylabel;
              };
              $('#downloadCsvData').val(getChartDataCsv(data, labels));
              $('#downloadCsvData')
                .unbind('click')
                .click(function() {
                  var fileName = 'chart-data.csv';
                  var csvData = $(this).val();
                  if (window.navigator.msSaveOrOpenBlob) {
                    var blob = new Blob([csvData], { type: 'text/csv' });
                    window.navigator.msSaveBlob(blob, fileName);
                  } else {
                    $(this).attr(
                      'href',
                      'data:text/csv;charset=utf-8,' +
                        encodeURIComponent(csvData)
                    );
                    $(this).attr('download', fileName);
                  }
                });
              if (chartType === 'forwardhourly') {
                $('.hourlyChartNextButton')
                  .unbind('click')
                  .on(
                    'click',
                    { energyMarketCharts: energyMarketCharts, obj: obj },
                    hourlyChartNextPage
                  );
                $('.hourlyChartPrevButton')
                  .unbind('click')
                  .on(
                    'click',
                    { energyMarketCharts: energyMarketCharts, obj: obj },
                    hourlyChartPreviousPage
                  );
              } else {
                $('.chartNextButton')
                  .unbind('click')
                  .on(
                    'click',
                    { energyMarketCharts: energyMarketCharts, obj: obj },
                    nextPage
                  );
                $('.chartPrevButton')
                  .unbind('click')
                  .on(
                    'click',
                    { energyMarketCharts: energyMarketCharts, obj: obj },
                    previousPage
                  );
              }
            } else {
              chartobj.options.tooltips.callbacks.label = function(
                tooltipItem,
                data
              ) {
                var n = formatCurrency(
                  data.datasets[tooltipItem.datasetIndex].data[
                    tooltipItem.index
                  ],
                  2
                );
                return n + ' ' + ylabel;
              };
            }
            var noPaging = false;
            obj.find('.chartErrorText').hide();
            obj.find('canvas').show();
            if (chartobj.config.type === 'bar') {
              changeChartPage(chartobj, noPaging);
            } else {
              chartobj.data.datasets[0].data = data;
              chartobj.data.labels = labels;
              chartobj.update(300);
            }
          }
          obj.find('.chartLoader').hide();
        })
        .catch(function(/* err */) {
          console.log(
            'There was an error loading chart data from API. Try again..'
          );
        });
    }

    function getChartDataCsv(data, labels) {
      var chartData = 'Tidspunkt;Elpris\r\n';
      for (var i = 0; i < labels.length; i++) {
        chartData +=
          labels[i] +
          ';' +
          data[i].toLocaleString('da-DK', {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
          }) +
          '\r\n';
      }
      return chartData;
    }

    function updateChartPaging(obj) {
      var chartType = obj.find('canvas').attr('data-chart-type');
      if (chartType != 'forwardhourly') {
        return;
      }
      if (!pagingData) {
        return;
      }
      var chartObj =
        energyMarketCharts[obj.find('canvas').attr('data-chart-id')];
      changeChartPage(chartObj);
    }

    function hourlyChartNextPage(event) {
      if (
        forwardHourlyParam.hasNextData[forwardHourlyParam.formattedChartDate()]
      ) {
        var chartobj =
          event.data.energyMarketCharts[
            event.data.obj.find('canvas').attr('data-chart-id')
          ];
        changeHourlyChartPage(chartobj, 1);
      }
    }

    function hourlyChartPreviousPage(event) {
      if (
        forwardHourlyParam.hasPrevData[forwardHourlyParam.formattedChartDate()]
      ) {
        var chartobj =
          event.data.energyMarketCharts[
            event.data.obj.find('canvas').attr('data-chart-id')
          ];
        changeHourlyChartPage(chartobj, -1);
      }
    }

    function changeHourlyChartPage(chartobj, page) {
      forwardHourlyParam.prevChartDate.setDate(
        forwardHourlyParam.chartDate.getDate()
      );
      forwardHourlyParam.chartDate.setDate(
        forwardHourlyParam.chartDate.getDate() + page
      );
      setXaxisLabel();
      if (
        // eslint-disable-next-line no-prototype-builtins
        forwardHourlyParam.chartData.hasOwnProperty(
          forwardHourlyParam.formattedChartDate()
        )
      ) {
        pagingData.Data =
          forwardHourlyParam.chartData[forwardHourlyParam.formattedChartDate()];
        chartobj.data.datasets[0].data = pagingData.Data;
        chartobj.update(300);
        $('#downloadCsvData').val(
          getChartDataCsv(pagingData.Data, pagingData.Labels)
        );
      } else {
        updateChart($('.marketchart:has(#hourlyMarketChart)'));
      }
      $('.hourlyChartPrevButton').css(
        'display',
        forwardHourlyParam.hasPrevData[forwardHourlyParam.formattedChartDate()]
          ? 'block'
          : 'none'
      );
      $('.hourlyChartNextButton').css(
        'display',
        forwardHourlyParam.hasNextData[forwardHourlyParam.formattedChartDate()]
          ? 'block'
          : 'none'
      );
    }

    function setXaxisLabel() {
      var datelabel = new Date(forwardHourlyParam.chartDate);
      var optionsday = { weekday: 'long' };
      var optionsdate = { year: 'numeric', month: 'long', day: '2-digit' };
      var daytext =
        datelabel
          .toLocaleString('da-DK', optionsday)
          .charAt(0)
          .toUpperCase() +
        datelabel.toLocaleString('da-DK', optionsday).substr(1);
      $('.canvasLabel').text(
        daytext + ' ' + datelabel.toLocaleString('da-DK', optionsdate)
      );
    }
    
    function nextPage(event) {
      if (
        pagingData &&
        (pagingData.Page + 1) * pagingData.PageSize <= pagingData.Data.length
      ) {
        pagingData.Page = pagingData.Page + 1;
        var chartobj =
          event.data.energyMarketCharts[
            event.data.obj.find('canvas').attr('data-chart-id')
          ];
        changeChartPage(chartobj);
      }
    }

    function previousPage(event) {
      if (pagingData && pagingData.Page > 1) {
        pagingData.Page = pagingData.Page - 1;
        var chartobj =
          event.data.energyMarketCharts[
            event.data.obj.find('canvas').attr('data-chart-id')
          ];
        changeChartPage(chartobj);
      }
    }
    
    function changeChartPage(chartobj, noPaging) {
      var data, labels;
      if (isScreenSmall && !noPaging) {
        var startIndex = (pagingData.Page - 1) * pagingData.PageSize;
        var endIndex = pagingData.Page * pagingData.PageSize;
        data = pagingData.Data.slice(startIndex, endIndex);
        labels = pagingData.Labels.slice(startIndex, endIndex);
      } else {
        data = pagingData.Data;
        labels = pagingData.Labels;
      }
      chartobj.data.datasets[0].data = data;
      chartobj.data.labels = labels;
      chartobj.update(300);
    }

    function filterDataAndLabels(data, labels, periodLength) {
      var searchedDate = getOffsetDate(periodLength, true);
      var firstDate = getFirstDateFromLabels(labels);

      if (searchedDate < firstDate) {
        searchedDate = firstDate;
      }
      var searchedDateString = getDateInPriceApiFormat(searchedDate);
      var cutOffIndex = labels.indexOf(searchedDateString);

      var offset = 0;
      while (cutOffIndex === -1) {
        offset++;
        searchedDate = getOffsetDate(offset, false, searchedDate);
        if (searchedDate < firstDate) {
          searchedDate = firstDate;
          break;
        }
        searchedDateString = getDateInPriceApiFormat(searchedDate);
        cutOffIndex = labels.indexOf(searchedDateString);
      }

      var result = {};
      result.data = data.slice(cutOffIndex);
      result.labels = labels.slice(cutOffIndex);
      return result;
    }

    function getOffsetDate(offset, byMonth, initialDate) {
      var currentDate = initialDate ? initialDate : new Date();
      if (byMonth === true) {
        currentDate.setMonth(currentDate.getMonth() - offset);
      } else {
        currentDate.setDate(currentDate.getDate() - offset);
      }
      return currentDate;
    }

    function getDateInPriceApiFormat(date) {
      var dd = date.getDate();
      var mm = date.getMonth() + 1;
      var yyyy = date.getFullYear();
      if (dd < 10) {
        dd = '0' + dd;
      }
      if (mm < 10) {
        mm = '0' + mm;
      }
      return yyyy + '-' + mm + '-' + dd + 'T00:00:00.0000000Z';
    }

    function getFirstDateFromLabels(labels) {
      var firstElem = labels[0];
      var yyyy = firstElem.substr(0, 4);
      var mm = firstElem.substr(5, 2);
      var dd = firstElem.substr(8, 2);
      var firstDate = new Date();
      firstDate.setFullYear(yyyy, mm - 1, dd);
      return firstDate;
    }

    var data = [];
    var labels = [];

    Chart.defaults.global.defaultFontSize = 14;
    Chart.defaults.global.defaultFontFamily = '"MaineSansRegular", sans';
    Chart.defaults.global.defaultFontColor = 'rgb(0,0,0)';
    Chart.defaults.global.tooltips.titleFontSize = 14;
    Chart.defaults.global.tooltips.bodyFontSize = 14;

    var energyMarketCharts = [];

    $('.marketchart')
      .find('canvas')
      .each(function() {
        var lineColor = '#4e9dd4';
        var chartStyle = $(this).attr('data-chart-style');

        //if style is not set the default is "line"
        var type = chartStyle ? chartStyle : 'line';

        var dataset = {
          data: data,
          borderColor: lineColor,
          fill: false,
          pointRadius: 1,
          pointBackgroundColor: lineColor,
          pointHoverRadius: 6,
          pointHitRadius: 15,
          xLabels: ''
        };
        var yAxis = {
          gridLines: {
            zeroLineWidth: 1,
            zeroLineColor: 'rgb(0,0,0)'
          },
          scaleLabel: {
            display: true,
            fontStyle: 'bold',
            fontSize: 18
          },
          ticks: {
            maxRotation: 0
          }
        };

        var xAxis = {
          gridLines: {
            zeroLineWidth: 1,
            zeroLineColor: 'rgb(0,0,0)'
          },
          scaleLabel: {
            display: true,
            labelString: 'Dato',
            fontStyle: 'bold',
            fontSize: 18
          },
          ticks: {
            maxRotation: 0
          }
        };

        var optionplugins = {};
        var plugins = [];

        var chartType = $(this).attr('data-chart-type');
        if (chartType == 'forwardhourly') {
          plugins.push({
            id: 'swipe',
            afterEvent: function afterEvent(chart, event, options) {
              if (event.type === 'touchstart' || event.type === 'mousedown') {
                this.startPosX = event.native.changedTouches[0].screenX;
              } else if (
                event.type === 'touchend' ||
                event.type === 'mouseup'
              ) {
                this.endPosX = event.native.changedTouches[0].screenX;
                if (this.startPosX > this.endPosX) {
                  options.nextSeries({
                    data: {
                      energyMarketCharts: energyMarketCharts,
                      obj: $('.marketchart')
                    }
                  });
                }
                if (this.startPosX < this.endPosX) {
                  options.prevSeries({
                    data: {
                      energyMarketCharts: energyMarketCharts,
                      obj: $('.marketchart')
                    }
                  });
                }
              }
            }
          });
          optionplugins = {
            swipe: {
              prevSeries: hourlyChartPreviousPage,
              nextSeries: hourlyChartNextPage
            }
          };
        }

        var options = {
          legend: { display: false },
          scales: {
            yAxes: [yAxis],
            xAxes: [xAxis]
          },
          tooltips: {
            displayColors: false
          },
          events: ['mousemove', 'touchstart', 'touchend'],
          elements: {
            line: {
              tension: 0.0,
              borderJoinStyle: 'round',
              borderWidth: 3
            }
          },
          plugins: optionplugins
        };

        if (type === 'bar') {
          dataset.backgroundColor = lineColor;
          options.hover = { mode: null };
          yAxis.offsetGridLines = false;
          xAxis.offsetGridLines = false;
          //xAxis.ticks.autoSkip = false;
          xAxis.gridLines.display = false;
          yAxis.gridLines.drawBorder = false;
          xAxis.gridLines.drawBorder = false;
          yAxis.gridLines.zeroLineColor = 'rgba(0,0,0,0.1)';
          yAxis.scaleLabel.display = false;
          xAxis.scaleLabel.fontFamily = 'OrstedSansBold';
          xAxis.scaleLabel.fontColor = '#3b4956';
          xAxis.scaleLabel.fontSize = 16;
          xAxis.ticks.fontFamily = 'OrstedSansRegular';
          xAxis.ticks.fontColor = '#3b4956';
          yAxis.ticks.fontFamily = 'OrstedSansRegular';
          yAxis.ticks.fontColor = '#3b4956';
          yAxis.ticks.padding = 10;
        }

        energyMarketCharts.push(
          new Chart(this, {
            type: type,
            data: {
              labels: labels,
              datasets: [dataset]
            },
            options: options,
            plugins: plugins
          })
        );
      });
  }
}
