/**
 * Class: Toaster
 * Description: Her goes description
 */
import $ from 'jquery';
import Cookies from 'js-cookie';
import { emitter } from '../base/emitter/emitter';
import { $win } from '../base/utils/utils';

export default class Toaster {
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.modal'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$toasterCloseButtons = this.$el.find('button.close');

    this.init();
  }

  init() {
    if (!$('body').hasClass('is-experience-editing')) {
      this.findAndSetupToasterLinks();
      this.setToasterHeight();
      this.bindEvents();
    }
  }

  bindEvents() {
    var self = this;
    $(document).ready(function() {
      const $modals = $('.modal');
      $modals.each((index, value) => {
        const $modal = $(value);
        let delayTime = $modal.data('delaytime');

        let $toasterId = $modal.attr('id');
        let cookieVal = Cookies.get('closedToaster_' + $toasterId);

        if (delayTime && !cookieVal) {
          setTimeout(function() {
            $modal.modal('show');
          }, delayTime);
        }
      });
    });

    this.$el.on('hide.bs.modal', function(/* e */) {
      let $toasterId = $(this).attr('id');
      Cookies.set('closedToaster_' + $toasterId, true);
    });

    this.$el.on('hidden.bs.modal', function(/* e */) {
      $('body').removeClass('modal-no-backdrop');
    });

    this.$el.on('show.bs.modal', function(/* e */) {
      let $toaster = $(this);
      if (
        $toaster.data('backdrop') === false &&
        $toaster.find('.left-bottom').length > 0
      ) {
        $('body').addClass('modal-no-backdrop');
      }
    });

    emitter.on('resize', function(/* obj */) {
      self.setToasterHeight();
    });
  }

  findAndSetupToasterLinks() {
    let firstToasterId = $('.modal')
      .eq(0)
      .attr('id');
    let $toasterLinks = $("a[href*='#toaster']");

    $toasterLinks.each((index, value) => {
      let $toasterLink = $(value);
      let toasterId = $toasterLink.attr('href').replace('#toaster_', '');
      if (!toasterId) {
        toasterId = firstToasterId;
      }
      $toasterLink
        .attr('data-toggle', 'modal')
        .attr('data-target', '#' + toasterId)
        .attr('href', 'javascript:void(0)');
    });
  }

  setToasterHeight() {
    let bodyHeight = $win.height();
    let teaserHeight = bodyHeight - 32;
    this.$el.find('.toaster-content').css('max-height', teaserHeight + 'px');
  }
}
