/**
 * Class: AnnouncementPresentation
 * Description: AnnouncementList script
 */
//import {m, utils} from '../../js/main';
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions } from '../base/utils/utils';

export default class AnnouncementPresentation {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */

  constructor(config) {
    this.config = $.extend({
        el: '.announcements-presentation-list'
      },config);
    this.$el = $(this.config.el);
    this.$dates = this.$el.find('.list-items-item-value-type-date');

    if(this.$el.length){
      this.init();
    }
  }

  init(options) {
    this.$dates.each(function (idx, elem) {
      var $elem = $(elem);
      $elem.text(moment($elem.text()).format(DateFormatingOptions.dateFormat));
    });
    //this.bindEvents();
  }

  bindEvents() {
  }
}
