import $ from 'jquery';

export default class Calendar {
  constructor(options) {
    this.$el = $(options.$el);
    this.addBtnClassName = '.calendar__add-btn';
    this.popOverClassName = '.calendar__popover';
    this.popOverListClassName = '.calendar__popover-list';
    this.activeBtnClassName = '.calendar__add-btn--active';
    this.animationSpeed = 300;
    if (this.$el) {
      this.init();
    }
  }

  init() {
    if (this.$el.find(this.popOverClassName)) {
      this.popover();
    }
  }

  removeCharacterFromString(checked) {
    return checked.split('.').join('');
  }

  popover() {
    const $btn = this.$el.find(this.addBtnClassName);
    const $popOver = $btn.next(this.popOverClassName);
    $btn.on('click', e => {
      const currentBtn = $(e.currentTarget);
      if (
        !currentBtn.hasClass(
          this.removeCharacterFromString(this.activeBtnClassName)
        )
      ) {
        $popOver.fadeOut(this.animationSpeed);
        $btn.removeClass(
          this.removeCharacterFromString(this.activeBtnClassName)
        );
        currentBtn.closest('.container-fluid').css('zIndex', null);
      }
      currentBtn.next(this.popOverClassName).fadeIn(this.animationSpeed);
      currentBtn.addClass(
        this.removeCharacterFromString(this.activeBtnClassName)
      );
      currentBtn.closest('.container-fluid').css('zIndex', 1);
    });

    $(document).on('click', e => {
      const popoverTarget = $(e.target);
      if (
        !popoverTarget.is(this.popOverListClassName) &&
        !popoverTarget.is(this.addBtnClassName)
      ) {
        $popOver.fadeOut(this.animationSpeed);
        $popOver.closest('.container-fluid').css('zIndex', null);
      }
    });
  }
}
