/* eslint-disable */
// Single line function
import "./google.clusterer";

function $d(d) {
    return document.getElementById(d);
}

// http://www.dongenergy.com/EN/business%20activities/Pages/default.aspx

// Google Maps special map styles
var styles = [
	{ "featureType": "water", "elementType": "labels.text.stroke", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "transit.line", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.attraction", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.place_of_worship", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.sports_complex", "elementType": "labels.text", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.school", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.medical", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.government", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "poi.business", "stylers": [{ "visibility": "off" }] },
	{ "elementType": "labels.icon", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "administrative.province", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "transit", "stylers": [{ "visibility": "off" }] },
	{ "featureType": "water", "stylers": [{ "visibility": "simplified" }] },
	{ "featureType": "poi", "stylers": [{ "visibility": "off" }] }
];

/* Google Maps (MarkerCluster ref by Google - http://google-maps-utility-library-v3.googlecode.com/svn/trunk/markerclusterer/docs/reference.html)
---------------------------------------------------------------- */

// Markers array
var markersarray = [], overlaysarray = [];

// Global variables
var map, counter = 0, allow = true, zindex = 555, markerCluster;

var googlemaps = function () {
    return {
        initialize: function () {

            // Styled
            var styledmap = {};
            try {
                styledmap = new google.maps.StyledMapType(styles, { name: "dongmap" });
            } catch (e) {
                return true;
            }


            // Start position
            var lat = $('#map').attr('data-latitude');
            var lng = $('#map').attr('data-longitude');

            // Options
            var options = {
                zoom: 4,
                center: new google.maps.LatLng(lat, lng),
                mapTypeControl: false,
                streetViewControl: false,
                panControl: false,
                zoomControl: false,
                mapTypeId: google.maps.MapTypeId.ROADMAP
            };

            // Base map
            map = new google.maps.Map($d('map'), options);

            /* Set styles */
            map.mapTypes.set('mapstyle', styledmap);
            map.setMapTypeId('mapstyle');

            // Click
            google.maps.event.addListener(map, "click", function () { googlemaps.mapclick(this, false); });

            // Zoom
            google.maps.event.addListener(map, "zoom_changed", function () { googlemaps.zoomobserve(); });

            // Get data
            googlemaps.getxml();

            // Attach events
            $('.filt-t1 .activities').bind('click', function () { googlemaps.activities(this); return false; });
            $('.filt-t1 #btn-mrls').bind('click', function () { googlemaps.mrls(this); return false; });
            $('#fltmrls .countries a').bind('click', function () { googlemaps.countries(this); return false; });
            $('#fltmrls .units a').bind('click', function () { googlemaps.units(this); return false; });
            $('#zoom-in').bind('click', function () { googlemaps.zoom(this, 'in'); return false; });
            $('#zoom-out').bind('click', function () { googlemaps.zoom(this, 'out'); return false; });
            $('#map-back a').bind('click', function () { googlemaps.zoomtopos(this, 'out'); return false; });

        },
        zoomobserve: function () {

            // Get level
            var lvl = map.getZoom();

            // Set level
            if (lvl >= 5) {
                $('#map-back').fadeIn('fast');
            } else {
                $('#map-back').fadeOut('fast');
            }

        },
        zoomtopos: function () {

            // Variables
            var typ = '';

            // Get active
            $('.filt-t1 .activities').each(function () {


                // Get class
                var cls = $(this).hasClass('active');

                // Has class?
                if (cls) {

                    // If is active, then remember
                    typ = $(this).attr('data-type');

                }

            });

            // Set zoom
            googlemaps.backto(typ);

        },
        backto: function (a) {

            // Start position
            var lat = $('#map').attr('data-latitude');
            var lng = $('#map').attr('data-longitude');
            var zoom = 4;

            // Licenses : Removed as per requirement from business
            //if (a == 'license' || a == 'oilandgas') {
            //    lat = '66.296685';
            //    lng = '-6.020508';
            //    zoom = 3;
            //}

            // Offices
            //if (a == 'office' || a == 'group') {
            //    lat = '58.941968';
            //    lng = '10.983887';
            //    zoom = 3;
            //}

            // Get position
            var latlng = new google.maps.LatLng(lat, lng);

            // Set center
            map.setCenter(latlng);

            // Set zoom
            map.setZoom(zoom);

            // Hide overlay
            $('#ioverlay').hide();
            allow = true;

        },
        zoom: function (f, z) {

            // Get level
            var level = map.getZoom();

            // Toogle level
            if (z == 'in') {
                level = level + 1;
            } else {
                level = level - 1;
            }

            // Set zoom
            map.setZoom(level);

        },
        adddata: function (typ) {

            // Get selected values
            var a = $('#mapw').attr('data-activity');
            var b = $('#mapw').attr('data-country');
            var c = $('#mapw').attr('data-unit');

            // Variables
            var found = 0;

            // Clear current markers
            googlemaps.deletemarkers();

            // Get data
            $('#xml-data .node').each(function () {
                // Show
                var show = true;


                // Get data
                var lat = $(this).attr('data-latitude');
                var lng = $(this).attr('data-longitude');
                var tip = $(this).attr('data-tooltip');
                var ico = $(this).attr('data-icon');
                var act = $(this).attr('data-activity');
                var cou = $(this).attr('data-country');
                var unt = $(this).attr('data-unit');
                var fid = $(this).attr('id');

                // Activity
                if (a) {
                    var cls = $(this).hasClass(a);
                    if (!cls) { show = false; }
                }

                // Country
                if (b) {
                    var cls = $(this).hasClass(b);
                    if (!cls) { show = false; }
                }

                // Unit
                if (c) {
                    var cls = $(this).hasClass(c);
                    if (!cls) { show = false; }
                }


                // Show items
                if (show) {
                    // Add marker
                    googlemaps.addmarker({ id: fid, lat: lat, lng: lng, tip: tip, icon: ico, atr: cls });

                    // Add
                    found++;
                }

            });

            // Cluster styles
            var assetsPath = `${location.protocol}//${window.location.hostname}/Assets_/dist`;
            var clusterStyles = [
				{ textColor: '#fff', textSize: '16', url: `${assetsPath}/images/maps-m2.png`, height: 41, width: 41 },
				{ textColor: '#fff', textSize: '16', url: `${assetsPath}/images/maps-m2.png`, height: 41, width: 41 },
				{ textColor: '#fff', textSize: '16', url: `${assetsPath}/images/maps-m2.png`, height: 41, width: 41 }
            ];

            // Options
            var mcOptions = {
                styles: clusterStyles,
                minimumClusterSize: 2
            };

            // Add to cluster
            markerCluster = new MarkerClusterer(map, markersarray, mcOptions);

            // Set center
            googlemaps.backto(typ);

        },
        infowindow: function (f) {

            // Get positions
            var lat = $(f).attr('data-latitude');
            var lng = $(f).attr('data-longitude');

            // Get position
            var latlng = new google.maps.LatLng(lat, lng);

            // Clear existing overlays
            if (overlaysarray) {
                for (var i = 0; i < overlaysarray.length; i++) {
                    overlaysarray[i].setMap(null);
                }
            }

            /* Clear array */
            overlaysarray.length = 0;

            // Infowindow
            var overlay = new ioverlay({ map: map, point: latlng });

            // Store in array
            overlaysarray.push(overlay);

            // Get node id
            var fid = $(f).attr('data-relation');

            // Get content
            var tit = $('#' + fid).attr('data-title');
            var des = $('#' + fid).html();
            var pho = $('#' + fid).attr('data-image');
            var lnk = $('#' + fid).attr('data-link');
            var ico = $('#' + fid).attr('data-icon');
            var rdm = $('#' + fid).attr('data-more');

            // Build html
            var str = '';
            str += '<div class="hd"><div class="' + ico + '"></div><div class="close"><div class="rl"><a href="#" title="" id="map-close">&nbsp;</a></div></div></div>';
            if (pho != '') { str += '<div class="photo"><img src="' + pho + '" alt="" /></div>'; }
            if (tit != '') { str += '<div class="title">' + tit + '</div>'; }
            if (des != '') { str += '<div class="description">' + des + '</div>'; }
            if (lnk != '') { str += '<div class="link"><a href="' + lnk + '" title="">' + rdm + '</a></div>'; }

            // Load data
            setTimeout(function () {

                // Append
                $('#ioverlay-text').html(str);

                // Attach
                $('#map-close').bind('click', function () { googlemaps.mapclick(this, false); return false; });

                // Adjust
                setTimeout(function () {
                    googlemaps.infowindowadjust();
                }, 20);

            }, 50);

        },
        infowindowadjust: function () {

            // Wrap positions
            var mx = Math.round($('#map').offset().left);
            var my = Math.round($('#map').offset().top);
            var mw = Math.round($('#map').width());
            var mh = Math.round($('#map').height());

            // Get overlay positions
            var x = Math.round($('#ioverlay').offset().left);
            var y = Math.round($('#ioverlay').offset().top);
            var w = Math.round($('#ioverlay').width());
            var h = Math.round($('#ioverlay').height());

            // Calculated
            var nx = x - mx;
            var ny = y - my;

            // Pan
            var px = 0;
            var py = 0;

            // Adjust left?
            if (nx < 20) {
                px = Math.abs(nx) + 20;
                px = -px;
            }

            // Adjust top?
            if (ny < 20) {
                py = Math.abs(ny) + 20;
                py = -py;
            }

            // Adjust right?
            if ((nx + w + 20) >= mw) {
                px = (nx + w + 20) - mw;
            }

            // Adjust location
            if (px != 0 || py != 0) {
                setTimeout(function () {
                    googlemaps.pantoxy({ x: px, y: py });
                }, 250);
            }

        },
        mapclick: function (f, x) {

            // Click in infowindow
            if (x) {

                // Dont allow
                allow = false;

                // Re-allow
                setTimeout(function () {
                    allow = true;
                }, 400);

            }

            // Hide infowindow
            if (allow) {
                $('#ioverlay').hide();
            }

        },
        addmarker: function (options) {

            // Location
            var latlng = new google.maps.LatLng(options.lat, options.lng);

            // Overlay
            var overlay = new goverlay({ map: map, point: latlng, id: options.id, cls: options.icon, tip: options.tip, atr: options.atr });

            // Store in array
            markersarray.push(overlay);

        },
        deletemarkers: function () {

            // Reset markers
            if (markersarray) {
                for (var i = 0; i < markersarray.length; i++) {
                    markersarray[i].setMap(null);
                }
            }

            // Clear array
            markersarray.length = 0;

            // Clear markers
            try { markerCluster.clearMarkers(); } catch (e) { }

        },
        pantoxy: function (options) {

            // Get projection
            var latlng = map.getCenter();
            var point = map.getProjection().fromLatLngToPoint(latlng);
            var zoom = map.getZoom();

            // Set point
            var pixes = new google.maps.Point(Math.floor(point.x * Math.pow(2, zoom)), Math.floor(point.y * Math.pow(2, zoom)));

            // Adjust
            if (options.x != '0') { pixes.x += options.x; }
            if (options.y != '0') { pixes.y += options.y; }

            // Re-set point
            var point = new google.maps.Point(pixes.x / Math.pow(2, zoom), (pixes.y) / Math.pow(2, zoom));
            var fxp = map.getProjection().fromPointToLatLng(point);

            // Pan to
            map.panTo(fxp);

        },
        getxml: function (url) {

            // Get path
            var path = $('#xml-data').attr('data-xml');
            var lang = $('#xml-data').attr('data-language');

            // Get xml
            $.ajax({
                url: path,
                dataType: 'json',
                success: function (data) {

                    // Loop
                    $(data.EntryList).each(function (index) {
                        var entry = $(this)[0];

                        // Language
                        var tit = entry.Title;
                        var tip = entry.Tooltip;
                        var des = entry.Description; des = des.replace(/&gt;/gi, ">"); des = des.replace(/&lt;/gi, "<");
                        var lat = entry.Latitude;
                        var lng = entry.Longtitude;
                        var ico = googlemaps.toclass(entry.Icon);
                        var cou = googlemaps.toclass(entry.Country);
                        var lnk = entry.Link.Url;
                        var rdm = entry.Link.Description;
                        var pho = entry.Image;
                        var unt = '';

                        // Business unit
                        $(entry.BusinessUnit).each(function (i, v) {
                            unt += googlemaps.toclass(v) + ' ';
                        });

                        // Add
                        if (lat != '' && lng != '') {

                            // Counter
                            var cx = counter++;

                            // Append
                            $("#xml-data").append('<div class="node ' + ico + ' ' + cou + ' ' + unt + '" id="node' + cx + '" data-title="' + tit + '" data-tooltip="' + tip + '" data-image="' + pho + '" data-link="' + lnk + '" data-latitude="' + lat + '" data-longitude="' + lng + '" data-icon="' + ico + '" data-activity="' + ico + '" data-country="' + cou + '" data-unit="' + unt + '" data-more="' + rdm + '">' + des + '</div>');

                        }

                    });

                    // Run
                    googlemaps.adddata();

                },
                error: function (data) {

                    // Error
                    console.log('Error loading XML data');

                }
            });

        },
        toclass: function (str) {

            // Get string
            var s = str;

            // Lowercase
            s = s.toLowerCase();

            // Replace spaces
            s = s.replace(/ /gi, '');

            // Return
            return s;

        },
        activities: function (f) {

            // Get variables
            var typ = $(f).attr('data-type');

            // Loop
            $('.filt-t1 .activities').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Set class
            $(f).addClass('active');

            // Update
            $('#mapw').attr('data-activity', typ);

            // Reset country radios
            $('#fltmrls .countries a').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Reset country
            $('#mapw').attr('data-country', '');

            // Reset unit radios
            $('#fltmrls .units a').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Reset unit
            $('#mapw').attr('data-unit', '');

            // Add data
            googlemaps.adddata(typ);

        },
        mrls: function (f) {

            // Get variables
            var mor = $(f).attr('data-lang-more');
            var les = $(f).attr('data-lang-less');
            var cls = $(f).hasClass('more');

            // Toogle
            if (cls) {

                // Add / remove classes
                $(f).removeClass('more');
                $(f).addClass('less');

                // Update label
                $('#map-flt-mrls').html(les);

                // Show arrow
                $('#mrls-ico').show();

                // Show
                $('#fltmrls').slideDown('fast');

            } else {

                // Add / remove classes
                $(f).addClass('more');
                $(f).removeClass('less');

                // Update label
                $('#map-flt-mrls').html(mor);

                // Hide arrow
                $('#mrls-ico').hide();

                // Hide
                $('#fltmrls').slideUp('fast');

            }

        },
        countries: function (f) {

            // Get variables
            var rel = $(f).attr('data-rel');
            var cls = $(f).hasClass('active');
            var typ = $(f).attr('data-type');

            // Loop
            $('#fltmrls .countries a').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Toogle class
            if (cls) {
                $(f).removeClass('active');
            } else {
                $(f).addClass('active');
            }

            // Update
            $('#mapw').attr('data-country', typ);

            // Add data
            googlemaps.adddata();

        },
        units: function (f) {

            // Variables
            var typ = $(f).attr('data-type');
            var cls = $(f).hasClass('active');

            // Reset activities
            $('.filt-t1 .activities').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Reset activity
            $('#mapw').attr('data-activity', '');

            // Reset countries
            $('#fltmrls .countries a').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Reset countries
            $('#mapw').attr('data-country', '');

            // Reset units
            $('#fltmrls .units a').each(function () {

                // Remove class
                $(this).removeClass('active');

            });

            // Set active
            $(f).addClass('active');

            // Update
            $('#mapw').attr('data-unit', typ);

            // Add data
            googlemaps.adddata(typ);

        }
    };
}();

/* Attach */
$(window).bind('load', function () { googlemaps.initialize(); });

/* Overlays
---------------------------------------------------------------- */

// Marker overlay
function goverlay(options) {
    this.setValues(options);
    var div = this.div_ = document.createElement('div');
    div.className = 'marker mrk ' + 'ico-' + options.cls + ' ' + options.atr;
    div.id = options.id + '-relation';
    div.innerHTML = '<div class="inx"><div class="ico"></div></div>';
    div.setAttribute('data-relation', options.id);
    div.setAttribute('data-latitude', options.point.lat());
    div.setAttribute('data-longitude', options.point.lng());
    div.setAttribute('data-position', options.point);
    div.setAttribute('data-tooltip', options.tip);
    div.onclick = function () { googlemaps.infowindow(this); };
};

try { goverlay.prototype = new google.maps.OverlayView; } catch (e) { goverlay.prototype = {}; }

goverlay.prototype.onAdd = function () {
    var pane = this.getPanes().overlayMouseTarget;
    pane.appendChild(this.div_);
}

goverlay.prototype.onRemove = function () {
    this.div_.parentNode.removeChild(this.div_);
}

goverlay.prototype.draw = function () {
    var projection = this.getProjection();
    var position = projection.fromLatLngToDivPixel(this.point);
    var div = this.div_;
    div.style.left = position.x + 'px';
    div.style.top = position.y + 'px';
};

// Infowindow overlay
function ioverlay(options) {
    this.setValues(options);
    var div = this.div_ = document.createElement('div');
    div.id = 'ioverlay';
    div
        .innerHTML =
        '<div class="rel"><div class="abs"><div class="pad" id="ioverlay-text"></div><div class="arrow" onclick="googlemaps.mapclick(this,true);"></div></div></div>';
    div.onclick = function () { googlemaps.mapclick(this, true); };
};

try { ioverlay.prototype = new google.maps.OverlayView; } catch (e) { ioverlay.prototype = {} }

ioverlay.prototype.onAdd = function () {
    var pane = this.getPanes().overlayMouseTarget;
    pane.appendChild(this.div_);
}

ioverlay.prototype.onRemove = function () {
    this.div_.parentNode.removeChild(this.div_);
}

ioverlay.prototype.draw = function () {
    var projection = this.getProjection();
    var position = projection.fromLatLngToDivPixel(this.point);
    var div = this.div_;
    div.style.left = position.x + 'px';
    div.style.top = position.y + 'px';
};
