import { combineReducers, compose, createStore } from 'redux';
import debounce from 'lodash/debounce';

const componentClassName = 'related-reads';
const contentClassName = `${componentClassName}__content`;
const articlesClassName = `${componentClassName}__articles`;
const articleClassName = `${componentClassName}__article`;
const articleDisabledClassName = `${articleClassName}--disabled`;
const slideActionClassName = `${componentClassName}__slideAction`;
const slideActionNextClassName = `${slideActionClassName}--next`;
const slideActionPreviousClassName = `${slideActionClassName}--previous`;
const slideActionShowClassName = `${slideActionClassName}--show`;

const defaultOptions = {
  className: {
    component: componentClassName,
    content: contentClassName,
    articles: articlesClassName,
    article: articleClassName,
    articleDisabled: articleDisabledClassName,
    slideAction: slideActionClassName,
    slideActionNext: slideActionNextClassName,
    slideActionPrevious: slideActionPreviousClassName,
    slideActionShow: slideActionShowClassName,
  }
};

const actionName = {
  NEXT: 'next',
  PREVIOUS: 'previous'
}

const initArticlesState = ($nodes) => Array.from($nodes).map(($el, index) => {
  return {
    $el,
    index,
    isActive: false
  };
});

const initButtonState = ($nodes, className) => Array.from($nodes).reduce((result, $el) => {
  let objKey;
  if ($el.classList.contains(className.slideActionNext)) {
    objKey = actionName.NEXT;
  } else if ($el.classList.contains(className.slideActionPrevious)) {
    objKey = actionName.PREVIOUS;
  }

  result[objKey] = {
    $el,
    isActive: false
  };

  return result;
}, {});

const elementIsInContainerView = ($element, $container) => {
  const containerScroll = $container.scrollLeft;
  const containerWidth = getWidth($container);
  const elementPosition = $element.offsetLeft - containerScroll;
  const elementWidth = getWidth($element);
  const initialScrollPosition = 0;

  return (
    elementPosition >= initialScrollPosition &&
    elementPosition + elementWidth <= containerWidth
  );
};

const getElementPropertyValue = ($element, property) => {
  const computedStyle = window.getComputedStyle($element);
  return parseFloat(computedStyle.getPropertyValue(property), 10);
}

const getElementMarginRight = ($element) => {
  return getElementPropertyValue($element, 'margin-right');
}

const getWidth = ($el) => $el.getBoundingClientRect().width;

const maxItemInContainer = (childWidth, parentWidth, offset) => parseInt((parentWidth - offset) / childWidth, 10);

const numberOfElementCanFitInContainerView = ($element, $container) => maxItemInContainer(getWidth($element), getWidth($container), getElementMarginRight($element));

const getElementIndex = (element) => [...element.parentNode.children].indexOf(element);

const setupActiveArticle = (state, $container) => {
  return state.map(item => {
    item.isActive = elementIsInContainerView(item.$el, $container);
    return item;
  });
};

const setActiveArticle = (state, indexes) => {
  return state.map(item => {
    item.isActive = indexes.includes(item.index);
    return item;
  });
};

function setupSlideAction(state, $container) {
  let { next, previous } = state;

  previous.isActive = $container.scrollLeft !== 0;
  next.isActive = $container.scrollLeft !== $container.scrollLeft + getWidth($container);
  return state;
}

function initSelf(self) {
  let { className } = self.options;
  self.$el = self.options.el;

  self.classSelector = {
    content: `.${className.content}`,
    articles: `.${className.articles}`,
    article: `.${className.article}`,
    articleDisable: `.${className.articleDisabled}`,
    slideAction: `.${className.slideAction}`,
  }

  self.$content = self.$el.querySelector(self.classSelector.content);
  self.$articles = self.$el.querySelector(self.classSelector.articles);
  self.$articleNodes = self.$articles.querySelectorAll(self.classSelector.article);
  self.$slideActionNodes = self.$el.querySelectorAll(self.classSelector.slideAction);
  self.scrollTo = {
    scrollLeft: (pos) => {
      // easing functions http://goo.gl/5HLl8
      Math.easeInOutQuad = function (t, b, c, d) {
        t /= d / 2;
        if (t < 1) {
          return c / 2 * t * t + b
        }
        t--;
        return -c / 2 * (t * (t - 2) - 1) + b;
      };

      Math.easeInCubic = function (t, b, c, d) {
        var tc = (t /= d) * t * t;
        return b + c * (tc);
      };

      Math.inOutQuintic = function (t, b, c, d) {
        var ts = (t /= d) * t,
          tc = ts * t;
        return b + c * (6 * tc * ts + -15 * ts * ts + 10 * tc);
      };

      // requestAnimationFrame for Smart Animating http://goo.gl/sx5sts
      var requestAnimFrame = (function () {
        return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function (callback) { window.setTimeout(callback, 1000 / 60); };
      })();

      function scrollTo(to, callback, duration) {
        // because it's so fucking difficult to detect the scrolling element, just move them all
        function move(amount) {
          self.$articles.scrollLeft = amount;
        }
        function position() {
          return self.$articles.scrollLeft;
        }
        var start = position(),
          change = to - start,
          currentTime = 0,
          increment = 20;
        duration = (typeof (duration) === 'undefined') ? 500 : duration;
        var animateScroll = function () {
          // increment the time
          currentTime += increment;
          // find the value with the quadratic in-out easing function
          var val = Math.easeInOutQuad(currentTime, start, change, duration);
          // move the document.body
          move(val);
          // do the animation unless its over
          if (currentTime < duration) {
            requestAnimFrame(animateScroll);
          } else {
            if (callback && typeof (callback) === 'function') {
              // the animation is done so lets callback
              callback();
            }
          }
        };
        animateScroll();
      }
      return scrollTo(pos, () => {}, 400);
    }
  }
};

function setupEvents(self) {
  const { $articles, store } = self;
  const getState = store.getState;
  const executor = {
    next: (index) => index + 1,
    previous: (index) => index - 1
  }

  const updateItem = (action) => {
    const states = getState();
    let { articles: articlesState } = states;
    const activeArticles = articlesState.filter((item) => item.isActive);
    const newIndexes = [];
    if (action === actionName.NEXT && articlesState[articlesState.length - 1].isActive) {
      return;
    }
    if (action === actionName.PREVIOUS && articlesState[0].isActive) {
      return;
    }
    const maxElem = numberOfElementCanFitInContainerView(articlesState[0].$el, $articles);
    const firstIndex = activeArticles[0].index;

    for (let i = 0; i < maxElem; i++) {
      const newIndex = executor[action](firstIndex + i);
      if (newIndex < articlesState.length || newIndex >= 0) {
        newIndexes.push(newIndex);
      }
    }

    self.store.dispatch({
      type: 'SET_ACTIVE_ELEMENTS',
      payload: newIndexes
    });
  };

  const findActionOnIndexState = (target, states) => {
    const { articles: articlesState } = states;
    const currentArticles = articlesState.filter(item => item.isActive);
    const index = getElementIndex(target);
    if (!target.classList.contains(self.options.className.articleDisabled)) {
      return;
    }
    let action;
    if (index > currentArticles[currentArticles.length - 1].index) {
      action = actionName.NEXT;
    }

    if (index < currentArticles[0].index) {
      action = actionName.PREVIOUS;
    }
    return action;
  };

  (() => {
    self.$slideActionNodes.forEach(($el) => {
      const action = $el.classList.contains(self.options.className.slideActionNext) ? actionName.NEXT : actionName.PREVIOUS;
      $el.addEventListener('click', (e) => {
        updateItem(action);
      });
    });
  })();

  (() => {
    let currentDelta = 0;
    $articles.addEventListener('wheel', (e) => {
      e.preventDefault();
    });

    $articles.addEventListener('wheel', debounce((e) => {
      currentDelta += (e.deltaY > 0 ? 1 : -1);
      if (Math.abs(currentDelta) > 3) {
        let action = currentDelta > 0 ? actionName.NEXT : actionName.PREVIOUS;
        updateItem(action);
        currentDelta = 0;
      }
    }), 400);
  })();

  (() => {
    let currentPos;
    let action;
    let timeout;
    let currentScroll;
    let isTap;
    let hasEnded;
    let maxScrollDistance;
    const tapDistance = 30;
    const endMove = (e) => {
      updateItem(action);
    };

    $articles.addEventListener('touchstart', (e) => {
      e.preventDefault();
      currentPos = e.touches[0].clientX;
      currentScroll = $articles.scrollLeft;
      isTap = true;
      hasEnded = false;
      maxScrollDistance = getWidth(self.$articles) * 0.25;
    });

    $articles.addEventListener('touchmove', (e) => {
      if (hasEnded) return;
      if (timeout) {
        window.cancelAnimationFrame(timeout);
      }

      timeout = window.requestAnimationFrame(function () {
        const offset = currentPos - e.touches[0].clientX;
        action = offset > 0 ? actionName.NEXT : actionName.PREVIOUS;
        isTap = Math.abs(offset) < tapDistance;

        if (!isTap) {
          $articles.scrollLeft = currentScroll + offset;
        }

        if (Math.abs(offset) > maxScrollDistance) {
          endMove(e);
          hasEnded = true;
        }
      });
    });

    $articles.addEventListener('touchend', (e) => {
      if (isTap) {
        const isAnchor = e.target.tagName === 'A';
        const isDisabledArticle = e.target.classList.contains(self.options.className.articleDisabled);
        if (isAnchor) {
          e.target.click();
        } else if (isDisabledArticle) {
          const states = getState();
          const action = findActionOnIndexState(e.target, states);
          if (!action) return;

          updateItem(action);
        }
      } else {
        if (!hasEnded) {
          endMove(e);
        }
      }
    });
  })();

  (() => {
    const states = getState();
    const { articles: articlesState } = states;
    articlesState.forEach(item => {
      const { $el } = item;
      $el.addEventListener('click', (e) => {
        const states = getState();
        const action = findActionOnIndexState(e.target, states);
        if (!action) return;

        updateItem(action);
      });
    })
  })();

  (() => {
    let currentArticleWidth = getWidth(self.$articles);
    window.addEventListener('resize', debounce((e) => {
      const newArticleWidth = getWidth(self.$articles);
      const articleWidthChanged = currentArticleWidth !== newArticleWidth;
      currentArticleWidth = newArticleWidth;

      if (articleWidthChanged) {
        self.store.dispatch({
          type: 'UPDATE_CONTAINER_WIDTH',
          payload: getWidth($articles)
        });
      }
    }), 100);
  })();
};

function initStore() {
  function articles(state, action) {
    if (!state) {
      state = setupActiveArticle(initArticlesState(this.$articleNodes), this.$articles);
    }

    switch (action.type) {
      case 'SET_ACTIVE_ELEMENTS':
        return action.payload && action.payload.length > 0 ? setActiveArticle(state, action.payload) : state;
      default:
        return state;
    }
  }

  function buttons(state, action) {
    if (!state) {
      state = setupSlideAction(initButtonState(this.$slideActionNodes, this.options.className), this.$articles);
    }

    switch (action.type) {
      case 'UPDATE_BUTTON':
        state.next.isActive = typeof action.payload.next === 'boolean' ? action.payload.next : state.next.isActive;
        state.previous.isActive = typeof action.payload.previous === 'boolean' ? action.payload.previous : state.previous.isActive;
        return state;
      default:
        return state;
    }
  }

  function position(state = 0, action) {
    switch (action.type) {
      case 'UPDATE_POSITION':
        return action.payload;
      default:
        return state;
    }
  }

  function lastAction(state = null, action) {
    return action;
  }

  function containerWidth(state = null, action) {
    switch (action) {
      case 'UPDATE_CONTAINER_WIDTH':
        return action.payload;
      default:
        return state;
    }
  }

  const storeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
  this.store = createStore(combineReducers({
    articles: articles.bind(this),
    buttons: buttons.bind(this),
    position: position.bind(this),
    containerWidth: containerWidth.bind(this),
    lastAction: lastAction.bind(this)
  }), storeEnhancers());

  return this.store;
}

function render() {
  const self = this;
  const { store, $articles } = self;
  const state = store.getState();
  let type = state.lastAction.type;

  if (type.includes('INIT')) {
    type = '@@INIT';
  }

  const rendering = {
    '@@INIT': () => {
      self.store.dispatch({
        type: 'UPDATE_CONTAINER_WIDTH',
        payload: getWidth($articles)
      });

    },
    UPDATE_CONTAINER_WIDTH: () => {
      const { articles: articlesState } = state;
      const maxElem = numberOfElementCanFitInContainerView(articlesState[0].$el, $articles);
      const activeArticles = articlesState.filter(item => item.isActive);
      let firstArticleIndex = 0;
      const indexes = [];

      if (activeArticles) {
        firstArticleIndex = activeArticles[0].index;
      }

      for (let i = 0; i < maxElem; i++) {
        let nextIndex = i + firstArticleIndex;
        if (nextIndex > articlesState.length) {
          nextIndex = nextIndex - (i * 2);
        }

        indexes.push(nextIndex);
      }

      self.store.dispatch({
        type: 'SET_ACTIVE_ELEMENTS',
        payload: indexes
      });
    },
    SET_ACTIVE_ELEMENTS: () => {
      const { articles: articlesState } = state;
      const { $articles } = self;
      const activeArticles = articlesState.filter(item => item.isActive);
      if (activeArticles.length === 0) return;

      const activeArticlesWidth = activeArticles.reduce((result, item) => {
        return result + getWidth(item.$el);
      }, 0);
      const elemMarginRight = getElementMarginRight(activeArticles[0].$el);
      const activeArticlesWidthWithMargin = activeArticlesWidth + (elemMarginRight * (activeArticles.length - 1));
      const activeArticlesLeftOffset = activeArticles[0].$el.offsetLeft - $articles.offsetLeft;
      const offset = ($articles.clientWidth - activeArticlesWidthWithMargin) / 2;

      articlesState.forEach((item) => {
        if (item.isActive) {
          item.$el.classList.remove(self.options.className.articleDisabled);
        } else {
          item.$el.classList.add(self.options.className.articleDisabled);
        }
      });

      store.dispatch({
        type: 'UPDATE_POSITION',
        payload: activeArticlesLeftOffset - offset
      });

      store.dispatch({
        type: 'UPDATE_BUTTON',
        payload: {
          next: !articlesState[articlesState.length - 1].isActive,
          previous: !articlesState[0].isActive
        }
      });
    },
    UPDATE_BUTTON: () => {
      const { buttons: buttonsState } = state;
      for (const [action, item] of Object.entries(buttonsState)) {
        if (item.isActive) {
          item.$el.classList.add(self.options.className.slideActionShow);
        } else {
          item.$el.classList.remove(self.options.className.slideActionShow);
        }
      }
    },
    UPDATE_POSITION: () => {
      const { position: positionState } = state;

      self.scrollTo.scrollLeft(positionState);
    }
  }

  typeof rendering[type] !== 'undefined' && rendering[type]();
};

export default class RelatedReads {
  constructor(options) {
    this.options = {
      ...defaultOptions,
      ...options
    };

    initSelf(this);
    (initStore.bind(this))();

    const rendering = render.bind(this);
    this.store.subscribe(rendering);
    rendering();
    setupEvents(this);
  }
};
