/**
 * Class: NewsList
 * Description: NewsList script
 */
import $ from 'jquery';
import moment from 'moment';
import { DateFormatingOptions } from '../base/utils/utils';

export default class ArticleHead {
    /**
     * @param {number} param this is param.
     * @return {number} this is return.
     */
    constructor(config) {
        this.config = $.extend({
            el: '.article'
        },config);
        this.$el = $(this.config.el);
        
        if(this.$el.length){
            this.init();
        }
    }
    
    init(options) {
      this.bindEvents();
      this.$published = this.$el.find('.article-moment-replace');
      this.$published.html(
        moment($.trim(this.$published.html())).format(
          DateFormatingOptions.dateFormat
        )
      );
      this.$published.show();
    }
    bindEvents() {
        // bind your events here.
    }
}
