/**
 * Class: ExpandCollapseCircle
 * Description: Her goes description
 */
import $ from 'jquery';

export default class ExpandCollapseCircle {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.expandCollapseCircle-module'
      },
      config
    );
    this.$el = $(this.config.el);
    if (this.$el.length) {
      this.init();
    }
  }

  init() {
    var self = this;
    self.$toggle = self.$el.find('.js-expandCollapseCircle-expand-toggle');

    // clickhandler bind
    self.$toggle.on('click', function(e) {
      e.preventDefault();
      var expandCollapseModuleObj = $(this).parents(
        'div[class^="expandCollapseCircle-module"]'
      );
      if (expandCollapseModuleObj.hasClass('is-expanded')) {
        expandCollapseModuleObj.removeClass('is-expanded');
        $(this).text($(this).attr('data-close'));
      } else {
        expandCollapseModuleObj.addClass('is-expanded');
        $(this).text($(this).attr('data-open'));
      }
    });
  }
}
