/**
 * Class: Teaser
 * Description: Her goes description
 */
// import {utils} from '../../js/main';
import $ from "jquery";
import { emitter } from "../base/emitter/emitter";
import Modernizr from "modernizr";
import { imageFallBack, getImageSize } from "../base/utils/utils";
export default class Teaser {
  constructor(config) {
    this.config = $.extend(
      {
        $el: ".image"
      },
      config
    );
    this.$el = $(this.config.$el);
    this.$imageTag = this.$el.find(".image__element");
    this.$teaserSvgImage = this.$el.find(".teaser-svg-image");
    this.isInfoGraphic = this.$imageTag.hasClass("infographic__image");
    this.init();
  }
  init() {
    this.bindEvents();
  }
  bindEvents() {
    var self = this;

    // Fix for IE11. Converts to div element
    if (
      !Modernizr.objectfit &&
      self.$teaserSvgImage.length === 0 &&
      !this.isInfoGraphic
    ) {
      imageFallBack({ image: self.$imageTag });
    }

    if (self.$teaserSvgImage.length > 0 && !this.isInfoGraphic) {
      // Fix for IE11. Scale svg images and keep div
      if (!Modernizr.objectfit) {
        const imageSize = getImageSize(self.$teaserSvgImage);

        imageFallBack({
          image: self.$teaserSvgImage,
          width: imageSize.width,
          height: imageSize.height,
          cssClasses:
            "teaser-img background-image-center-center teaser-svg-image",
          svgDimensions: {
            height: self.$teaserSvgImage.attr("data-svg-height"),
            width: self.$teaserSvgImage.attr("data-svg-width")
          }
        });

        emitter.on("resize", function() {
          let teaserImg = self.$el.find("div.teaser-svg-image");
          let imageSize = getImageSize(teaserImg);
          teaserImg.css({
            "background-size": `${imageSize.width}px ${imageSize.height}px `
          });
        });
      } else {
        emitter.on("resize", function() {
          let imageSize = getImageSize(self.$teaserSvgImage);
          self.$teaserSvgImage.css({
            width: imageSize.width,
            height: imageSize.height
          });
        });
      }
    }
  }
}
