/**
 * Class: Login
 * Description: Her goes description
 */
import $ from 'jquery';
import Accordions from '../accordion/accordion';
import Form from '../form/form';
import { $win } from '../base/utils/utils';

export default class Login {
  /**
   * @param {number} param this is param.
   * @return {number} this is return.
   */
  constructor(config) {
    // put in defaults here
    //defaults
    this.config = $.extend(
      {
        el: '.login'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$loginBtn = this.$el.find('.login-button');
    this.$logoutLink = this.$el.find('.logout-button-link');

    this.init();
  }

  init() {
    this.bindEvents();
  }

  bindEvents() {
    var self = this;
    this.customValidationRules();
    this.$loginBtn.one('shown.bs.popover', function() {
      var $popoverAccordiionNode = self.$el.find('.popover');
      Accordions.bindClickToAccordions($popoverAccordiionNode);

      self.$el.on('keyup', '#CustomerNumber', function() {
        const $customerNumber = $(this);
        var pod = self.$el.find(
          "label[for='PointOfDeliveryNumber'] span:first-child"
        );
        var label = self.$el.find('#PointOfDeliveryNumber');

        if ($customerNumber.val().length < 6) {
          pod.text(label.data('five'));
        }

        if ($customerNumber.val().length === 6) {
          pod.text(label.data('six'));
        }

        if ($customerNumber.val().length === 8) {
          pod.text(label.data('eight'));
        }

        if ($customerNumber.val().length === 9) {
          pod.text(label.data('nine'));
        }
      });

      self.$el.on('click', '.login-show-password-reminder.em', function() {
        self.$el.find('.login-password-reminder').show();
        self.$el.find('.login-email-section').hide();
        $('input#EmailToRemindPassword.form-control.text-box.single-line').val(
          $('input#Email.form-control.text-box.single-line').val()
        );
        $(
          'input#EmailToRemindPassword.form-control.text-box.single-line'
        ).focus();
      });

      self.$el.on('click', '.login-show-password-reminder.cn', function() {
        self.$el.find('.login-password-reminder-cn').show();
        self.$el.find('.login-customerno-section').hide();
        $(
          'input#EmailToRemindPasswordCn.form-control.text-box.single-line'
        ).focus();
      });

      self.$el.on(
        'click',
        '.login-password-reminder .login-show-password-back-btn',
        function() {
          self.$el.find('.login-password-reminder').hide();
          self.$el.find('.login-email-section').show();
        }
      );

      self.$el.on(
        'click',
        '.login-password-reminder-cn .login-show-password-back-btn',
        function() {
          self.$el.find('.login-password-reminder-cn').hide();
          self.$el.find('.login-customerno-section').show();
        }
      );

      self.$el.on('click', 'button[name=submittingPrivateData]', function() {
        $('input#Email.form-control.text-box.single-line').val(
          $('input#Email.form-control.text-box.single-line')
            .val()
            .replace(/\s/g, '')
        );

        if (
          $('input#Email.form-control.text-box.single-line').val() != '' &&
          $('input#Password.form-control.text-box.single-line').val() != '' &&
          $('[id=EmailToRemindPassword]').is(':focus') == false
        ) {
          self.$el.find('.div-form-check').hide();
          self.$el.find('.privatelogin').hide();
          self.$el.find('.login-show-password-reminder').hide();
          self.$el.find('.form-check').hide();
          self.$el.find('.login-form-email').hide();
          self.$el.find('.login-form-password').hide();
          self.$el.find('.text-danger').hide();
          self.$el.find('.login-message').hide();
          self.$el.find('.loader-private').show();
        }
      });

      self.$el.on('click', 'button[name=submittingBusinessData]', function() {
        if (
          $('input#CustomerNumber.form-control.text-box.single-line').val() !=
            '' &&
          $(
            'input#PointOfDeliveryNumber.form-control.text-box.single-line'
          ).val() != '' &&
          $('[id=EmailToRemindPasswordCn]').is(':focus') == false
        ) {
          self.$el.find('.div-form-check').hide();
          self.$el.find('.businesslogin').hide();
          self.$el.find('.login-show-password-reminder').hide();
          self.$el.find('.form-check').hide();
          self.$el.find('.form-business .form-check').hide();
          self.$el.find('.login-form-custnumber').hide();
          self.$el.find('.login-form-pod').hide();
          self.$el.find('.text-danger').hide();
          self.$el.find('.login-message').hide();
          self.$el.find('.loader-bussiness').show();
        }
      });

      $('input#Password.form-control.text-box.single-line').focus(function() {
        $('input#Password.form-control.text-box.single-line').prop(
          'type',
          'password'
        );
      });

      self.$el.on('click', '.login-email-section .show-password', function() {
        var $passwordField = self.$el.find('#Password');
        var $hideBtn = $(this);
        if ($hideBtn.attr('data-shown') === '') {
          $passwordField.attr('type', 'text');
          $hideBtn.attr('data-shown', 'true');
          $hideBtn.html($hideBtn.attr('data-hide-msg'));
        } else {
          $passwordField.attr('type', 'password');
          $hideBtn.attr('data-shown', '');
          $hideBtn.html($hideBtn.attr('data-show-msg'));
        }
      });

      self.$el.on(
        'click',
        '.login-customerno-section .show-password',
        function() {
          var $passwordField = self.$el.find('#PointOfDeliveryNumber');
          var $hideBtn = $(this);
          if ($hideBtn.attr('data-shown') === '') {
            $passwordField.attr('type', 'text');
            $hideBtn.attr('data-shown', 'true');
            $hideBtn.html($hideBtn.attr('data-hide-msg'));
          } else {
            $passwordField.attr('type', 'password');
            $hideBtn.attr('data-shown', '');
            $hideBtn.html($hideBtn.attr('data-show-msg'));
          }
        }
      );
    });

    this.$loginBtn.on('show.bs.popover', function(/* e */) {
      self.$loginBtn.addClass('active');
    });

    this.$loginBtn.on('shown.bs.popover', function(/* e */) {
      self.initFormValidation();
      self.initFormValidationEmail();

      new Form({
        el: self.$el.find('.form')
      });
      self.$loginBtn.addClass('active');

      self.$el
        .find('.form-control-help-bs-tooltip')
        .tooltip({ viewport: '.popover' });
    });

    this.$loginBtn.on('hidden.bs.popover', function(e) {
      self.$loginBtn.removeClass('active');

      if (self.$loginBtn.hasClass('active')) {
        self.$loginBtn.removeClass('active');
        console.log(e.target);
      }
    });

    $(document).ready(function() {
      self.$loginBtn.on('click', function(e) {
        // Makes link button clickable for logout
        console.log(e.target);
        self.$loginBtn.popover('toggle');
        if (
          self.$logoutLink.length > 0 &&
          !$(e.target).hasClass('logout-button-link')
        ) {
          window.location.href = self.$logoutLink.attr('href');
        }
      });
    });

    self.$el.on('click', '.popover-content .login-menu', function(/* event */) {
      self.$loginBtn.trigger('click');
    });

    $('.showLogin').on('click', function(e) {
      e.preventDefault();
      console.log(e.target);

      if (
        $win.width() <= 768 &&
        typeof self.$loginBtn[0].lastChild.classList == 'undefined'
      ) {
        return;
      }

      self.$loginBtn.trigger('click');
    });

    // TODO: never ever use setTimeout for sth like this, don't even look at this, have to change it!
    if (
      typeof window.LoginDataObject !== 'undefined' &&
      JSON.parse(window.LoginDataObject.IsPostback)
    ) {
      setTimeout(function() {
        if (
          $win.width() <= 768 &&
          typeof self.$loginBtn[0].lastChild.classList == 'undefined'
        ) {
          return;
        }
        self.$loginBtn.trigger('click');
      }, 200);
    }
  }

  initFormValidation() {
    this.$el.find('form').validate({
      rules: {
        Email: {
          required: true,
          customEmailAndCustomerNumber: true
        },
        Password: {
          required: true
        },
        EmailToRemindPassword: {
          required: true,
          email: true
        },
        EmailToRemindPasswordCn: {
          required: true,
          email: true
        },
        CustomerNumber: {
          required: true,
          customerNumber: true
        },
        PointOfDeliveryNumber: {
          required: true
          //pointOfDeliveryNumber: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
  }

  initFormValidationEmail() {
    this.$el.find('form.email-form').validate({
      rules: {
        Email: {
          required: true,
          customEmailAndCustomerNumber: true
        },
        Password: {
          required: true
        },
        EmailToRemindPassword: {
          required: true,
          email: true
        }
      },
      unhighlight: function(/* element */) {
        $('#Email').keyup(function() {
          $('input#Password.form-control.text-box.single-line.password').val(
            ''
          );
        });
      }
    });
  }

  customValidationRules() {
    $.validator.addMethod(
      'customerNumber',
      function(value /* , element */) {
        if (
          // eslint-disable-next-line no-empty-character-class
          /((^K?\s*(\d\s*){5,8}\d[\s]*$)|(^(\d[ -]*){5}\d$))|((^(\d[]*){8}\d$)|(^(\d[]*){5}\d$))/.test(
            value
          )
        ) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Customer Number'
    );
    $.validator.addMethod(
      'pointOfDeliveryNumber',
      function(value /* , element */) {
        if (/^\d[\d ]*\d$/.test(value)) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Point of delivery number'
    );
    $.validator.addMethod(
      'customEmailAndCustomerNumber',
      function(value /* , element */) {
        //if(/((^K?(\d[ -]*){6,8}\d$)|(^(\d[ -]*){5}\d$))|(^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$)/.test(value)){
        if (
          // eslint-disable-next-line no-useless-escape
          /(^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$)/.test(
            value
          )
        ) {
          return true;
        } else {
          return false;
        }
      },
      'Invalid Customer Number or email'
    );
  }
}
