/**
 * Class: peoplepresentation
 */
import $ from 'jquery';
import ExpandCollapseRectangleItem from '../expandcollapserectangle/expandcollapserectangleitem';

export default class ExpandCollapseRectanglePresentation {
  constructor(config) {
    this.config = $.extend(
      {
        el: '.rectangle-presentation-module',
        rectanglecontext: '.rectangle-presentation-module-context'
      },
      config
    );
    this.$el = $(this.config.el);
    this.$rectanglecontext = $(this.config.rectanglecontext);

    if (this.$el.length) {
      this.init();
    }
  }

  init() {
    var self = this;
    this.$select = this.$el.find('.js-rectangle-presentation-module-select');

    // init filter list
    $.each(this.$rectanglecontext, function(index, element) {
      self.getpeopleitems(
        $(element)
          .find('.onloadcategoryid')
          .val(),
        $(element).find('.rectangle-presentation-item-list-container'),
        $(element)
          .find('.readmorelabel')
          .val(),
        $(element)
          .find('.readlesslabel')
          .val()
      );
    });

    // clickhandler bind
    this.$select.on('change', function(e) {
      e.preventDefault();
      self.$rectanglecontext = $(e.target).parents(
        '.rectangle-presentation-module-context'
      );

      self.getpeopleitems(
        self.$rectanglecontext
          .find('.rectangle-presentation-module-select option:selected')
          .attr('data-categoryid'),
        self.$rectanglecontext.find(
          '.rectangle-presentation-item-list-container'
        ),
        self.$rectanglecontext.find('.readmorelabel').val(),
        self.$rectanglecontext.find('.readlesslabel').val()
      );
    });
  }

  getpeopleitems(categoryid, contextitem, readmorelabel, readlesslabel) {
    var itemscontainereement = contextitem;
    var readmore = readmorelabel;
    var readless = readlesslabel;

    var apiurl = '/expandcollapse/getlist?catergoryid=' + categoryid;
    if (typeof window.appendApi !== 'undefined') {
      apiurl =
        '/expandcollapse/getlist?catergoryid=' + categoryid + window.appendApi;
    }

    $.ajax({
      type: 'GET',
      url: apiurl,
      success: function(data) {
        itemscontainereement.empty();
        itemscontainereement.html(data);
        let expandcollapserectangleitem = new ExpandCollapseRectangleItem();
        expandcollapserectangleitem.init(itemscontainereement);
        itemscontainereement
          .find('.js-rectangle-presentation-expand-toggle')
          .attr('data-open', readmore)
          .attr('data-close', readless)
          .html(readmore);
      },
      error: function(err) {
        console.log(err);
      }
    });
  }
}
